import { Container, Image, BitmapText } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { MenuEvents } from "../events/MenuEvents";

export class TitleMenu extends Container {

    private background: Image;
    private tfTitle: BitmapText;

    constructor(scene: BaseScene) {
        super(scene);

        this.background = this.scene.add.image(0, 0, "ui", "panelBottom");
        this.background.setOrigin(.5, 0);
        this.add(this.background);

        this.tfTitle = this.scene.add.bitmapText(0, this.background.height / 5 * 2, "aivaGold", "", 65);
        this.tfTitle.setOrigin(.5);
        this.add(this.tfTitle);
    }

    public init(): void {
        this.scene.game.events.addListener(MenuEvents.CHANGE_TITLE, this.onChangeTitle, this);
    }

    public free(): void {
        this.scene.game.events.removeListener(MenuEvents.CHANGE_TITLE, this.onChangeTitle, this);
    }

    private onChangeTitle(txt: string): void {
        this.tfTitle.text = txt;
    }
}