import { Language } from "../../content/Language";
import { GameDataModel } from "../../data/models/GameDataModel";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Button } from "../../ui/views/Button";
import { SoundToggle } from "../../ui/views/SoundToggle";
import { MenuButtonsBuilder } from "../builders/MenuButtonsBuilder";
import { MenuConstants } from "../constants";
import { MenuEvents } from "../events/MenuEvents";
import { IResetButtonsVO } from "../interfaces/IResetButtonsVO";
import { SubMenu } from "./SubMenu";
import { ButtonIconsUtils } from "../../utils/ButtonIconsUtils";

export class OptionsMenu extends SubMenu {

    private readonly URL: string = "https://play.google.com/store/apps/dev?id=8998397624813711389";

    protected scene: BaseScene;

    private btnBack: Button;
    private btnPlayMarket: Button;

    public regulatorMusic: SoundToggle;
    public regulatorSound: SoundToggle;

    private isDarkEscape: boolean = false;

    constructor(scene: BaseScene) {
        super(scene);

        this.regulatorSound = MenuButtonsBuilder.createBtnLargePassive(this.scene, this, this.arrButtons, SoundToggle.SOUNDS, "o");
        this.regulatorMusic = MenuButtonsBuilder.createBtnLargePassive(this.scene, this, this.arrButtons, SoundToggle.MUSIC, "y");

        MenuButtonsBuilder.createBtnLargeActive(this.scene, this, this.arrButtons, "g", () => this.switchLanguage());
        MenuButtonsBuilder.createBtnLargeActive(this.scene, this, this.arrButtons, "d3", () => this.darkPolicy());

        this.btnBack = MenuButtonsBuilder.createBtnSmall(this.scene, this, this.arrButtons, () => this.backToMainMenu());

        this.btnPlayMarket = new Button(this.scene, "playMarket", "", () => this.goToTheAivaGames());
        this.btnPlayMarket.x = this.scene.gameWidth / 2;
        this.btnPlayMarket.y = this.btnBack.y - 100;
        this.arrButtons.push(this.btnPlayMarket);
        this.add(this.btnPlayMarket);
    }

    private backToMainMenu(): void {
        this.scene.game.events.emit(
            MenuEvents.HIDE_SUBMENU,
            MenuConstants.subMenus.OPTIONS
        );
        this.scene.game.events.emit(
            MenuEvents.SHOW_SUBMENU,
            MenuConstants.subMenus.MAIN
        );
    }

    //  
    public init(): void {
        this.scene.game.events.emit(
            MenuEvents.CHANGE_TITLE,
            Language.getText(Language.OPTIONS)
        );
        this.scene.game.events.emit(
            MenuEvents.HIDE_SUBMENU,
            MenuConstants.subMenus.MAIN
        );
        this.scene.game.events.emit(
            MenuEvents.RESET_MENU_BUTTONS,
            {
                menu: this,
                buttons: this.arrButtons,
                odd: 2
            } as IResetButtonsVO
        );

        super.init();

        //     
        this.regulatorSound.loadVolume(GameDataModel.volumeSounds);
        this.regulatorMusic.loadVolume(GameDataModel.volumeMusic);
    }

    public language(): void {
        this.scene.game.events.emit(MenuEvents.CHANGE_TITLE, Language.getText(Language.OPTIONS));

        this.arrButtons[MenuConstants.optionsMenu.SOUNDS].text = Language.getText(Language.SOUND);
        this.arrButtons[MenuConstants.optionsMenu.MUSIC].text = Language.getText(Language.MUSIC);
        this.arrButtons[MenuConstants.optionsMenu.LANGUAGE].text = Language.getText(Language.LANGUAGE);
        this.switchDark();
        this.arrButtons[MenuConstants.optionsMenu.BACK].text = Language.getText(Language.BACK);

        const iconName: string = Language.current === "rus" ? "g2" : "g3";
        const iconFullName: string = ButtonIconsUtils.getIconAnimationName(iconName);
        (this.arrButtons[MenuConstants.optionsMenu.LANGUAGE] as Button).setIcon(iconFullName);
    }

    private switchLanguage(): void {
        Language.current === "eng"
            ? Language.setRussian()
            : Language.setEnglish();

        GameDataModel.language = Language.current;
        this.scene.gameDataModel.saveData();
        
        this.language();
    }

    private darkPolicy(): void {
        this.isDarkEscape = !this.isDarkEscape;

        GameDataModel.policy = this.isDarkEscape;
        this.scene.gameDataModel.saveData();

        this.switchDark();
    }

    private switchDark(): void {
        this.isDarkEscape = true;

        if (GameDataModel.policy !== undefined) {
            this.isDarkEscape = GameDataModel.policy;
        }

        const labelText: string = this.isDarkEscape
            ? Language.getText(Language.IS_DARK_ESCAPE)
            : Language.getText(Language.NOT_DARK_ESCAPE);
        this.arrButtons[MenuConstants.optionsMenu.POLICY].text = labelText;

        const iconName: string = this.isDarkEscape ? "d3" : "d4";
        const iconFullName: string = ButtonIconsUtils.getIconAnimationName(iconName);
        (this.arrButtons[MenuConstants.optionsMenu.POLICY] as Button).setIcon(iconFullName);
    }

    private goToTheAivaGames(): void {
        if (this.URL !== "") {
            window.open(this.URL);
        }
    }
}