    private searchDarkMonsters(): void {
        this._arrDarks = [];
        // const len: number = monsters.length;
        // for (let i: number = 0; i < len; i++) {
        //     if (monsters.objects[i].color === MonsterColor.DARK && !monsters.objects[i].isOutGame &&
        //         movable(MapModel.toTile(monsters.objects[i].x), MapModel.toTile(monsters.objects[i].y))) {
        //         _arrDarks.push(monsters.objects[i]);
        //     }
        // }

        if (this._arrDarks.length > 0) {
            this.choiseDarkRandom();
        } else {
            this.gameProcessModel.dontMove = this.gameProcessModel.dontMoveInRoom;
        }
    }

    private choiseDarkRandom(): void {
        while (this._arrDarks.length > this._countDarks) {
            this._arrDarks.splice(Phaser.Math.Between(0, this._arrDarks.length - 1), 1);
        }
        this._ticket = this._arrDarks.length + 1;
        this.setDarksPoints();
    }

    private setDarksPoints(): void {
        this.mapModel.mapMaskTemp = this.copyArray2D(this.mapModel.mapMask);

        this._arrDarks.forEach((monster: Monster) => {
            monster.setEscape(this.setEscape(MapModel.toTile(monster.x), MapModel.toTile(monster.y)));
        });
    }

    private setEscape(indexJ: number, indexI: number): Point[] {
        const arr: Point[] = [];

        for (let i: number = -1; i < 2; i++) {
            for (let j: number = -1; j < 2; j++) {
                if (i === 0 && j === 0) {
                    break;
                } else {
                    if (MapModel.inMap(indexI + i, indexJ + j)) {
                        if (this.mapModel.mapMaskTemp[indexI + i][indexJ + j] === 0) {
                            arr.push(new Point(indexJ + j, indexI + i));
                            this.mapModel.mapMaskTemp[indexI + i][indexJ + j] = 1;
                        }
                    }
                }
            }
        }

        while (arr.length > 1) {
            const rand: number = Phaser.Math.Between(0, arr.length - 1);
            this.mapModel.mapMaskTemp[arr[rand].y][arr[rand].x] = 0;
            arr.splice(rand, 1);
        }

        arr.push(new Point(indexJ, indexI));

        return arr;
    }

    private movable(indexJ: number, indexI: number): boolean {
        for (let i: number = -1; i < 2; i++) {
            for (let j: number = -1; j < 2; j++) {
                if (i === 0 && j === 0) {
                    break;
                } else {
                    if (MapModel.inMap(indexI + i, indexJ + j)) {
                        if (this.mapModel.mapMask[indexI + i][indexJ + j] === 0) {
                            return true;
                        }
                    }
                }
            }
        }
        return false;
    }

