import { BitmapText, Container, Image, Scene, Sprite, Texture } from "../../exports";
import { GameDataModel } from "../../data/models/GameDataModel";
import { IInteractiveUI } from "../interfaces/IInteractiveUI";

export class Button extends Container implements IInteractiveUI {

    protected button: Image;
    protected icon: Image | Sprite;
    protected hover: Image;
    protected textLabel: BitmapText;

    protected buttonContainer: Container;

    protected uiTextures: string[] = ["ui", "menu"];

    private command: () => void;

    constructor(scene: Scene, textureName: string, iconName: string, command?: () => void) {
        super(scene);

        this.scene = scene;
        if (command) {
            this.command = command;
        }

        this.buttonContainer = this.scene.add.container(0, 0);
        this.add(this.buttonContainer);

        this.createButton(textureName);
        this.createHover(textureName);
        this.createIcon(iconName);
        
        this.scene.add.existing(this);
    }

    protected createHover(textureName: string): void {
        this.hover = this.getView(textureName);
        this.hover.blendMode = Phaser.BlendModes.ADD;
        this.hover.alpha = 0;
        this.buttonContainer.add(this.hover);
    }

    protected createIcon(iconName: string): void {
        if (iconName === "") {
            return;
        }
        this.icon = this.getView(iconName);
        this.buttonContainer.add(this.icon);
    }

    protected createButton(textureName: string): void {
        this.button = this.getView(textureName);
        this.buttonContainer.add(this.button);
    }

    public activate(): void {
        this.onOut();
        this.button.setInteractive();
        this.addListeners();
    }

    public deactivate(): void {
        this.removeListeners();
        this.button.disableInteractive();
        this.onOut();
    }

    public addLabel(text: string, font: string, size: number, offsetX: number = 0, offsetY: number = 0): void {
        this.textLabel = this.getTextLabel(text, font, size);
        this.textLabel.setPosition(offsetX, offsetY);
        this.buttonContainer.add(this.textLabel);
    }

    public setIcon(frame: string): void {
        if (this.icon) {
            const atlasName: string = this.getAtlasName(frame);
            this.icon.setTexture(atlasName, frame);
        }
    }

    protected getView(frame: string): Image {
        const atlasName: string = this.getAtlasName(frame);
        return this.scene.add.image(0, 0, atlasName, frame);
    }

    private getAtlasName(frame: string): string {
        let atlasName: string = this.uiTextures[0];
        const countAtlases: number = this.uiTextures.length;
        for (let indexAtlas: number = 0; indexAtlas < countAtlases; indexAtlas++) {
            const atlasNameTemp: string = this.uiTextures[indexAtlas];
            const texture: Texture = this.scene.sys.textures.get(atlasNameTemp);
            if (texture.getFrameNames().indexOf(frame) >= 0) {
                atlasName = atlasNameTemp;
                break;
            }
        }
        return atlasName;
    }

    protected getTextLabel(text: string, font: string, size: number): BitmapText {
        return this.scene.add.bitmapText(0, 0, font, text, size).setOrigin(.5);
    }

    private addListeners(): void {
        this.button.on("pointerover", this.onOver, this);
        this.button.on("pointerout", this.onOut, this);
        this.button.on("pointerdown", this.onDown, this);
        this.button.on("pointerup", this.onClick, this);
    }

    private removeListeners(): void {
        this.button.off("pointerover", this.onOver, this);
        this.button.off("pointerout", this.onOut, this);
        this.button.off("pointerdown", this.onDown, this);
        this.button.off("pointerup", this.onClick, this);
    }

    protected onOver(): void {
        this.hover.alpha = .4;
    }

    protected onOut(): void {
        this.hover.alpha = 0;
        this.buttonContainer.scale = 1;
    }
    
    protected onDown(): void {
        this.buttonContainer.scale = .95;
    }

    protected onClick(): void {
        this.buttonContainer.scale = 1;
        this.scene.sound.play("Move", { volume: GameDataModel.volumeSounds });
        if (this.guard && this.command) {
            this.command();
        } else {
            console.error("Button dosen't have command: " + this);
        }
    }

    protected guard(): boolean {
        return true;
    }

    public set text(value: string) {
        this.textLabel.text = value;
    }

    public get width(): number {
        return this.button.width;
    }

    public get height(): number {
        return this.button.height;
    }
}