import { Language } from "../../content/Language";
import { GameDataModel } from "../../data/models/GameDataModel";
import { Actions, Shape } from "../../exports";
import { LoginEvents } from "../../login/events/LoginEvents";
import { ILoginData } from "../../login/interfaces/ILoginData";
import { Profile } from "../../login/views/Profile";
import { SignIn } from "../../login/views/SignIn";
import { SignUp } from "../../login/views/SignUp";
import { SeasonsEvents } from "../../panels/events/SeasonsEvents";
import { AmbientSoundPlugin } from "../../plugins/sounds/AmbientSoundPlugin";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";
import { ScenesConstants } from "../constants";
import { SceneEvents } from "../events/SceneEvents";
import { BaseScene } from "./BaseScene";
import { UISceneEvents } from "../events/UISceneEvents";

export class LoginScene extends BaseScene {

    private ambientSoundPlugin: AmbientSoundPlugin;

    private readonly RADIUS_BLOW: number = 210;

    private readonly monsterDefaultNames: { [key: string]: string } = {
        r: Language.getText(Language.TITLE_HELP9),
        o: Language.getText(Language.TITLE_HELP8),
        y: Language.getText(Language.TITLE_HELP14),
        g: Language.getText(Language.TITLE_HELP13),
        b: Language.getText(Language.TITLE_HELP10),
        p: Language.getText(Language.TITLE_HELP11),
        d: Language.getText(Language.TITLE_HELP12)
    };

    private signIN: SignIn;
    private signUP: SignUp;

    private vCellsPlayers: Profile[] = [];

    constructor() {
        super(ScenesConstants.names.login);
    }

    protected preload(): void {
        super.preload();

        this.anims.create({ key: "addUser", frames: this.anims.generateFrameNames("profiles", { prefix: "addUser", end: 39, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "blueOut", frames: this.anims.generateFrameNames("profiles", { prefix: "blueOut", end: 73, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "orangeOut", frames: this.anims.generateFrameNames("profiles", { prefix: "orangeOut", end: 82, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "redOut", frames: this.anims.generateFrameNames("profiles", { prefix: "redOut", end: 60, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "greenOut", frames: this.anims.generateFrameNames("profiles", { prefix: "greenOut", end: 91, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "yellowOut", frames: this.anims.generateFrameNames("profiles", { prefix: "yellowOut", end: 58, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "darkOut", frames: this.anims.generateFrameNames("profiles", { prefix: "darkOut", end: 54, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "purpleOut", frames: this.anims.generateFrameNames("profiles", { prefix: "purpleOut", end: 7, zeroPad: 4 }), repeat: -1 });

        this.anims.create({ key: "blueUp", frames: this.anims.generateFrameNames("profiles", { prefix: "blueUp", end: 54, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "orangeUp", frames: this.anims.generateFrameNames("profiles", { prefix: "orangeUp", end: 21, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "redUp", frames: this.anims.generateFrameNames("profiles", { prefix: "redUp", end: 10, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "greenUp", frames: this.anims.generateFrameNames("profiles", { prefix: "greenUp", end: 29, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "yellowUp", frames: this.anims.generateFrameNames("profiles", { prefix: "yellowUp", end: 58, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "darkUp", frames: this.anims.generateFrameNames("profiles", { prefix: "darkUp", end: 12, zeroPad: 4 }), repeat: -1 });
        this.anims.create({ key: "purpleUp", frames: this.anims.generateFrameNames("profiles", { prefix: "purpleUp", end: 19, zeroPad: 4 }), repeat: -1 });
    }

    protected buildScene(): void {
        this.ambientSoundPlugin = this.plugins.get("ambientSoundPlugin") as AmbientSoundPlugin;

        this.game.events.emit(SeasonsEvents.CHANGE_SEASON);

        //   
        this.signIN = new SignIn(this, this.gameWidth / 2, this.gameHeight / 2);
        this.resizeContainer.add(this.signIN);

        //    
        this.signUP = new SignUp(this, this.gameWidth / 2, this.gameHeight / 2);
        this.resizeContainer.add(this.signUP);

        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            const profile: Profile = new Profile(this);
            profile.init();
            this.resizeContainer.add(profile);
            this.vCellsPlayers.push(profile);
        }
        const circle: Shape.Circle = new Shape.Circle(this.gameWidth * .5, this.gameHeight * .5, this.RADIUS_BLOW);
        Actions.PlaceOnCircle(this.vCellsPlayers, circle);
    }
    
    public activate(): void {
        super.activate();
        this.ambientSoundPlugin.setActive("menuMusic");
        
        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_SEASONS_PANEL, true);

        this.refreshCells();
        this.freeSigns();

        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            this.vCellsPlayers[indexPlayer].init();
        }
        this.events.addListener(LoginEvents.BACK_TO_LOGIN, this.onBack, this);
        this.events.addListener(LoginEvents.SELECT_PROFILE, this.onSelectProfile, this);
        this.events.addListener(LoginEvents.CREATE_PROFILE, this.onCreateProfile, this);
        this.events.addListener(LoginEvents.DELETE_PROFILE, this.onDeleteProfile, this);
        this.events.addListener(LoginEvents.PLAY_GAME, this.onPlayGame, this);

        this.language();
    }

    public deactivate(): void {
        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            this.vCellsPlayers[indexPlayer].free();
        }
        this.events.removeListener(LoginEvents.BACK_TO_LOGIN, this.onBack, this);
        this.events.removeListener(LoginEvents.SELECT_PROFILE, this.onSelectProfile, this);
        this.events.removeListener(LoginEvents.CREATE_PROFILE, this.onCreateProfile, this);
        this.events.removeListener(LoginEvents.DELETE_PROFILE, this.onDeleteProfile, this);
        this.events.removeListener(LoginEvents.PLAY_GAME, this.onPlayGame, this);
    }

    private freeSigns(): void {
        this.signUP.free();
        this.signIN.free();
    }

    private refreshCells(): void {
        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            this.vCellsPlayers[indexPlayer].setProfileData(
                GameDataModel.allPlayersData[indexPlayer].name,
                GameDataModel.allPlayersData[indexPlayer].monster,
                indexPlayer
            );
        }
    }

    private onSelectProfile(id: number): void {
        GameDataModel.playerId = id;

        if (GameDataModel.playerData.name !== "???") {
            const animName: string = ColorIDsUtils.getColorFromID(GameDataModel.playerData.monster);
            const playerName: string = GameDataModel.playerData.name;
            this.signIN.resetData(animName, playerName);
            this.signIN.init();
        } else {
            this.signUP.init();
        }

        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            this.vCellsPlayers[indexPlayer].hide();
        }
    }

    public onBack(): void {
        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            this.vCellsPlayers[indexPlayer].show();
        }
    }

    private onCreateProfile(playerData: ILoginData): void {
        GameDataModel.playerData.name = playerData.playerName.length === 0
            ? this.choiseName(playerData.playerColor)
            : this.cutName(playerData.playerName);
        GameDataModel.playerData.monster = playerData.playerColor;

        this.gameDataModel.saveData();

        this.refreshCells();
        this.onBack();
    }

    private onDeleteProfile(): void {
        this.gameDataModel.deletePlayerData(GameDataModel.playerId);

        this.refreshCells();
        this.onBack();
    }

    public onPlayGame(): void {
        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_SEASONS_PANEL, false);
        this.add.tween({
            targets: this,
            alpha: 0,
            duration: 750,
            onComplete: () => {
                this.loadMenu();
            }
        });
    }

    private choiseName(colorChar: string): string {
        return this.monsterDefaultNames[colorChar];
    }

    private cutName(playerName: string): string {
        return playerName.substr(0, 10).toUpperCase();
    }

    public language(): void {
        this.signUP.language();
        this.signIN.language();
    }

    private loadMenu(): void {
        // _game.loadGameMenu(this.arrPlayersData[_id][Game.PROFILE_NAME],
        //     this.arrPlayersData[_id][Game.PROFILE_MONSTER],
        //     _id);
        // this.free();
        this.game.events.emit(SceneEvents.CHANGE_SCENE, ScenesConstants.names.menu);
    }
}
