import { Container } from "../exports";
import { IAnimatable } from "../scenes/interfaces/IAnimatable";
import { EnterFrameManager } from "../scenes/managers/EnterFrameManager";

export class ShakerUtils implements IAnimatable {
    private static readonly RANGE: number = 15;
    private static readonly DAMP: number = .9;

    private targetObj: Container;
    private startPositionX: number;

    private range: number;
    private coeff: number;

    public start(targetObj: Container): void {
        this.targetObj = targetObj;
        this.startPositionX = this.targetObj.x;

        this.coeff = 1;
        this.range = ShakerUtils.RANGE;
        
        EnterFrameManager.add(this);
    }

    public stop(): void {
        this.targetObj.x = this.startPositionX;
        EnterFrameManager.remove(this);
    }

    public onUpdate(deltaTime: number): void {
        this.range *= ShakerUtils.DAMP * (this.coeff *= -1);
        this.targetObj.x = this.startPositionX + this.range;

        if (Phaser.Math.Distance.Between(this.targetObj.x, 0, this.startPositionX, 0) <= .1) {
            stop();
        }
    }
}