import { BitmapText, Container, Sprite } from "../../exports";
import { Button } from "../../ui/views/Button";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";

export class UserChoise extends Container {

    private readonly RANGE_FOR_BUTTONS: number = 150;
    private readonly POS_Y_FOR_BUTTONS: number = 20;

    private monster: Sprite;
    private btnNext: Button;
    private btnPrev: Button;

    private index: number;

    private tf: BitmapText;
    private txtCountMonsters: string;

    constructor(scene: Phaser.Scene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        this.monster = this.scene.add.sprite(0, 0, "").setScale(1.5).play("redUp");

        this.btnNext = new Button(this.scene, "btnGreenPoint", "iconRight", () => this.onNext());
        this.btnNext.x = this.RANGE_FOR_BUTTONS;
        this.btnNext.y = this.POS_Y_FOR_BUTTONS;

        this.btnPrev = new Button(this.scene, "btnGreenPoint", "iconLeft", () => this.onPrev());
        this.btnPrev.x = -this.RANGE_FOR_BUTTONS;
        this.btnPrev.y = this.POS_Y_FOR_BUTTONS;

        this.tf = this.scene.add.bitmapText(0, 45, "aivaStroke", "", 40).setOrigin(.5, 0);
        
        this.add([
            this.monster,
            this.btnNext,
            this.btnPrev,
            this.tf
        ]);

        this.txtCountMonsters = "/" + ColorIDsUtils.count;
    }

    public init(): void {
        this.random();
        this.btnNext.activate();
        this.btnPrev.activate();
    }

    public free(): void {
        this.monster.anims.stop();
        this.btnNext.deactivate();
        this.btnPrev.deactivate();
    }

    public random(): void {
        this.index = Phaser.Math.Between(0, ColorIDsUtils.count - 1);
        this.refreshMonster();
    }

    private refreshMonster(): void {
        const color: string = ColorIDsUtils.getColorFromIndex(this.index);

        this.monster.play(color + "Up");
        this.tf.text = (this.index + 1) + this.txtCountMonsters;
    }

    public getColor(): string {
        return ColorIDsUtils.getColorFromChar(this.index);
    }

    private onNext(): void {
        if (this.index < ColorIDsUtils.count - 1) {
            this.index++;
            this.refreshMonster();
        }
    }

    private onPrev(): void {
        if (this.index > 0) {
            this.index--;
            this.refreshMonster();
        }
    }
}