import { Image, Scene, Sprite } from "../../exports";
import { ButtonIconsUtils } from "../../utils/ButtonIconsUtils";
import { Button } from "./Button";

export class BtnLarge extends Button {

    protected icon: Sprite;

    constructor(scene: Scene, colorIcon: string, text: string, command?: () => void) {
        super(scene, "btnWood", ButtonIconsUtils.getIconAnimationName(colorIcon), command);

        this.addLabel(text, "aivaStroke", 40, this.button.width / 10, -20);
    }

    protected createIcon(iconName: string): void {
        this.icon = this.scene.add.sprite(-this.button.width / 3, -20, iconName);
        this.setIcon(iconName);
        this.buttonContainer.add(this.icon);
    }

    public setIcon(anim: string): void {
        this.icon.play(anim);
    }
}