import { GameDataModel } from "../../data/models/GameDataModel";
import { BitmapText, Container, Image, Pointer } from "../../exports";
import { SceneEvents } from "../../scenes/events/SceneEvents";
import { IMuteSound } from "../../scenes/interfaces/IMuteSound";
import { BaseScene } from "../../scenes/views/BaseScene";
import { MoveH } from "../components/MoveH";
import { IInteractiveUI } from "../interfaces/IInteractiveUI";

export class SoundToggle extends Container implements IInteractiveUI {

    public static RANGE: number = 200;
    public static RANGE_HALF: number = SoundToggle.RANGE / 2;

    public static MUSIC: string = "music";
    public static SOUNDS: string = "sounds";

    private sprite: Image;
    private tfLabel: BitmapText;
    private tfLabelPercent: BitmapText;
    private tfValue: BitmapText;

    private move: MoveH;

    private isDown: boolean;
    private toggleId: string;

    protected scene: BaseScene;

    constructor(scene: BaseScene, toggleId: string, color: string) {
        super(scene);

        this.toggleId = toggleId;

        this.sprite = this.scene.add.image(0, 0, "menu", "btnWoodSplit");
        this.add(this.sprite);

        const wSprite: number = this.sprite.width;
        const hSprite: number = this.sprite.height;

        const posXCenterLeft: number = wSprite / 48 * 17.5 - wSprite / 2;
        const posXCenterRight: number = wSprite / 20 * 17 - wSprite / 2;
        const posYBottom: number = hSprite / 5 * 3 - hSprite / 2;
        const posYTop: number = hSprite / 7 * 2 - hSprite / 2;

        const fontSizeBottom: number = 25;
        const fontSizeTop: number = 40;

        this.tfLabel = this.scene.add.bitmapText(posXCenterLeft, posYBottom, "aivaStroke", "MUSIC", fontSizeBottom).setOrigin(.5);
        this.tfLabelPercent = this.scene.add.bitmapText(posXCenterRight, posYBottom, "aivaStroke", "%", fontSizeBottom).setOrigin(.5);
        this.tfValue = this.scene.add.bitmapText(posXCenterRight, posYTop, "aivaGold", "100", fontSizeTop).setOrigin(.5);
        this.add([
            this.tfLabel,
            this.tfLabelPercent,
            this.tfValue
        ]);

        this.move = new MoveH(this.scene as BaseScene, this.toggleId, color);
        this.move.x = posXCenterLeft;
        this.move.y = posYTop + 15;
        this.add(this.move);
    }

    public activate(): void {
        this.sprite.setInteractive();

        this.sprite.on("pointerdown", this.onTouchDown, this);
        this.sprite.on("pointerup", this.onTouchUp, this);
        this.scene.input.on("pointerup", this.onTouchUp, this);
        this.scene.input.on("pointermove", this.onMove, this);

        this.scene.game.events.addListener(SceneEvents.MUTE_SOUNDS, this.updateSoundsUI, this);
    }

    public deactivate(): void {
        this.sprite.disableInteractive();

        this.sprite.off("pointerdown", this.onTouchDown, this);
        this.sprite.off("pointerup", this.onTouchUp, this);
        this.scene.input.off("pointerup", this.onTouchUp, this);
        this.scene.input.off("pointermove", this.onMove, this);

        this.scene.game.events.removeListener(SceneEvents.MUTE_SOUNDS, this.updateSoundsUI, this);
    }

    private updateSoundsUI(data: IMuteSound): void {
        if (data.toggleId === this.toggleId) {
            this.loadVolume(+data.muted);
        }
    }

    public loadVolume(value: number): void {
        this.move.volumeLevel = value;
        this.tfValue.text = this.move.percent.toString();
    }

    private onTouchDown(pointer: Pointer): void {
        this.isDown = true;
        this.move.startMoveScroll();
        this.updateScroller(pointer.x);
    }

    private onTouchUp(): void {
        if (this.isDown) {
            this.isDown = false;
            this.move.saveVolume();
            this.move.stopMoveScroll();
            this.scene.sound.play("Move", { volume: GameDataModel.volumeSounds });
        }
    }

    private onMove(pointer: Pointer): void {
        if (this.isDown) {
            this.updateScroller(pointer.x);
        }
    }

    private updateScroller(pointerX: number): void {
        const x1: number = this.move.x;
        const x2: number = (pointerX - this.scene.x) * this.scene.globalScale - this.x + SoundToggle.RANGE_HALF;
        this.move.update(x1, x2);
        this.tfValue.text = this.move.percent.toString();
    }

    public set text(value: string) {
        this.tfLabel.text = value;
    }

    public get width(): number {
        return this.sprite.width;
    }
}