import { Container, Sprite } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { ShakerUtils } from "../../utils/ShakerUtils";
import { ComponentPool } from "../../utils/ComponentPool";

export class Spark extends Container {

    private shaker: ShakerUtils;
    private componentPool: ComponentPool;
    private sprite: Sprite;

    protected scene: BaseScene;

    constructor(scene: BaseScene) {
        super(scene);

        this.shaker = new ShakerUtils();
        this.componentPool == new ComponentPool();
        this.sprite = this.scene.add.sprite(0, 0, "");
    }
    
    public init(posX: number, posY: number): void {
        //   
        this.x = posX;
        this.y = posY;
        
        this.sprite.play("spark");
        this.add(this.sprite);
        this.sprite.on(Phaser.Animations.Events.ANIMATION_COMPLETE, this.free, this);

        this.shaker.start(this.scene.resizeContainer);
    }

    public free(): void {
        this.sprite.off(Phaser.Animations.Events.ANIMATION_COMPLETE, this.free, this);
        this.sprite.anims.stop();
        this.remove(this.sprite);

        this.parentContainer.remove(this);
        this.componentPool.dispose(this);
    }
}