import { Container, Scene, Tween } from "../exports";

export class ChangeVisibilityUtils {
    
    private static removeTweens(scene: Scene, object: Container): void {
        scene.tweens.getTweensOf(object).forEach((tween: Tween) => tween.remove());
    }
    
    public static show(scene: Scene, object: Container, time: number, complete?: () => void): void {
        ChangeVisibilityUtils.removeTweens(scene, object);
        object.visible = true;
        scene.add.tween({
            targets: object,
            alpha: 1,
            duration: time,
            onComplete: () => {
                if (complete) {
                    complete();
                }
            }
        });
    }
    
    public static hide(scene: Scene, object: Container, time: number, complete?: () => void): void {
        ChangeVisibilityUtils.removeTweens(scene, object);
        object.visible = true;
        scene.add.tween({
            targets: object,
            alpha: 0,
            duration: time,
            onComplete: () => {
                object.visible = false;
                if (complete) {
                    complete();
                }
            }
        });
    }
}