import { Container, Image, Sprite, BitmapText } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { ButtonIconsUtils } from "../../utils/ButtonIconsUtils";

export class Achievement extends Container {

    private bg: Image;
    private icon: Sprite;
    private tfName: BitmapText;
    private tfScore: BitmapText;

    constructor(scene: BaseScene) {
        super(scene);

        this.bg = this.scene.add.image(0, 0, "menu", "btnWood").setOrigin(.5);
        this.add(this.bg);

        const widthBg: number = this.bg.width;
        const dX: number = -widthBg / 2;

        this.icon = this.scene.add.sprite(0, 0, "scores", "unknown").setOrigin(0);
        this.icon.x = widthBg / 5 + dX;
        this.icon.y = -15;
        this.add(this.icon);

        this.tfName = this.scene.add.bitmapText(widthBg / 8 * 5 + dX, -40, "aivaStroke", "", 30).setOrigin(.5);
        this.add(this.tfName);

        this.tfScore = this.scene.add.bitmapText(widthBg / 8 * 5 + dX, 0, "aivaGold", "", 50).setOrigin(.5);
        this.add(this.tfScore);
    }

    public init(color: string, name: string, score: string, isActive: boolean = false): void {
        this.icon.play(ButtonIconsUtils.getIconAnimationName(color));

        const index: number = +color.substr(color.length - 1);

        if (index < 4) {
            this.icon.setOrigin(1);
        } else {
            this.icon.setOrigin(.5);
        }

        this.tfName.text = name;
        this.tfScore.text = score;

        // this.tfScore.color = 0xFFFFCC;

        if (isActive) {
            // this.tfScore.color = 0xFFFF00;
        }
    }

    public free(): void {
        this.icon.anims.stop();
    }

    // public advanceTime(time: number): void {
    //     _deltaTime += time;
    //     x = 10 * Math.sin(_deltaTime * 5);
    // }

    public get height(): number { return this.bg.height * this.scaleY; }
}
