import { PopupInfo } from "./PopupInfo";
import { Button } from "../../ui/views/Button";
import { Language } from "../../content/Language";
import { LocalStorageUtils } from "../../utils/LocalStorageUtils";
import { IPopupOptions } from "../interfaces/IPopupOptions";

export class PopupYesNo extends PopupInfo {

    private btnYes: Button;
    private btnNo: Button;

    private yesFunction: () => void;
    private noFunction: () => void;

    constructor(scene: Phaser.Scene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        this.tfMessage.y = -40;

        this.btnYes = new Button(this.scene, "btnGreenPoint", "iconYes", () => this.answerYes());
        this.btnYes.x = -70;
        this.btnYes.y = 50;
        this.arrButtons.push(this.btnYes);

        this.btnNo = new Button(this.scene, "btnPinkPoint", "iconNo", () => this.answerNo());
        this.btnNo.x = 70;
        this.btnNo.y = 50;
        this.arrButtons.push(this.btnNo);

        this.add([
            this.btnYes,
            this.btnNo
        ]);
    }

    public callPopup(data: IPopupOptions): void {
        if (data.yes) {
            this.setYes(data.yes);
        }
        if (data.no) {
            this.setNo(data.no);
        }
        this.language();
        super.callPopup();
    }

    public setYes(yesFunction: () => void): void {
        this.yesFunction = yesFunction;
    }

    public setNo(noFunction: () => void): void {
        this.noFunction = noFunction;
    }

    private answerYes(): void {
        if (this.yesFunction) {
            this.yesFunction();
            this.free();
        } else {
            console.warn("PopupYesNo:  answerYes  !");
            this.answerNo();
        }
    }

    public answerNo(): void {
        if (this.noFunction) {
            this.noFunction();
        }
        this.free();
    }

    public language(): void {
        this.tfMessage.text = Language.getText(Language.ARE_YOU_SURE);
    }
}