import { MonsterColor } from "../monsters/constants";

export class ColorIDsUtils {

    private static arrColorMonsters: string[] = [
        MonsterColor.RED,
        MonsterColor.ORANGE,
        MonsterColor.YELLOW,
        MonsterColor.GREEN,
        MonsterColor.BLUE,
        MonsterColor.PURPLE,
        MonsterColor.DARK
    ];
    private static readonly arrColorChars: string[] = ["r", "o", "y", "g", "b", "p", "d"];
    private static readonly arrColorIDs: { [key: string]: string } = {
        r: "red",
        o: "orange",
        y: "yellow",
        g: "green",
        b: "blue",
        p: "purple",
        d: "dark"
    };

    public static getColorFromID(id: string): string {
        return this.arrColorIDs[id] ? this.arrColorIDs[id] : "addUser";
    }

    public static getColorFromIndex(index: number): string {
        return ColorIDsUtils.getColorFromID(ColorIDsUtils.getColorFromChar(index));
    }

    public static getFullColorFromIndex(index: number): string {
        return ColorIDsUtils.arrColorMonsters[index];
    }

    public static getColorFromChar(index: number): string {
        const colorChar: string = this.arrColorChars[index];

        if (colorChar) {
            return colorChar;
        }

        return "";
    }

    public static getIndexFromColor(color: string): number {
        return this.arrColorMonsters.indexOf(color);
    }

    public static getCharColorFromColor(color: string): string {
        return this.arrColorChars[this.getIndexFromColor(color)];
    }

    public static getRandomColor(): string {
        return ColorIDsUtils.arrColorMonsters[Phaser.Math.Between(0, ColorIDsUtils.count - 1)];
    }

    public static get count(): number {
        return this.arrColorChars.length;
    }
}