import { PopupConstants } from "../../popups/constants";
import { PopupEvents } from "../../popups/events/PopupEvents";
import { IPopupOptions } from "../../popups/interfaces/IPopupOptions";
import { PopupBase } from "../../popups/views/PopupBase";
import { PopupYesNo } from "../../popups/views/PopupYesNo";
import { ScenesConstants } from "../constants";
import { BaseScene } from "./BaseScene";

export class PopupsScene extends BaseScene {

    private popup: PopupBase;

    constructor() {
        super(ScenesConstants.names.popups);
    }

    protected buildScene(): void {
        this.game.events.addListener(PopupEvents.SHOW_POPUP, this.onShowPopup, this);
    }

    private onShowPopup(popupOptions: IPopupOptions): void {
        switch (popupOptions.id) {
            case PopupConstants.ids.POPUP_APPROVE:
                this.callApprovePopup(popupOptions);
                break;
        }

        this.scene.bringToTop(ScenesConstants.names.popups);
    }

    private callApprovePopup(popupOptions: IPopupOptions): void {
        this.popup = new PopupYesNo(this, this.gameWidth * .5, this.gameHeight * .5) as PopupYesNo;
        this.popup.callPopup(popupOptions);
        this.resizeContainer.add(this.popup);
    }
}
