import { Scene } from "phaser";
import { Button } from "./Button";
import { Image } from "../../exports";

export class Toggle extends Button {

    public static readonly ICON_MUSIC: string = "iconMusic";
    public static readonly ICON_SOUND: string = "iconSounds";
    public static readonly ICON_CELL: string = "iconCell";

    private lineSwitchOff: Image;
    private isSwitch: boolean = true;

    constructor(scene: Scene, textureName: string, iconName: string, command?: () => void) {
        super(scene, textureName, iconName, command);

        this.lineSwitchOff = this.getView("lineSwitch");
        this.lineSwitchOff.rotation = 45;
        this.lineSwitchOff.visible = false;
        this.add(this.lineSwitchOff);
    }

    public switchOn(): void {
        this.isSwitch = true;
        this.lineSwitchOff.visible = !this.isSwitch;
    }

    public switchOff(): void {
        this.isSwitch = false;
        this.lineSwitchOff.visible = !this.isSwitch;
    }

    protected onClick(): void {
        this.isSwitch = !this.isSwitch;
        super.onClick();

        this.isSwitch ? this.switchOn() : this.switchOff();
    }

    public get switched(): boolean { return this.isSwitch; }
}