import { GameDataModel } from "../../data/models/GameDataModel";
import { Image } from "../../exports";
import { SeasonsEvents } from "../../panels/events/SeasonsEvents";
import { SeasonsUtils } from "../../utils/SeasonsUtils";
import { ScenesConstants } from "../constants";
import { BaseScene } from "./BaseScene";

export class BackgroundScene extends BaseScene {
    // todo: fill or fit?
    // protected isMinScale: boolean = false;

    private background: Image;

    constructor() {
        super(ScenesConstants.names.background);
    }

    protected buildScene(): void {
        this.setBackground("preloader");
        this.game.events.addListener(SeasonsEvents.CHANGE_SEASON, this.onChangeBackground, this);
    }

    public setBackground(backgroundId: string): void {
        if (!this.background) {
            this.background = this.add.image(this.gameWidth / 2, this.gameHeight / 2, backgroundId);
            this.background.setScale(1.17);
            this.background.setOrigin(.5, .5);
            this.resizeContainer.add(this.background);
        } else {
            this.background.setTexture("backgrounds", backgroundId);
        }
    }

    private onChangeBackground(): void {
        this.setBackground("background" + SeasonsUtils.getSeasonNameByID(GameDataModel.seasonId));
    }
}