import { Container, Image, Point, Scene, Tween } from "../../exports";
import { MonsterColor } from "../../monsters/constants";
import { Monster } from "../../monsters/views/Monster";
import { TutorialEvents } from "../../panels/events/TutorialEvents";
import { MapModel } from "../models/MapModel";
import { PathfinderUtils } from "../utils/PathfinderUtils";

export class MonsterNavigator extends Container {

    private static instance: MonsterNavigator;

    private pathfinder: PathfinderUtils = new PathfinderUtils();

    private targetMonster: Monster;
    private imgStartPoint: Image;
    private imgStopPoint: Image;
    private wayPoints: Image[];

    private pointEnd: Point;
    private way: Point[];

    private tweens: Tween[] = [];

    constructor(scene: Scene) {
        super(scene);

        if (!MonsterNavigator.instance) {
            this.imgStartPoint = this.scene.add.image(0, 0, "ui", "pointBegan");
            this.imgStopPoint = this.scene.add.image(0, 0, "ui", "pointEnd");
            this.wayPoints = [];

            this.add([
                this.imgStartPoint,
                this.imgStopPoint
            ]);

            this.hide();

            MonsterNavigator.instance = this;
        }
        return MonsterNavigator.instance;
    }

    public init(targetObj: Monster, pointEnd: Point): void {
        this.hide();

        this.targetMonster = targetObj;
        this.pointEnd = pointEnd;

        this.imgStopPoint.y = MapModel.toPix(this.pointEnd.y);
        this.imgStopPoint.x = MapModel.toPix(this.pointEnd.x);

        this.scene.game.events.addListener(TutorialEvents.NAVIGATOR_SHOW, this.show, this);
        this.scene.game.events.addListener(TutorialEvents.NAVIGATOR_HIDE, this.hide, this);
    }

    public free(): void {
        this.reset();
        this.scene.game.events.removeListener(TutorialEvents.NAVIGATOR_SHOW, this.show, this);
        this.scene.game.events.removeListener(TutorialEvents.NAVIGATOR_HIDE, this.hide, this);
    }

    public show(monster: Monster): void {
        if (this.targetMonster && this.targetMonster === monster && !this.targetMonster.isFree) {
            this.imgStartPoint.visible = true;
            this.imgStopPoint.visible = true;

            this.refreshWay();
            this.addTweenAnimation(this.imgStartPoint);
            this.addTweenAnimation(this.imgStopPoint);
        }
    }

    public hide(): void {
        this.imgStartPoint.scale = 1;
        this.imgStopPoint.scale = 1;

        this.imgStartPoint.visible = false;
        this.imgStopPoint.visible = false;

        this.clear();
    }

    public reset(): void {
        this.hide();
        this.targetMonster = null;
    }

    public refreshWay(): void {
        this.clear();

        this.pathfinder.setStartAndEndPoints(this.targetMonster.point, this.pointEnd);
        this.way = this.pathfinder.getWay(this.targetMonster.typePathfinder, this.targetMonster.maxTurn);

        this.imgStartPoint.x = MapModel.toPix(this.targetMonster.point.x);
        this.imgStartPoint.y = MapModel.toPix(this.targetMonster.point.y);

        if (this.targetMonster.x === this.imgStopPoint.x && this.targetMonster.y === this.imgStopPoint.y) {
            this.imgStartPoint.visible = false;
            this.imgStopPoint.visible = false;
            return;
        } else {
            this.imgStartPoint.visible = true;
            this.imgStopPoint.visible = true;
        }

        if (this.targetMonster.color !== MonsterColor.BLUE) {
            const lengthWay: number = this.way.length;
            if (lengthWay > 1) {
                for (let i: number = 1; i < lengthWay - 1; i++) {
                    const pointWay: Image = this.scene.add.image(0, 0, "ui", "pointWay");
                    pointWay.x = MapModel.toPix(this.way[i].x);
                    pointWay.y = MapModel.toPix(this.way[i].y);
                    this.wayPoints.push(pointWay);
                    this.add(pointWay);
                }
            }
        }
        this.add(this.imgStartPoint);
    }

    private clear(): void {
        const countPointsWay: number = this.wayPoints.length;
        for (let i: number = 0; i < countPointsWay; i++) {
            this.remove(this.wayPoints[i]);
        }
        this.wayPoints.length = 0;

        this.tweens.forEach((tween: Tween) => {
            tween.stop();
            tween.remove();
        });
        this.tweens.length = 0;
    }

    private addTweenAnimation(image: Image): void {
        image.setScale(1);
        this.tweens.push(this.scene.add.tween({
            targets: image,
            scaleX: 1.2,
            scaleY: 1.2,
            duration: 500,
            loop: -1,
            yoyo: true
        }));
    }
}