import { Language } from "../../content/Language";
import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Button } from "../../ui/views/Button";
import { ButtonIconsUtils } from "../../utils/ButtonIconsUtils";
import { MenuButtonsBuilder } from "../builders/MenuButtonsBuilder";
import { MenuConstants } from "../constants";
import { MenuEvents } from "../events/MenuEvents";
import { IResetButtonsVO } from "../interfaces/IResetButtonsVO";
import { SubMenu } from "./SubMenu";

export class MainMenu extends SubMenu {

    private isNewGame: boolean;

    constructor(scene: BaseScene) {
        super(scene);

        //  
        this.createBtnLargeActive("p", () => this.onContinueGame());
        this.createBtnLargeActive("d", () => this.newGame());
        this.createBtnLargeActive("b", () => this.onStartTutorial());
        this.createBtnLargeActive("r", () => this.onOpenScores());
        this.createBtnLargeActive("g", () => this.onOpenOptions());
    }

    private createBtnLargeActive(iconColor: string, command: () => void): void {
        MenuButtonsBuilder.createBtnLargeActive(
            this.scene,
            this,
            this.arrButtons,
            iconColor,
            command
        );
    }

    private onContinueGame(): void {
        this.scene.game.events.emit(GameProcessEvents.CONTINUE_GAME);
    }

    private newGame(): void {
        if (!this.isNewGame) {
            this.arrButtons[MenuConstants.mainMenu.NEW_GAME].text = Language.getText(Language.ARE_YOU_SURE);
            const iconName: string = ButtonIconsUtils.getIconAnimationName("d2");
            (this.arrButtons[MenuConstants.mainMenu.NEW_GAME] as Button).setIcon(iconName);
            this.isNewGame = true;
        } else {
            this.scene.game.events.emit(GameProcessEvents.STAT_NEW_GAME);
        }
    }

    private onStartTutorial(): void {
        this.scene.game.events.emit(GameProcessEvents.START_TUTORIAL);
    }

    private onOpenScores(): void {
        this.scene.game.events.emit(MenuEvents.SHOW_SUBMENU, MenuConstants.subMenus.SCORES);
    }

    private onOpenOptions(): void {
        this.scene.game.events.emit(MenuEvents.SHOW_SUBMENU, MenuConstants.subMenus.OPTIONS);
    }

    //  
    public init(): void {
        this.scene.game.events.emit(
            MenuEvents.CHANGE_TITLE,
            Language.getText(Language.MENU)
        );
        this.scene.game.events.emit(
            MenuEvents.RESET_MENU_BUTTONS,
            {
                menu: this,
                buttons: this.arrButtons
            } as IResetButtonsVO
        );

        super.init();

        this.arrButtons[MenuConstants.mainMenu.NEW_GAME].text = Language.getText(Language.NEW_GAME);
        const iconName: string = ButtonIconsUtils.getIconAnimationName("d");
        (this.arrButtons[MenuConstants.mainMenu.NEW_GAME] as Button).setIcon(iconName);
        this.isNewGame = false;
    }

    public language(): void {
        this.arrButtons[MenuConstants.mainMenu.CONTINUE_GAME].text = Language.getText(Language.CONTINUE);
        this.arrButtons[MenuConstants.mainMenu.NEW_GAME].text = Language.getText(Language.NEW_GAME);
        this.arrButtons[MenuConstants.mainMenu.TUTORIAL].text = Language.getText(Language.TUTORIAL);
        this.arrButtons[MenuConstants.mainMenu.SCORES].text = Language.getText(Language.SCORES);
        this.arrButtons[MenuConstants.mainMenu.OPTIONS].text = Language.getText(Language.OPTIONS);
    }
}