import { Game } from "../../exports";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";
import { GameProcessEvents } from "../events/GameProcessEvents";
import { GameDataModel } from "./GameDataModel";

export class GameProcessModel {

    private static instance: GameProcessModel;

    private game: Game;

    public gameOver: boolean;
    public isTutorial: boolean;
    public isNewGame: boolean;
    public addingMonsters: boolean;
    public addingInRoom: boolean;
    public dontMove: boolean;
    public dontMoveInRoom: boolean;
    public isBlackthorn: boolean;
    public monsterInGame: number;
    public monsterInGameTotal: number;
    public countOfTurns: number;
    public turns: number;
    public arrNextMonsters: string[];
    private _score: number;

    public elvisIsDel: boolean;
    public monsterIsDel: boolean;

    private _advice: number;

    constructor(game: Game) {
        if (!GameProcessModel.instance) {
            GameProcessModel.instance = this;

            this.game = game;
            this.monsterInGameTotal = 3;
        }
        return GameProcessModel.instance;
    }

    public createNextMonsters(): void {
        this.arrNextMonsters = [];

        for (let i: number = 0; i < this.monsterInGameTotal; i++) {
            this.arrNextMonsters[i] = ColorIDsUtils.getRandomColor();
        }
        if (!this.isTutorial) {
            this.game.events.emit(GameProcessEvents.SET_NEXT_MONSTERS, this.arrNextMonsters);
        }
    }

    public resetScore(score: number = 0): void {
        this._score = score;
        this.game.events.emit(GameProcessEvents.INIT_SCORE);
    }

    public get advice(): number {
        return this._advice;
    }

    public set advice(value: number) {
        this._advice = value;
    }

    public set score(value: number) {
        this._score = value;
        GameDataModel.score = this.score;
        this.game.events.emit(GameProcessEvents.UPDATE_SCORE);
    }

    public get score(): number {
        return this._score;
    }
}