import { Container, Scene, BitmapText, TimerEvent } from "../../exports";

export class Tablo extends Container {

    private static COLOR_BLINK: number = 0xFFFF00;
    private static BLINK_INTERVAL: number = 100;

    private tfAmount: BitmapText;
    private color: number;
    private score: number;
    private scoreLimit: number;

    private timer: TimerEvent;

    constructor(scene: Scene, hAlign: string, color: number = 0xFFFF99, colorBot: number = 0xFF9900, size: number = 60) {
        super(scene);
        this.color = color;

        this.tfAmount = this.scene.add.bitmapText(0, 0, "aivaGold", "", size);
        if (hAlign === "left") {
            this.tfAmount.setOrigin(0, .5);
            this.tfAmount.setLeftAlign();
        } else {
            this.tfAmount.setOrigin(1, .5);
            this.tfAmount.setRightAlign();
        }
        this.add(this.tfAmount);

        this.timer = this.scene.time.addEvent({
            delay: Tablo.BLINK_INTERVAL,
            callback: this.onTick,
            callbackScope: this,
            loop: true
        });
        this.timer.paused = true;

        this.init();
    }

    public init(score: number = 0): void {
        this.score = score - 1;
    }

    public free(): void {
        this.tfAmount.text = "";
        this.timer.paused = true;
    }

    public setScoreNow(score: number): void {
        this.score = score;
        this.tfAmount.text = this.score.toString();
    }

    public setScore(score: number): void {
        this.scoreLimit = score;
        this.blick();
        this.timer.paused = false;
    }

    public blick(): void {
        this.tfAmount.tint = Tablo.COLOR_BLINK;
    }

    public stopBlick(): void {
        this.tfAmount.tint = 0xFFFFFF;
    }

    private onTick(): void {
        if (this.score < this.scoreLimit) {
            this.score++;
            this.setScoreNow(this.score);
        } else {
            this.stopBlick();
            this.timer.paused = true;
        }
    }
}