import { GameDataModel } from "../../data/models/GameDataModel";
import { Container, Image } from "../../exports";
import { MenuEvents } from "../../menu/events/MenuEvents";
import { AmbientSoundPlugin } from "../../plugins/sounds/AmbientSoundPlugin";
import { IMuteSound } from "../../scenes/interfaces/IMuteSound";
import { BaseScene } from "../../scenes/views/BaseScene";
import { AMath } from "../../utils/AMath";
import { IMove } from "../interfaces/IMove";
import { SoundToggle } from "../views/SoundToggle";
import { ScrollMonster } from "./ScrollMonster";

export class MoveH extends Container implements IMove {

    protected scene: BaseScene;

    private toggleId: string;

    private scrollMonster: ScrollMonster;

    private ambientSoundPlugin: AmbientSoundPlugin;

    private _volumeLevel: number;
    private _percent: number;

    constructor(scene: BaseScene, toggleId: string, color: string) {
        super(scene);

        this.ambientSoundPlugin = this.scene.plugins.get("ambientSoundPlugin") as AmbientSoundPlugin;

        this.toggleId = toggleId;

        const circleL: Image = this.scene.add.image(-SoundToggle.RANGE / 2, 0, "menu", "soundToggleCircle");
        circleL.setOrigin(.5);

        const circleR: Image = this.scene.add.image(SoundToggle.RANGE / 2, 0, "menu", "soundToggleCircle");
        circleR.setOrigin(.5);

        this.scrollMonster = new ScrollMonster(scene, color);
        this.scrollMonster.init();
        this.add([circleL, circleR, this.scrollMonster]);
    }

    public startMoveScroll(): void {
        this.scrollMonster.startMove();
    }

    public stopMoveScroll(): void {
        this.scrollMonster.stopMove();
    }

    public update(x1: number, x2: number): void {
        const val: number = x2 - x1;
        this.calc(
            val > SoundToggle.RANGE
                ? SoundToggle.RANGE
                : val < 0
                    ? 0
                    : val
        );
    }

    public saveVolume(): void {
        this.toggleId === SoundToggle.SOUNDS
            ? GameDataModel.volumeSounds = this._volumeLevel
            : GameDataModel.volumeMusic = this._volumeLevel;
        this.scene.gameDataModel.saveData();
    }

    private calc(val: number): void {
        this.scrollMonster.move(val - SoundToggle.RANGE_HALF);

        this._volumeLevel = AMath.toPercent(val, SoundToggle.RANGE);
        this._percent = Math.floor(this._volumeLevel * 100);

        if (this.toggleId === SoundToggle.MUSIC) {
            this.ambientSoundPlugin.setVolume(this._volumeLevel);
        }

        this.scene.game.events.emit(
            MenuEvents.MUTE_SOUNDS,
            {
                toggleId: this.toggleId,
                muted: this._volumeLevel <= 0
            } as IMuteSound
        );
    }

    public set volumeLevel(val: number) {
        this.calc(val * SoundToggle.RANGE);
    }

    public get volumeLevel(): number { return this._volumeLevel; }
    public get percent(): number { return this._percent; }
}