import { BitmapText, Container, Scene, Tween } from "../../exports";
import { ComponentPool } from "../../utils/ComponentPool";
import { MapModel } from "../models/MapModel";
import { MapEvents } from "../events/MapEvents";
import { IMapCellData } from "../interfaces/IMapCellData";

export class CostPerMonster extends Container {

    private static DIASPPEAR_DURATION: number = 1000;

    private componentPool: ComponentPool = new ComponentPool();

    private tf: BitmapText;
    private tween: Tween;

    constructor(scene: Scene) {
        super(scene);

        // ! we need change the font aivaGold to aivaBlue
        this.tf = this.scene.add.bitmapText(0, 0, "aivaGold", "", 60).setOrigin(.5);
        this.add(this.tf);
        // this.tf.tint = 0x00FFFF;
    }

    public init(tileX: number, tileY: number, txt: string, color: string): void {
        //   
        this.x = MapModel.toPix(tileX);
        this.y = MapModel.toPix(tileY);

        this.tf.text = txt;
        this.scaleX = this.scaleY = 1;

        this.tween = this.scene.add.tween({
            targets: this,
            scaleX: .3,
            scaleY: .3,
            duration: CostPerMonster.DIASPPEAR_DURATION,
            onComplete: () => {
                const mapCellData: IMapCellData = {
                    tileX: MapModel.toTile(this.x),
                    tileY: MapModel.toTile(this.y),
                    color: color
                };
                this.scene.game.events.emit(MapEvents.ADD_STARS, mapCellData);
                this.free();
            }
        });
    }

    private removeTweens(): void {
        if (this.tween) {
            this.tween.remove();
        }
    }

    public free(): void {
        this.removeTweens();
        this.componentPool.dispose(this);
        this.parentContainer.remove(this);
    }
}