import { CursorEvents } from "../../cursor/events/CursorEvents";
import { GameDataModel } from "../../data/models/GameDataModel";
import { Container, Image, Scene } from "../../exports";
import { IPosition } from "../../monsters/interfaces/IPosition";
import { ITile } from "../../monsters/interfaces/ITile";
import { IAnimatable } from "../../scenes/interfaces/IAnimatable";
import { EnterFrameManager } from "../../scenes/managers/EnterFrameManager";
import { MapEvents } from "../events/MapEvents";
import { MapModel } from "../models/MapModel";

export class Choice extends Container implements IAnimatable {

    private mapModel: MapModel;

    private deltaTime: number;
    private scaleChoice: number;
    private angleChoice: number;

    constructor(scene: Scene) {
        super(scene);

        this.mapModel = new MapModel();

        const img1: Image = this.scene.add.image(0, 0, "monstersInit2", "choise");
        const img2: Image = this.scene.add.image(0, 0, "monstersInit2", "choise");
        img2.rotation = Phaser.Math.DegToRad(45);

        this.add([
            img1,
            img2
        ]);
        this.visible = false;
    }

    public activate(): void {
        this.scene.game.events.addListener(MapEvents.CHOICE_SHOW, this.onShow, this);
        this.scene.game.events.addListener(MapEvents.CHOICE_HIDE, this.onHide, this);
        this.scene.game.events.addListener(MapEvents.CHOICE_RESET, this.onReset, this);
    }

    public deactivate(): void {
        this.scene.game.events.removeListener(MapEvents.CHOICE_SHOW, this.onShow, this);
        this.scene.game.events.removeListener(MapEvents.CHOICE_HIDE, this.onHide, this);
        this.scene.game.events.removeListener(MapEvents.CHOICE_RESET, this.onReset, this);
    }

    public onUpdate(time: number): void {
        this.deltaTime += time;
        this.angleChoice++;

        if (this.deltaTime >= 100) {
            this.deltaTime %= 100;
        }
        this.scaleChoice = Math.cos(this.deltaTime * 4) * .25 + 1.1;
        this.scaleX = this.scaleY = this.scaleChoice;
        this.rotation = Phaser.Math.DegToRad(this.angleChoice);
    }

    private init(): void {
        this.deltaTime = 0;
        this.angleChoice = 0;
        this.visible = true;
        this.scaleX = this.scaleY = this.scaleChoice;
        EnterFrameManager.add(this);
    }

    private free(): void {
        this.visible = false;
        EnterFrameManager.remove(this);
    }

    private onShow(position: IPosition): void {
        this.x = position.x;
        this.y = position.y;
        this.init();
        this.scene.sound.play("Choice", { volume: GameDataModel.volumeSounds });
    }

    private onHide(tile: ITile): void {
        this.free();
        this.mapModel.isGreen = false;
        this.mapModel.isMagic = false;
        this.mapModel.isKill = false;

        if (tile.x > -1 && tile.y > -1) {
            this.scene.game.events.emit(CursorEvents.RESET);
        }
    }

    private onReset(): void {
        const tile: ITile = {
            x: -1,
            y: -1
        };
        const n: number = this.mapModel.allMonsters.length;

        //   
        for (let i: number = 0; i < n; i++) {
            if (this.mapModel.allMonsters[i].select) {
                tile.y = MapModel.toTile(this.mapModel.allMonsters[i].y);
                tile.x = MapModel.toTile(this.mapModel.allMonsters[i].x);
                break;
            }
        }
        this.onHide(tile);
    }
}