    protected buildScene(): void {
        this.ambientSoundPlugin = this.plugins.get("ambientSoundPlugin") as AmbientSoundPlugin;

        this.resetMenuManager = new ResetMenuManager(this.game);
        this.gameProcessModel = new GameProcessModel(this.game);

        this.title = new TitleMenu(this);
        this.title.x = this.gameWidth / 2;
        this.title.y = 25;
        this.resizeContainer.add(this.title);

        this.scoreMenu = new ScoreMenu(this);
        this.resizeContainer.add(this.scoreMenu);

        this.optionsMenu = new OptionsMenu(this);
        this.resizeContainer.add(this.optionsMenu);

        this.mainMenu = new MainMenu(this);
        this.resizeContainer.add(this.mainMenu);
    }

    //   
    private hideAllMenus(): void {
        this.hideSubmenu(MenuConstants.subMenus.MAIN);
        this.hideSubmenu(MenuConstants.subMenus.SCORES);
        this.hideSubmenu(MenuConstants.subMenus.OPTIONS);
    }

    //   
    public showSubmenu(menuId: number): void {
        this.getSubMenuById(menuId).init();
    }

    //   
    public hideSubmenu(menuId: number): void {
        this.getSubMenuById(menuId).hide();
    }

    private getSubMenuById(menuId: number): SubMenu {
        switch (menuId) {
            case MenuConstants.subMenus.MAIN:
                return this.mainMenu;

            case MenuConstants.subMenus.OPTIONS:
                return this.optionsMenu;

            case MenuConstants.subMenus.SCORES:
                return this.scoreMenu;
        }
        return this.mainMenu;
    }

