    //  
    public activate(): void {
        super.activate();
        this.ambientSoundPlugin.setActive("menuMusic");
        
        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_PROFILE_BUTTON, true);
        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_SOCIAL_BUTTONS, true);

        this.game.events.addListener(MenuEvents.SHOW_SUBMENU, this.showSubmenu, this);
        this.game.events.addListener(MenuEvents.HIDE_SUBMENU, this.hideSubmenu, this);

        this.game.events.addListener(GameProcessEvents.START_TUTORIAL, this.startTutorial, this);
        this.game.events.addListener(GameProcessEvents.STAT_NEW_GAME, this.startNewGame, this);
        this.game.events.addListener(GameProcessEvents.CONTINUE_GAME, this.continueGame, this);

        this.resetMenuManager.init();
        this.title.init();

        this.hideAllMenus();
        // this.createButterfly();

        this.showSubmenu(MenuConstants.subMenus.MAIN);

        /*if(Main.admob != null)
            Main.admob.showAdTopCenter();
    	
        if(Main.admob == null)
            Main.free();
    	
        if(AdMob.isSupported)
            Main.admob.showSmartBanner();*/
    }

    public deactivate(): void {
        this.resetMenuManager.free();
        this.title.free();

        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_SOCIAL_BUTTONS, false);
        this.game.events.removeListener(MenuEvents.SHOW_SUBMENU, this.showSubmenu, this);
        this.game.events.removeListener(MenuEvents.HIDE_SUBMENU, this.hideSubmenu, this);

        this.game.events.removeListener(GameProcessEvents.START_TUTORIAL, this.startTutorial, this);
        this.game.events.removeListener(GameProcessEvents.STAT_NEW_GAME, this.startNewGame, this);
        this.game.events.removeListener(GameProcessEvents.CONTINUE_GAME, this.continueGame, this);
    }

    public language(): void {
        this.mainMenu.language();
        this.scoreMenu.language();
        this.optionsMenu.language();
    }

