import { Container, Sprite } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";

export class ScrollMonster extends Container {

    private sprite: Sprite;
    private color: string;

    constructor(scene: BaseScene, animName: string) {
        super(scene);

        this.color = animName.toUpperCase();

        this.sprite = this.scene.add.sprite(0, 0, "");
        this.sprite.y = -40;

        this.add([
            this.scene.add.image(0, 0, "menu", "soundTogglePoint"),
            this.sprite
        ]);
    }

    public init(): void {
        this.sprite.play(this.setAnim(this.color));
    }

    public free(): void {
        this.sprite.anims.stop();
    }

    public setIcon(animName: string): void {
        this.sprite.play(this.setAnim(animName), true, this.getRandomFrame());
    }

    private getRandomFrame(): number {
        return Phaser.Math.Between(0, this.sprite.anims.getTotalFrames() - 1) || 0;
    }

    public startMove(): void {
        this.setIcon("move" + this.color);
        this.sprite.anims.setRepeat(-1);
    }

    public stopMove(): void {
        this.setIcon(this.color);
    }

    public move(posX: number): void {
        this.x = posX;
    }

    private setAnim(animName: string): string {
        let animNameFull: string = animName;
        switch (animName) {
            case "Y": animNameFull = "animMusic"; break;
            case "O": animNameFull = "animSounds"; break;
        }
        return animNameFull;
    }
}