export class AMath {

    public static distance(x1: number, y1: number, x2: number, y2: number): number {
        const X1X2: number = Math.abs(x1 - x2); //       
        const Y1Y2: number = Math.abs(y1 - y2); //       Y
        return Math.sqrt(Math.pow(X1X2, 2) + Math.pow(Y1Y2, 2));
    }

    public static random(lower: number, upper: number, float = 0): number {
        const grade: number = Math.pow(10, float);
        return (Math.floor(((upper - lower) * grade + .99) * Math.random() + (lower * grade))) / grade;
    }

    public static toDegrees(radians: number): number {
        return radians * (180 / Math.PI);
    }

    public static toRadians(degrees: number): number {
        return degrees * (Math.PI / 180);
    }

    public static equal(a: number, b: number, diff: number = 0.00001): boolean {
        return (Math.abs(a - b) <= diff);
    }

    public static getAngleRad(x1: number, y1: number, x2: number, y2: number, norm: boolean = true): number {
        const dx: number = x2 - x1;
        const dy: number = y2 - y1;
        let angle: number = Math.atan2(dy, dx);

        if (norm) {
            if (angle < 0) {
                angle = Math.PI * 2 + angle;
            } else if (angle >= Math.PI * 2) {
                angle = angle - Math.PI * 2;
            }
        }
        return angle;
    }

    public static getAngleDeg(x1: number, y1: number, x2: number, y2: number, norm: boolean = true): number {
        const dx: number = x2 - x1;
        const dy: number = y2 - y1;
        let angle: number = Math.atan2(dy, dx) / Math.PI * 180;

        if (norm) {
            if (angle < 0) {
                angle = 360 + angle;
            } else if (angle >= 360) {
                angle = angle - 360;
            }
        }
        return angle;
    }

    public static toPercent(current: number, total: number): number {
        return current / total;
    }

    public static fromPercent(percent: number, total: number): number {
        return total / 100 * percent;
    }

    public static summ(values: number[]): number {
        let s: number = 0;
        const n: number = values.length;

        for (let i: number = 0; i < n; i++) {
            s += values[i];
        }
        return s;
    }

    public static aver(values: number[], count: number = -1): number {
        let s: number = 0;
        const n: number = values.length;
        let aver: number;

        for (let i: number = 0; i < n; i++) {
            s += values[i];
        }
        aver = s / n;

        if (count === -1) {
            return aver;
        }
        const round: number = Math.pow(10, count);

        return Math.round(aver * round) / round;
    }
}