import { Scene } from "../../exports";
import { AssetTypes } from "../constants";
import { ILoadingAsset } from "../interfaces/ILoadingAsset";

export class LoadingAssetsModel {

    private static readonly preloadingAssets: ILoadingAsset[] = [
        { type: AssetTypes.ATLAS_XML, key: "preloader", url: "./assets/img/preloader.xml", textureUrl: "./assets/img/preloader.png" },
        { type: AssetTypes.FONT_BMP, key: "aivaStroke", url: "./assets/fonts/fontAivaStroke.fnt", textureUrl: "./assets/fonts/fontAivaStroke.png" }
    ];

    private static readonly initLoadingAssets: ILoadingAsset[] = [
        { type: AssetTypes.FONT_BMP, key: "aivaWhite", url: "./assets/fonts/fontAivaWhite.fnt", textureUrl: "./assets/fonts/fontAivaWhite.png" },
        { type: AssetTypes.FONT_BMP, key: "aivaGold", url: "./assets/fonts/fontAivaGold.fnt", textureUrl: "./assets/fonts/fontAivaGold.png" },

        { type: AssetTypes.ATLAS_XML, key: "backgrounds", url: "./assets/img/backgrounds.xml", textureUrl: "./assets/img/backgrounds.png" },
        { type: AssetTypes.ATLAS_JSON, key: "profiles", url: "./assets/img/profiles.json", textureUrl: "./assets/img/profiles.png" },
        { type: AssetTypes.ATLAS_XML, key: "menu", url: "./assets/img/menu.xml", textureUrl: "./assets/img/menu.png" },
        { type: AssetTypes.ATLAS_JSON, key: "ui", url: "./assets/img/ui.json", textureUrl: "./assets/img/ui.png" },
        { type: AssetTypes.ATLAS_XML, key: "monstersInit1", url: "./assets/img/monstersInit1.xml", textureUrl: "./assets/img/monstersInit1.png" },
        { type: AssetTypes.ATLAS_XML, key: "monstersInit2", url: "./assets/img/monstersInit2.xml", textureUrl: "./assets/img/monstersInit2.png" },
        { type: AssetTypes.ATLAS_XML, key: "monstersInit3", url: "./assets/img/monstersInit3.xml", textureUrl: "./assets/img/monstersInit3.png" },
        { type: AssetTypes.ATLAS_XML, key: "scores", url: "./assets/img/scores.xml", textureUrl: "./assets/img/scores.png" },
        { type: AssetTypes.ATLAS_JSON, key: "spark", url: "./assets/img/spark.json", textureUrl: "./assets/img/spark.png" },

        { type: AssetTypes.AUDIO, key: "menuMusic", audioUrl: ["./assets/music/Menu.mp3"] },
        { type: AssetTypes.AUDIO, key: "gameMusic", audioUrl: ["./assets/music/Game.mp3"] },

        { type: AssetTypes.AUDIO, key: "Move", audioUrl: ["./assets/sounds/Move.mp3"] },
        { type: AssetTypes.AUDIO, key: "Choice", audioUrl: ["./assets/sounds/Choice.mp3"] },
        { type: AssetTypes.AUDIO, key: "Bell", audioUrl: ["./assets/sounds/Bell.mp3"] },
        { type: AssetTypes.AUDIO, key: "Bonus", audioUrl: ["./assets/sounds/Bonus.mp3"] },
        { type: AssetTypes.AUDIO, key: "Key", audioUrl: ["./assets/sounds/Key.mp3"] },
        { type: AssetTypes.AUDIO, key: "Spark", audioUrl: ["./assets/sounds/Spark.mp3"] },
        { type: AssetTypes.AUDIO, key: "QuestComplete", audioUrl: ["./assets/sounds/QuestComplete.mp3"] },
        { type: AssetTypes.AUDIO, key: "Portal", audioUrl: ["./assets/sounds/Portal.mp3"] },
        { type: AssetTypes.AUDIO, key: "Abort", audioUrl: ["./assets/sounds/Abort.mp3"] },
        { type: AssetTypes.AUDIO, key: "Ingame", audioUrl: ["./assets/sounds/Ingame.mp3"] },
        { type: AssetTypes.AUDIO, key: "GClickAnim3", audioUrl: ["./assets/sounds/GClickAnim3.mp3"] },
        { type: AssetTypes.AUDIO, key: "OClickAnim1", audioUrl: ["./assets/sounds/OClickAnim1.mp3"] },
        { type: AssetTypes.AUDIO, key: "OOverAnim2", audioUrl: ["./assets/sounds/OOverAnim2.mp3"] },
        { type: AssetTypes.AUDIO, key: "POverAnim3", audioUrl: ["./assets/sounds/POverAnim3.mp3"] },
        { type: AssetTypes.AUDIO, key: "YClickAnim2", audioUrl: ["./assets/sounds/YClickAnim2.mp3"] },
        { type: AssetTypes.AUDIO, key: "YInsult", audioUrl: ["./assets/sounds/YInsult.mp3"] },

        { type: AssetTypes.HTML, key: "playerName", url: "./assets/html/signUp.html" }
    ];

    private scene: Scene;

    constructor(scene: Scene) {
        this.scene = scene;
    }

    public loadPreloadAssets(): void {
        this.loadAssets(LoadingAssetsModel.preloadingAssets);
    }
    
    public loadInitialAssets(): void {
        this.loadAssets(LoadingAssetsModel.initLoadingAssets);
    }

    private loadAssets(assets: ILoadingAsset[]): void {
        assets.forEach((asset: ILoadingAsset) => {
            switch (asset.type) {
                case AssetTypes.ATLAS_XML:
                    this.scene.load.atlasXML(asset.key, asset.textureUrl, asset.url);
                    break;

                case AssetTypes.ATLAS_JSON:
                    this.scene.load.atlas(asset.key, asset.textureUrl, asset.url);
                    break;

                case AssetTypes.AUDIO:
                    this.scene.load.audio(asset.key, asset.audioUrl);
                    break;

                case AssetTypes.FONT_BMP:
                    this.scene.load.bitmapFont(asset.key, asset.textureUrl, asset.url);
                    break;

                case AssetTypes.HTML:
                    this.scene.load.html(asset.key, asset.url);
                    break;

                default:
                    console.error(`${asset.type} is not define!`);
                    break;
            }
        });
    }
}