import { Language } from "../../content/Language";
import { BitmapText, Sprite } from "../../exports";
import { PopupConstants } from "../../popups/constants";
import { IPopupOptions } from "../../popups/interfaces/IPopupOptions";
import { Button } from "../../ui/views/Button";
import { LoginEvents } from "../events/LoginEvents";
import { SignUpInBase } from "./SignUpInBase";
import { PopupEvents } from "../../popups/events/PopupEvents";

export class SignIn extends SignUpInBase {

    private monster: Sprite;
    private tfName: BitmapText;

    private btnBack: Button;
    private btnPlay: Button;
    private btnDelete: Button;

    private showMessageBox: boolean;
    private playerName: string;
    private animName: string;

    constructor(scene: Phaser.Scene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        // 
        this.monster = this.scene.add.sprite(0, 0, "").setScale(1.8).play("redUp");
        this.monster.y = -40;

        //    
        this.tfName = this.scene.add.bitmapText(0, 70, "aivaGold", "", 70).setOrigin(.5);

        //  
        this.btnBack = new Button(this.scene, "btnPink", "", () => this.back());
        this.btnBack.addLabel("BACK", "aivaStroke", 36, 0, -12);
        this.btnBack.y = -180;
        this.arrButtons.push(this.btnBack);

        //   
        this.btnDelete = new Button(this.scene, "btnGreenPoint", "iconDel", () => this.areYouSure());
        this.btnDelete.y = this.btnBack.y;
        this.btnDelete.x = this.btnBack.x + this.btnBack.getBounds().width / 2 + this.btnDelete.getBounds().width / 2 + 10;
        this.arrButtons.push(this.btnDelete);

        //      
        this.btnPlay = new Button(this.scene, "btnGreen", "", () => this.playGame());
        this.btnPlay.addLabel("PLAY", "aivaStroke", 36, 0, -12);
        this.btnPlay.y = 180;
        this.arrButtons.push(this.btnPlay);

        this.add([
            this.monster,
            this.tfName,
            this.btnBack,
            this.btnDelete,
            this.btnPlay
        ]);
    }

    public resetData(animName: string, playerName: string): void {
        this.animName = animName;
        this.playerName = playerName;
    }

    public init(): void {
        this.monster.play(this.animName + "Up");
        this.tfName.text = this.playerName;

        super.init();
    }
    
    public show(): void {
        this.monster.play(this.animName + "Up");
        super.show();
    }

    public free(): void {
        if (this.showMessageBox) {
            this.showMessageBox = false;

            const popupOptions: IPopupOptions = {
                id: PopupConstants.ids.POPUP_APPROVE,
                yes: () => this.scene.events.emit(LoginEvents.DELETE_PROFILE),
                no: () => this.show()
            };
            this.scene.game.events.emit(PopupEvents.SHOW_POPUP, popupOptions);
        }
        this.monster.anims.stop();
        super.free();
    }

    private back(): void {
        this.scene.events.emit(LoginEvents.BACK_TO_LOGIN);
        this.hide();
    }

    private areYouSure(): void {
        this.showMessageBox = true;
        this.hide();
    }

    private playGame(): void {
        this.scene.events.emit(LoginEvents.PLAY_GAME);
        this.hide();
    }

    public language(): void {
        this.btnPlay.text = Language.getText(Language.PLAY);
        this.btnBack.text = Language.getText(Language.BACK);
    }
}