import { Point } from "../../exports";
import { Monster } from "../../monsters/views/Monster";
import { IAimMonsterData } from "../interfaces/IAimMonsterData";

export class MapModel {

    public static readonly MAP_POS: number = 245;

    public static readonly MAP_WIDTH_MAX: number = 9;
    public static readonly MAP_HEIGHT_MAX: number = 9;
    public static readonly MAP_WIDTH_MAX_INDEX: number = MapModel.MAP_WIDTH_MAX - 1;
    public static readonly MAP_HEIGHT_MAX_INDEX: number = MapModel.MAP_HEIGHT_MAX - 1;
    public static readonly MAP_CELL_SIZE: number = 70;
    public static readonly MAP_CELL_HALF: number = 35;

    private static instance: MapModel;

    private _mapMask: number[][];
    private _mapColor: string[][];
    private _mapMonsters: Monster[][];
    private _allMonsters: Monster[];

    private countMonsterMax: number;
    private _countMonster: number;

    public mapMaskTemp: number[][];

    public isGreen: boolean;
    public isMagic: boolean;
    public isKill: boolean;

    public activeMonster: Monster;
    public aimMonster: Monster;

    public pointStart: Point;
    public pointEnd: Point;
    public pointMagic: Point;
    public pointCursour: Point;

    public aimMonsterData: IAimMonsterData;

    constructor() {
        if (!MapModel.instance) {
            MapModel.instance = this;
            this.countMonsterMax = MapModel.MAP_WIDTH_MAX * MapModel.MAP_HEIGHT_MAX;

            this.pointStart = new Point(0, 0);
            this.pointEnd = new Point(0, 0);
            this.pointCursour = new Point(0, 0);
            this.pointMagic = new Point(0, 0);

            this.aimMonsterData = {
                isAimed: false,
                position: new Point(),
                cost: 0
            };

            this._allMonsters = [];
        }
        return MapModel.instance;
    }

    public static toTile(value: number): number {
        if (value > 0) {
            return Math.floor(value / MapModel.MAP_CELL_SIZE);
        }
        return -1;
    }

    public static toPix(value: number): number {
        return MapModel.MAP_CELL_HALF + value * MapModel.MAP_CELL_SIZE;
    }

    public static inMap(i: number, j: number): boolean {
        return (i >= 0 && j >= 0 && i < MapModel.MAP_HEIGHT_MAX && j < MapModel.MAP_WIDTH_MAX);
    }

    public createMap(): void {
        this.freeAllMonsters();

        this._mapMask = [];
        this._mapColor = [];
        this._mapMonsters = [];

        for (let ay: number = 0; ay < MapModel.MAP_HEIGHT_MAX; ay++) {
            this._mapMask[ay] = [];
            this._mapColor[ay] = [];
            this._mapMonsters[ay] = [];

            for (let ax: number = 0; ax < MapModel.MAP_WIDTH_MAX; ax++) {
                this._mapMask[ay][ax] = 0;
                this._mapColor[ay][ax] = "";
                this._mapMonsters[ay][ax] = null;
            }
        }
    }

    private freeAllMonsters(): void {
        this.activeMonster = null;
        this._allMonsters.forEach((monster: Monster) => {
            monster.free();
        });
        this._allMonsters.length = 0;
    }

    public leavePlace(tileY: number, tileX: number): void {
        this._mapMask[tileY][tileX] = 0;
        this._mapColor[tileY][tileX] = "";
        this._mapMonsters[tileY][tileX] = null;
    }

    public takePlace(monster: Monster): void {
        const tileY: number = monster.point.y;
        const tileX: number = monster.point.x;
        this._mapMask[tileY][tileX] = 1;
        this._mapColor[tileY][tileX] = monster.color;
        this._mapMonsters[tileY][tileX] = monster;
    }

    public addMonster(monster: Monster): void {
        this._allMonsters.push(monster);
    }

    public removeMonster(monster: Monster): void {
        const indexMonster: number = this._allMonsters.indexOf(monster);
        if (indexMonster >= 0) {
            this._allMonsters.splice(indexMonster, 1);
        }
    }

    public calcMonsters(): void {
        this._countMonster = 0;
        const n: number = this.mapMask.length;
        for (let i: number = 0; i < n; i++) {
            const m: number = this.mapMask[i].length;
            for (let j: number = 0; j < m; j++) {
                if (this.mapMask[i][j] === 1 || this.mapMask[i][j] === -1) {
                    this._countMonster++;
                }
            }
        }
    }

    public calcMonstersColor(color: string): number {
        let count: number = 0;
        const n: number = this.mapColor.length;
        for (let i: number = 0; i < n; i++) {
            const m: number = this.mapColor[i].length;
            for (let j: number = 0; j < m; j++) {
                if (this.mapColor[i][j] === color) {
                    count++;
                }
            }
        }
        return count;
    }

    public get mapMask(): number[][] {
        return this._mapMask;
    }

    public get mapColor(): string[][] {
        return this._mapColor;
    }

    public get mapMonsters(): Monster[][] {
        return this._mapMonsters;
    }

    public get allMonsters(): Monster[] {
        return this._allMonsters;
    }

    public get countMonster(): number {
        return this._countMonster;
    }

    public get mapIsFull(): boolean {
        return this.countMonster >= this.countMonsterMax;
    }
}