import { GameProcessModel } from "../../data/models/GameProcessModel";
import { Point } from "../../exports";
import { MapEvents } from "../../map/events/MapEvents";
import { MapModel } from "../../map/models/MapModel";
import { MapView } from "../../map/views/MapView";
import { MonsterColor, MonsterConstants } from "../../monsters/constants";
import { TutorialEvents } from "../../panels/events/TutorialEvents";
import { PanelTutorialBottom } from "../../panels/views/PanelTutorialBottom";
import { PanelTutorialTop } from "../../panels/views/PanelTutorialTop";
import { ScenesConstants } from "../constants";
import { TutorialSceneEvents } from "../events/TutorialSceneEvents";
import { UISceneEvents } from "../events/UISceneEvents";
import { BaseScene } from "./BaseScene";

export class TutorialScene extends BaseScene {

    public static readonly TUTORIAL_1: number = 1;
    public static readonly TUTORIAL_2: number = 2;
    public static readonly TUTORIAL_3: number = 3;
    public static readonly TUTORIAL_8: number = 8;
    public static readonly TUTORIAL_9: number = 9;
    public static readonly TUTORIAL_10: number = 10;
    public static readonly TUTORIAL_11: number = 11;
    public static readonly TUTORIAL_12: number = 12;
    public static readonly TUTORIAL_13: number = 13;
    public static readonly TUTORIAL_14: number = 14;

    private panelTutorialTop: PanelTutorialTop;
    private panelTutorialBottom: PanelTutorialBottom;
    private mapView: MapView;

    private gameProcessModel: GameProcessModel;
    private mapModel: MapModel;

    protected autoActivated: boolean = false;

    constructor() {
        super(ScenesConstants.names.tutorial);
    }

    protected buildScene(): void {
        this.gameProcessModel = new GameProcessModel(this.game);
        this.mapModel = new MapModel();

        this.panelTutorialTop = new PanelTutorialTop(this);
        this.panelTutorialBottom = new PanelTutorialBottom(this);
    }

    public setMapView(mapView: MapView): void {
        this.mapView = mapView;
    }

    public activate(): void {
        super.activate();
        this.resizeContainer.add([
            this.panelTutorialTop,
            this.mapView,
            this.panelTutorialBottom
        ]);

        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_PROFILE_BUTTON, false);
        this.game.events.emit(MapEvents.CHANGE_VISIBILITY_MAP_CELLS, true);

        this.game.events.addListener(TutorialEvents.CHANGE_TUTORIAL, this.onChangeTutorial, this);
        this.game.events.addListener(TutorialSceneEvents.AUTO_CHANGE_ADVICE, this.onAutoChangeAdvice, this);

        this.panelTutorialTop.init();
        this.panelTutorialBottom.init();

        this.getAdvice();
    }

    public deactivate(): void {
        this.panelTutorialTop.free();
        this.panelTutorialBottom.free();

        this.resizeContainer.remove([
            this.panelTutorialTop,
            this.mapView,
            this.panelTutorialBottom
        ]);

        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_MAP_CELLS_TOGGLE, false);
        this.game.events.emit(UISceneEvents.CHANGE_VISIBILITY_PROFILE_BUTTON, true);

        this.game.events.removeListener(TutorialEvents.CHANGE_TUTORIAL, this.onChangeTutorial, this);
        this.game.events.removeListener(TutorialSceneEvents.AUTO_CHANGE_ADVICE, this.onAutoChangeAdvice, this);
    }

    private onChangeTutorial(step: number): void {
        this.gameProcessModel.advice += step;
        this.getAdvice();
    }

    private getAdvice(): void {
        this.panelTutorialBottom.hideCountMonster();
        this.game.events.emit(MapEvents.CHOICE_RESET);
        this.mapModel.createMap();
        this.mapView.navigator.free();

        this.gameProcessModel.monsterInGame = this.gameProcessModel.monsterInGameTotal;

        this.panelTutorialTop.updatePanel(this.gameProcessModel.advice);
        this.panelTutorialBottom.updatePanel(this.gameProcessModel.advice);

        this.setConfigAdvice();
    }

    public setConfigAdvice(): void {
        this.gameProcessModel.dontMoveInRoom = true;
        this.gameProcessModel.addingInRoom = true;
        this.gameProcessModel.countOfTurns = 3;
        this.gameProcessModel.turns = 0;

        if (this.gameProcessModel.advice > TutorialScene.TUTORIAL_1) {
            this.gameProcessModel.dontMoveInRoom = false;
        }
        if (this.gameProcessModel.advice > TutorialScene.TUTORIAL_2) {
            this.gameProcessModel.addingInRoom = false;
        }
        switch (this.gameProcessModel.advice) {
            case TutorialScene.TUTORIAL_1:
            case TutorialScene.TUTORIAL_2:
                break;

            case TutorialScene.TUTORIAL_3:
                this.mapModel.mapColor[2][2] = MonsterColor.PURPLE;
                this.mapModel.mapColor[1][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[3][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[4][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[5][7] = MonsterColor.PURPLE;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                break;

            // orange
            case TutorialScene.TUTORIAL_8:
                this.mapModel.mapColor[2][2] = MonsterColor.ORANGE;
                this.mapModel.mapColor[2][3] = MonsterColor.BLUE;
                this.mapModel.mapColor[1][3] = MonsterColor.PURPLE;
                this.mapModel.mapColor[0][3] = MonsterColor.DARK;
                this.mapModel.mapColor[2][5] = MonsterColor.YELLOW;
                this.mapModel.mapColor[3][5] = MonsterColor.RED;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.ORANGE;
                this.mapModel.mapColor[3][7] = MonsterColor.ORANGE;
                this.mapModel.mapColor[4][7] = MonsterColor.ORANGE;
                this.mapModel.mapColor[5][7] = MonsterColor.ORANGE;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(6, MonsterConstants[MonsterColor.ORANGE].countInLineMin);
                break;

            // red
            case TutorialScene.TUTORIAL_9:
                this.mapModel.mapColor[2][2] = MonsterColor.RED;
                this.mapModel.mapColor[2][3] = MonsterColor.BLUE;
                this.mapModel.mapColor[1][3] = MonsterColor.PURPLE;
                this.mapModel.mapColor[0][3] = MonsterColor.DARK;
                this.mapModel.mapColor[2][5] = MonsterColor.YELLOW;
                this.mapModel.mapColor[3][5] = MonsterColor.ORANGE;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.RED;
                this.mapModel.mapColor[3][7] = MonsterColor.RED;
                this.mapModel.mapColor[4][7] = MonsterColor.RED;
                this.mapModel.mapColor[5][7] = MonsterColor.RED;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(6, MonsterConstants[MonsterColor.RED].countInLineMin);
                break;

            // blue
            case TutorialScene.TUTORIAL_10:
                this.mapModel.mapColor[1][1] = MonsterColor.ORANGE;
                this.mapModel.mapColor[1][2] = MonsterColor.PURPLE;
                this.mapModel.mapColor[1][3] = MonsterColor.RED;
                this.mapModel.mapColor[2][1] = MonsterColor.DARK;
                this.mapModel.mapColor[2][2] = MonsterColor.BLUE;
                this.mapModel.mapColor[2][3] = MonsterColor.ORANGE;
                this.mapModel.mapColor[3][1] = MonsterColor.RED;
                this.mapModel.mapColor[3][2] = MonsterColor.YELLOW;
                this.mapModel.mapColor[3][3] = MonsterColor.PURPLE;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.BLUE;
                this.mapModel.mapColor[3][7] = MonsterColor.BLUE;
                this.mapModel.mapColor[4][7] = MonsterColor.BLUE;
                this.mapModel.mapColor[5][7] = MonsterColor.BLUE;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(6, MonsterConstants[MonsterColor.BLUE].countInLineMin);
                break;

            // purple
            case TutorialScene.TUTORIAL_11:
                this.mapModel.mapColor[2][2] = MonsterColor.PURPLE;
                this.mapModel.mapColor[0][3] = MonsterColor.DARK;
                this.mapModel.mapColor[2][3] = MonsterColor.BLUE;
                this.mapModel.mapColor[3][2] = MonsterColor.RED;
                this.mapModel.mapColor[1][3] = MonsterColor.ORANGE;
                this.mapModel.mapColor[3][4] = MonsterColor.YELLOW;
                this.mapModel.mapColor[2][5] = MonsterColor.DARK;
                this.mapModel.mapColor[1][5] = MonsterColor.RED;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[3][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[4][7] = MonsterColor.PURPLE;
                this.mapModel.mapColor[5][7] = MonsterColor.PURPLE;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(6, MonsterConstants[MonsterColor.PURPLE].countInLineMin);
                break;

            // dark
            case TutorialScene.TUTORIAL_12:
                this.mapModel.mapColor[1][6] = MonsterColor.DARK;
                this.mapModel.mapColor[2][6] = MonsterColor.PURPLE;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.DARK;
                this.mapModel.mapColor[3][7] = MonsterColor.DARK;
                this.mapModel.mapColor[4][7] = MonsterColor.DARK;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[1][6], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(5, MonsterConstants[MonsterColor.DARK].countInLineMin);
                break;

            // green
            case TutorialScene.TUTORIAL_13:
                this.mapModel.mapColor[2][2] = MonsterColor.GREEN;
                this.mapModel.mapColor[2][3] = MonsterColor.BLUE;
                this.mapModel.mapColor[1][3] = MonsterColor.PURPLE;
                this.mapModel.mapColor[0][3] = MonsterColor.DARK;
                this.mapModel.mapColor[2][5] = MonsterColor.YELLOW;
                this.mapModel.mapColor[3][5] = MonsterColor.RED;
                //
                this.mapModel.mapColor[1][7] = MonsterColor.GREEN;
                this.mapModel.mapColor[2][7] = MonsterColor.ORANGE;
                this.mapModel.mapColor[3][7] = MonsterColor.GREEN;
                this.mapModel.mapColor[4][7] = MonsterColor.GREEN;
                this.mapModel.mapColor[5][7] = MonsterColor.GREEN;
                this.mapModel.mapColor[6][7] = MonsterColor.GREEN;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 2));
                this.panelTutorialBottom.showCountMonster(7, MonsterConstants[MonsterColor.GREEN].countInLineMin);
                break;

            // yellow
            case TutorialScene.TUTORIAL_14:
                this.mapModel.mapColor[2][2] = MonsterColor.YELLOW;
                this.mapModel.mapColor[2][3] = MonsterColor.BLUE;
                this.mapModel.mapColor[1][3] = MonsterColor.PURPLE;
                this.mapModel.mapColor[0][3] = MonsterColor.DARK;
                this.mapModel.mapColor[2][5] = MonsterColor.RED;
                this.mapModel.mapColor[3][5] = MonsterColor.ORANGE;
                //
                this.mapModel.mapColor[2][7] = MonsterColor.BLUE;
                this.mapModel.mapColor[3][7] = MonsterColor.RED;
                this.mapModel.mapColor[4][7] = MonsterColor.YELLOW;
                this.mapView.loadMonsters();
                this.mapView.navigator.init(this.mapModel.mapMonsters[2][2], new Point(7, 1));
                this.panelTutorialBottom.showCountMonster(5, MonsterConstants[MonsterColor.YELLOW].countInLineMin);
                break;
        }
        this.gameProcessModel.addingMonsters = this.gameProcessModel.addingInRoom;
    }

    private onAutoChangeAdvice(): void {
        switch (this.gameProcessModel.advice) {
            case TutorialScene.TUTORIAL_3:
                if (this.mapModel.calcMonstersColor(MonsterColor.PURPLE) === 0) {
                    this.onChangeTutorial(1);
                }
                break;

            case TutorialScene.TUTORIAL_8:
                if (this.mapModel.calcMonstersColor(MonsterColor.ORANGE) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_9:
                if (this.mapModel.calcMonstersColor(MonsterColor.RED) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_10:
                if (this.mapModel.calcMonstersColor(MonsterColor.BLUE) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_11:
                if (this.mapModel.calcMonstersColor(MonsterColor.PURPLE) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_12:
                if (this.mapModel.calcMonstersColor(MonsterColor.DARK) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_13:
                if (this.mapModel.calcMonstersColor(MonsterColor.GREEN) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                setTimeout(() => this.onChangeTutorial(1), 1000);
                break;

            case TutorialScene.TUTORIAL_14:
                if (this.mapModel.calcMonstersColor(MonsterColor.YELLOW) === 0) {
                    this.panelTutorialTop.butPrev.visible = false;
                }
                this.panelTutorialTop.butNext.visible = false;
                this.panelTutorialBottom.hideCountMonster();
                this.panelTutorialTop.butMenu.deactivate();
                setTimeout(() => this.panelTutorialTop.exitFromTutorial(), 3000);
                break;
        }
    }
}
