import { Container } from "../../exports";
import { Button } from "../../ui/views/Button";
import { ChangeVisibilityUtils } from "../../utils/ChangeVisibilityUtils";

export class SignUpInBase extends Container {

    protected arrButtons: Button[] = [];

    public init(): void {
        this.setEnableButtons(false);
        this.show();
    }
    
    public free(): void {
        this.visible = false;
    }

    public show(): void {
        this.alpha = 0;
        ChangeVisibilityUtils.show(this.scene, this, 500, () => this.addListeners());
    }

    public hide(): void {
        this.removeListeners();
        ChangeVisibilityUtils.hide(this.scene, this, 300, () => this.free());
    }

    protected addListeners(): void {
        this.setEnableButtons(true);
    }

    protected removeListeners(): void {
        this.setEnableButtons(false);
    }

    private setEnableButtons(isEnabled: boolean): void {
        this.arrButtons.forEach((button: Button) => {
            isEnabled ? button.activate() : button.deactivate();
        });
    }
}