import { Container, Scene } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { BtnLarge } from "../../ui/views/BtnLarge";
import { SoundToggle } from "../../ui/views/SoundToggle";
import { ContainerWithButtons } from "../views/ContainerWithButtons";
import { Button } from "../../ui/views/Button";
import { IInteractiveUI } from "../../ui/interfaces/IInteractiveUI";

export class MenuButtonsBuilder {

    //    
    public static createBtnLargeActive(
        scene: Scene,
        menu: ContainerWithButtons,
        menuButtonsList: IInteractiveUI[],
        iconColor: string = "u",
        command: () => void
    ): void {

        const btn: BtnLarge = new BtnLarge(scene, iconColor, "", command);
        menu.add(btn);
        menuButtonsList.push(btn);
    }

    //    
    public static createBtnLargePassive(
        scene: BaseScene,
        menu: ContainerWithButtons,
        menuButtonsList: IInteractiveUI[],
        toggleId: string,
        iconColor: string = "u"
    ): SoundToggle {

        const btn: SoundToggle = new SoundToggle(scene, toggleId, iconColor);
        menu.add(btn);
        menuButtonsList.push(btn);
        return btn;
    }

    //   
    public static createBtnSmall(
        scene: BaseScene,
        menu: ContainerWithButtons,
        menuButtonsList: IInteractiveUI[],
        command: () => void
    ): Button {

        const btn: Button = new Button(scene, "btnPink", "", command);
        btn.addLabel("", "aivaStroke", 36, 0, -12);
        btn.x = scene.gameWidth / 2;
        btn.y = 900;
        menu.add(btn);
        menuButtonsList.push(btn);
        return btn;
    }
}