import { Container } from "../../exports";
import { IInteractiveUI } from "../../ui/interfaces/IInteractiveUI";
import { Button } from "../../ui/views/Button";
import { ChangeVisibilityUtils } from "../../utils/ChangeVisibilityUtils";

export abstract class ContainerWithButtons extends Container {

	public arrButtons: IInteractiveUI[] = [];

	public init(): void {
		this.language();
		this.alpha = 0;
		ChangeVisibilityUtils.show(this.scene, this, 500, () => this.addListeners());
	}

	protected addListeners(): void {
		this.arrButtons.forEach((button: Button) => {
			button.activate();
		});
	}

	protected removeListeners(): void {
		this.arrButtons.forEach((button: Button) => {
			button.deactivate();
		});
	}

	public free(): void {
		this.removeListeners();
	}

	protected abstract language(): void;
}