import { LoadingAssetsModel } from "../../data/models/LoadingAssetsModel";
import { Scene } from "../../exports";
import { ScenesConstants } from "../constants";

export class BootScene extends Scene {

    constructor() {
        super(ScenesConstants.names.boot);
    }

    protected preload(): void {
        (new LoadingAssetsModel(this)).loadPreloadAssets();
    }

    protected create(): void {
        this.scene.start(ScenesConstants.names.manager);

        this.scene.start(ScenesConstants.names.background);
        this.scene.start(ScenesConstants.names.preloader);
        this.scene.start(ScenesConstants.names.popups);

        this.scene.remove(ScenesConstants.names.boot);
    }
}
