import { Game } from "../../exports";
import { IInteractiveUI } from "../../ui/interfaces/IInteractiveUI";
import { MenuConstants } from "../constants";
import { MenuEvents } from "../events/MenuEvents";
import { IResetButtonsVO } from "../interfaces/IResetButtonsVO";

export class ResetMenuManager {

    private game: Game;

    constructor(game: Game) {
        this.game = game;
    }

    public init(): void {
        this.game.events.addListener(MenuEvents.RESET_MENU_BUTTONS, this.onResetMenu, this);
    }

    public free(): void {
        this.game.events.removeListener(MenuEvents.RESET_MENU_BUTTONS, this.onResetMenu, this);
    }

    private onResetMenu(data: IResetButtonsVO): void {
        const countButtons: number = data.buttons.length - (data.odd || 0);

        const range: number = +this.game.config.width - data.buttons[0].width;
        const correction: number = data.buttons[0].width / 2;
        const buttonsIndecies: number[] = this.getAllButtonsIndecies(countButtons);
        
        for (let indexButton: number = 0; indexButton < countButtons; indexButton++) {
            
            const indexButtonTemp: number = buttonsIndecies.splice(Phaser.Math.Between(0, buttonsIndecies.length - 1), 1)[0];
            const button: IInteractiveUI = data.buttons[indexButtonTemp];
            
            data.menu.remove(button);
            button.y = MenuConstants.GAP_BETWEEN_BUTTONS * indexButton + MenuConstants.START_BUTTONS_POSITION_Y;
            button.x = range * Math.random() + correction;
            data.menu.add(button);
        }
    }

    private getAllButtonsIndecies(count: number): number[] {
        const arr: number[] = [];
        for (let i: number = 0; i < count; i++) {
            arr.push(i);
        }
        return arr;
    }
}