	public animName: string;

	private numberOfAnim: number;

	constructor(scene: BaseScene) {
		super(scene);

		this.mapModel = new MapModel();
		this.gameProcessModel = new GameProcessModel(this.scene.game);
		this.linefinder = new LinefinderUtils(this.scene.game);

		this.sprite = this.scene.add.sprite(0, 0, "");

		this.vector = new Vector2();
		this._point = new Point();

		this.numberOfAnim = 1;
	}

	public setMonsterData(model: IMonsterData): void {
		this.model = model;
	}

	public init(tileX: number, tileY: number): void {
		this._move = false;
		this._isFree = false;
		this._select = false;
		this._isWaiting = false;
		this._isOutGame = false;

		this.x = MapModel.toPix(tileX);
		this.y = MapModel.toPix(tileY);

		this._point.x = MapModel.toTile(this.x);
		this._point.y = MapModel.toTile(this.y);

		this._state = Monster.INGAME;
		this.switchAnim("ingame", false, 1.5);

		this.add(this.sprite);

		this.mapModel.addMonster(this);
		EnterFrameManager.add(this);

		this.updateDepth();
	}

	public onUpdate(delta: number): void {
		if (this._isFree) {
			this.free();
			return;
		}

		if (this.sprite.anims.getProgress() >= 1) {
			const tileX: number = MapModel.toTile(this.x);
			const tileY: number = MapModel.toTile(this.y);
			switch (this._state) {
				case Monster.OUT:
					break;

				case Monster.INGAME:
					this._state = Monster.OUT;
					this.switchAnim("out", false);
					if (this.gameProcessModel.isTutorial) {
						this.scene.game.events.emit(TutorialEvents.NAVIGATOR_SHOW, this);
					}
					break;

				case Monster.OUTGAME:
					this.mapModel.removeMonster(this);
					this._isFree = true;
					break;

				case Monster.PORTAL_VISIBLE:
					this._state = Monster.OUT;
					this.switchAnim("out", false);
					this.sprite.anims.reverse();
					this.gameProcessModel.dontMove = false;

					this.mapModel.mapMask[tileY][tileX] = 1;
					this.mapModel.mapColor[tileY][tileX] = this.color;
					this.mapModel.mapMonsters[tileY][tileX] = this;

					this.linefinder.searchMain(tileY, tileX, this.color, this.min, this.cost);

					this.updateDepth();

					if (this.gameProcessModel.isTutorial && this._state !== Monster.OUTGAME) {
						this.scene.game.events.emit(TutorialEvents.NAVIGATOR_SHOW, this);
					}
					break;

				case Monster.PORTAL_INVISIBLE:
					this.scene.game.events.emit(TutorialEvents.NAVIGATOR_HIDE);

					this.x = MapModel.toPix(this.mapModel.pointEnd.x);
					this.y = MapModel.toPix(this.mapModel.pointEnd.y);

					this._point.x = tileX;
					this._point.y = tileX;

					this._state = Monster.PORTAL_VISIBLE;
					this.sprite.anims.reverse();
					this.playSound(this.animName);
					break;

				case Monster.OVER:
					this._state = Monster.OVER_STOP;
					this.switchAnim("overStop", true);
					break;

				case Monster.CLICK:
					this._state = Monster.CLICK_STOP;
					this.switchAnim("clickStop", true);
					break;

				// 
				case Monster.ANIM_OUT:
					this._state = Monster.OUT;
					this.switchAnim("out", false);
					break;

				case Monster.ANIM_OVER:
					this._state = Monster.OVER_STOP;
					this.switchAnim("overStop", true);
					break;

				case Monster.ANIM_CLICK:
					this._state = Monster.CLICK_STOP;
					this.switchAnim("clickStop", true);
					break;

				case Monster.ESCAPE:
					this._state = Monster.ESCAPE_STOP;
					this.switchAnim("escapeStop", true, 1.5);
					this.gameProcessModel.dontMove = true;
					this._move = true;
					break;

				case Monster.INSULT:
					this._state = Monster.OUT;
					this.switchAnim("out", false);
					break;

				case Monster.ABORT:
					this._state = Monster.CLICK_STOP;
					this.switchAnim("clickStop", true);
					break;
			}
		}

		// 
		if (this._move) {
			this._wayX = MapModel.toPix(this._way[this._step].x);
			this._wayY = MapModel.toPix(this._way[this._step].y);

			if (!AMath.equal(this.x, this._wayX, Math.abs(this.vector.x * delta))) {
				this.x += this.vector.x * delta;
			} else {
				this.x = this._wayX;
			}
			if (!AMath.equal(this.y, this._wayY, Math.abs(this.vector.y * delta))) {
				this.y += this.vector.y * delta;
			} else {
				this.y = this._wayY;
			}
			this.updateDepth();

			if (AMath.distance(this.x, this.y, this._wayX, this._wayY) <= 4) {
				if (this._step > 0) {
					const angle: number = AMath.getAngleRad(this._way[this._step].x, this._way[this._step].y, this._way[this._step - 1].x, this._way[this._step - 1].y);

					if (this._state === Monster.ESCAPE_STOP) {
						this.vector.setToPolar(angle, Monster.SPEED * .25);
					} else {
						this.vector.setToPolar(angle, Monster.SPEED);
					}
				}

				this._step--;

				if (this._step < 0) {
					this.x = MapModel.toPix(this._way[0].x);
					this.y = MapModel.toPix(this._way[0].y);

					this._point.x = MapModel.toTile(this.x);
					this._point.y = MapModel.toTile(this.y);

					this.updateDepth();

					this._move = false;
					this._way = [];

					this.sprite.off("animationcomplete", this.jump, this);
					this._state = Monster.OUT;
					this.switchAnim("out", false);

					this.mapModel.takePlace(this);

					this.gameProcessModel.dontMove = false;

					this.linefinder.searchMain(MapModel.toTile(this.y), MapModel.toTile(this.x), this.color, this.min, this.cost);

					if (this.gameProcessModel.isTutorial && this._state !== Monster.OUTGAME) {
						this.scene.game.events.emit(TutorialEvents.NAVIGATOR_SHOW, this);
					}
				}
			}
			return;
		}

		//   /  
		this.cursorReaction();
	}

	private updateDepth(): void {
		this._depth = this.x + this.y * 10;
		this.setDepth(this._depth);
		this.scene.game.events.emit(MapEvents.UPDATE_DEPTHS);
	}

