import { Scene } from "../../exports";
import { ScenesConstants } from "../constants";
import { SceneEvents } from "../events/SceneEvents";
import { BaseScene } from "./BaseScene";

export class SceneManager extends Scene {

    private currentSceneId: string;

    constructor() {
        super(ScenesConstants.names.manager);
    }

    public init(): void {
        this.game.events.addListener(SceneEvents.CHANGE_SCENE, this.onChangeScene, this);
    }

    public onChangeScene(sceneId: string): void {
        this.stopCurrentScene();
        this.currentSceneId = sceneId;
        this.startNewScene();
    }

    private stopCurrentScene(): void {
        if (this.currentSceneId) {
            this.scene.setActive(false, this.currentSceneId);
            this.scene.setVisible(false, this.currentSceneId);

            const currentScene: BaseScene = this.game.scene.getScene(this.currentSceneId) as BaseScene;
            currentScene.deactivate();
        }
    }

    private startNewScene(): void {
        const newScene: BaseScene = this.game.scene.getScene(this.currentSceneId) as BaseScene;
        if (newScene.isCreated) {
            newScene.activate();
        } else {
            newScene.launchInnerScenes();
            this.scene.launch(this.currentSceneId);
        }
        this.scene.setActive(true, this.currentSceneId);
        this.scene.setVisible(true, this.currentSceneId);
    }
}