import { Scene, Sprite, Image } from "../../exports";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";
import { Button } from "./Button";
import { ChangeVisibilityUtils } from "../../utils/ChangeVisibilityUtils";

export class ButtonProfile extends Button {

    private arrPos: string[] = ["Out", "Up"];
    protected icon: Sprite;

    constructor(scene: Scene, command?: () => void) {
        super(scene, "btnWoodSplit", "redUp", command);

        this.addLabel("???", "aivaStroke", 40, -54, -20);
        this.setScale(.6);
        this.alpha = 0;
    }

    protected createIcon(iconName: string): void {
        this.icon = this.scene.add.sprite(138, -20, iconName);
        this.buttonContainer.add(this.icon);
    }

    public setProfile(playerName: string, playerColor: string): void {
        this.text = playerName;
        const animationNamePrefix: string = ColorIDsUtils.getColorFromID(playerColor);
        const animationNamePostfix: string = this.arrPos[Phaser.Math.Between(0, this.arrPos.length - 1)];
        this.icon.play(animationNamePrefix + animationNamePostfix);
    }

    public show(): void {
        ChangeVisibilityUtils.show(this.scene, this, 500, () => this.activate());
    }

    public hide(): void {
        this.deactivate();
        ChangeVisibilityUtils.hide(this.scene, this, 500);
    }
}