import { GameDataModel } from "../../data/models/GameDataModel";
import { BasePlugin, BaseSoundManager, PluginManager, WebAudioSound, WebAudioSoundManager } from "../../exports";

export class AmbientSoundPlugin extends BasePlugin {

    private soundManager: BaseSoundManager;

    private currentSoundId: string;
    private currentSound: WebAudioSound;

    constructor(pluginManager: PluginManager) {
        super(pluginManager);
        this.soundManager = this.game.sound as WebAudioSoundManager;
    }

    public setActive(soundId: string): void {
        if (this.currentSoundId !== soundId) {
            this.stopSound();
            this.currentSoundId = soundId;
            this.currentSound = this.soundManager.add(this.currentSoundId, { volume: GameDataModel.volumeMusic, loop: true }) as WebAudioSound;
            this.playSound();
        }
    }

    public stopSound(): void {
        if (this.currentSound) {
            this.currentSound.stop();
        }
    }

    public playSound(): void {
        if (this.currentSound) {
            this.currentSound.play();
        }
    }

    public setVolume(volume: number): void {
        if (this.currentSound) {
            this.currentSound.setVolume(volume);
        }
    }
}