import "fpsmeter";

export class FPSMeterApp {

    private readonly fpsmeterOptions: FPSMeterOptions = {
        theme: "dark", // 'dark', 'light', 'transparent', 'colorful'
        heat: 1,
        graph: 1,
        history: 20,
        zIndex: 100,
        top: "auto",
        left: "auto",
        right: "auto",
        bottom: "0"
    };

    private readonly fpsmeter: FPSMeter;

    constructor() {
        const element: HTMLElement = document.getElementById("fps-meter") || document.body;
        this.fpsmeter = new FPSMeter(element, this.fpsmeterOptions);
        this.fpsmeter.show();
    }

    public tick(): void {
        this.fpsmeter.tick();
    }
}
