import { Language } from "../../content/Language";
import { BitmapText, Container } from "../../exports";
import { MapModel } from "../../map/models/MapModel";
import { BaseScene } from "../../scenes/views/BaseScene";

export class PanelTutorialBottom extends Container {

    protected scene: BaseScene;

    private tfInfo: BitmapText;

    private countMonsterInLine: Container;
    private tfCountMonsterInLine: BitmapText;

    constructor(scene: BaseScene) {
        super(scene);

        this.tfInfo = this.scene.add.bitmapText(30, 850, "aivaStroke", "", 38).setOrigin(0, 1);
        this.tfInfo.setTint(0xFFFFFF, 0xFFFFFF, 0xFFFF99, 0xFFFF99);
        this.tfInfo.setMaxWidth(420);
        this.tfInfo.setLeftAlign();
        this.add(this.tfInfo);

        this.countMonsterInLine = this.scene.add.container(0, 0);
        this.countMonsterInLine.x = MapModel.toPix(7);
        this.add(this.countMonsterInLine);

        this.countMonsterInLine.add(this.scene.add.image(0, 0, "ui", "labelLine"));

        this.tfCountMonsterInLine = this.scene.add.bitmapText(0, 35, "aivaStroke", "", 50).setOrigin(.5);
        this.countMonsterInLine.add(this.tfCountMonsterInLine);
    }

    public init(): void {
        this.hideCountMonster();
        this.visible = true;
    }

    public free(): void {
        this.visible = false;
    }

    public showCountMonster(tileY: number, count: number): void {
        this.countMonsterInLine.y = MapModel.toPix(tileY) + MapModel.MAP_POS - MapModel.MAP_CELL_HALF;
        this.countMonsterInLine.visible = true;
        this.tfCountMonsterInLine.text = count.toString();
    }

    public hideCountMonster(): void {
        this.countMonsterInLine.visible = false;
    }

    public updatePanel(advice: number): void {
        this.tfInfoText = Language.getText((Language as any)[`HELP${advice}`]);
    }

    public set tfInfoText(str: string) {
        this.tfInfo.text = str;
    }
}