import { App } from "../../App";
import { GameDataModel } from "../../data/models/GameDataModel";
import { Container, Graphics, Scene, Zone } from "../../exports";
import { IScale } from "../interfaces/IScale";

export abstract class BaseScene extends Scene {

    private readonly RESIZE_AREA_OPACITY: number = 0;

    public game: App;

    private _resizeContainer: Container;
    protected resizeZone: Zone;

    protected isMinScale: boolean = true;
    protected _isCreated: boolean = false;
    protected autoActivated: boolean = true;

    private _localScale: number;
    private _globalScale: number;

    protected preload(): void {

        const resizeArea: Graphics = this.add.graphics();
        resizeArea.fillStyle(0xFF00FF, this.RESIZE_AREA_OPACITY);
        resizeArea.fillRect(0, 0, this.gameWidth, this.gameHeight);

        this.resizeZone = this.add.zone(0, 0, this.gameWidth, this.gameHeight);

        this._resizeContainer = this.add.container(0, 0, resizeArea);
        this.preResize();
    }

    protected create(): void {
        if (!this.isCreated) {
            if (__DEV__) {
                const windowId = `s_${this.sys.config}`;
                (window as any)[windowId] = this;
            }
            this.buildScene();
            this._isCreated = true;
            if (this.autoActivated) {
                this.activate();
            }
        }
    }

    public launchInnerScenes(): void {

    }

    protected abstract buildScene(): void;

    public activate(): void {
        this.preResize();
    }

    public deactivate(): void {

    }

    private preResize(): void {
        const width: number = this.game.canvas.width;
        const height: number = this.game.canvas.height;
        const scaleX: number = width / this.gameWidth;
        const scaleY: number = height / this.gameHeight;

        const scale: IScale = {
            scaleMin: Math.min(scaleX, scaleY),
            scaleMax: Math.max(scaleX, scaleY)
        };

        this.resize(width, height, scale);
    }

    public resize(width: number, height: number, scaleMinMax: IScale): void {
        if (this.resizeContainer) {

            this._localScale = this.isMinScale ? scaleMinMax.scaleMin : scaleMinMax.scaleMax;
            this._globalScale = 1 / this._localScale;

            this.resizeContainer.scale = this.localScale;

            Phaser.Display.Align.In.Center(
                this.resizeContainer,
                this.add.zone(width * .5, height * .5, width, height)
            );

            const offsetX: number = (this.resizeZone.width * .5) * this.localScale;
            const offsetY: number = (this.resizeZone.height * .5) * this.localScale;

            this.resizeContainer.x -= offsetX;
            this.resizeContainer.y -= offsetY;
        }
    }

    public get gameWidth(): number {
        return this.sys.game.config.width as number;
    }

    public get gameHeight(): number {
        return this.sys.game.config.height as number;
    }

    public get gameDataModel(): GameDataModel {
        return this.game.gameDataModel;
    }

    public get isCreated(): boolean {
        return this._isCreated;
    }

    public get x(): number {
        return this.resizeContainer.x;
    }
    
    public get y(): number {
        return this.resizeContainer.y;
    }

    public get localScale(): number {
        return this._localScale;
    }
    
    public get globalScale(): number {
        return this._globalScale;
    }

    public get resizeContainer(): Container {
        return this._resizeContainer;
    }
}