import { Container } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Button } from "../../ui/views/Button";
import { SeasonsUtils } from "../../utils/SeasonsUtils";
import { SeasonsEvents } from "../events/SeasonsEvents";
import { GameDataModel } from "../../data/models/GameDataModel";

export class PanelSeasons extends Container {

	private readonly DX: number = 70;

	private vButtons: Button[] = [];
	// private gameDataModel: GameDataModel = new GameDataModel();

	private showPosY: number;
	private hidePosY: number;

	constructor(scene: BaseScene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
		super(scene, x, y, children);

		for (let indexSeason: number = 0; indexSeason < SeasonsUtils.SEASONS_COUNT; indexSeason++) {
			const btn: Button = this.createButton(SeasonsUtils.getSeasonNameByID(indexSeason), indexSeason);
			btn.x = indexSeason * this.DX;
			this.vButtons.push(btn);
		}
		this.showPosY = this.y;
		this.hidePosY = this.showPosY - 50;

		this.alpha = 0;
		this.y = this.hidePosY;
	}

	private createButton(seasonName: string, indexSeason: number): Button {
		const btn: Button = new Button(
			this.scene,
			"btnSeason" + seasonName,
			"icon" + seasonName, () => this.onChangeSeason(indexSeason)
		);
		this.add(btn);
		return btn;
	}

	public show(): void {
		this.scene.add.tween({
			targets: this,
			y: this.showPosY,
            alpha: 1,
            duration: 500,
            onComplete: () => this.addListeners()
        });
	}

	public hide(): void {
		this.removeListeners();
		this.scene.add.tween({
			targets: this,
			y: this.hidePosY,
            alpha: 0,
            duration: 500
        });
	}

	private addListeners(): void {
		for (let indexButton: number = 0; indexButton < SeasonsUtils.SEASONS_COUNT; indexButton++) {
			this.vButtons[indexButton].activate();
		}
	}

	private removeListeners(): void {
		for (let indexButton: number = 0; indexButton < SeasonsUtils.SEASONS_COUNT; indexButton++) {
			this.vButtons[indexButton].deactivate();
		}
	}

	private onChangeSeason(seasonId: number): void {
		GameDataModel.seasonId = seasonId;
		console.log(seasonId);
		this.scene.game.events.emit(SeasonsEvents.CHANGE_SEASON);
	}
}