import { Language } from "../../content/Language";
import { BitmapText, Container, Image } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Button } from "../../ui/views/Button";
import { Nexter } from "./Nexter";
import { Tablo } from "./Tablo";
import { SceneEvents } from "../../scenes/events/SceneEvents";
import { ScenesConstants } from "../../scenes/constants";

export class Panel extends Container {

    private butMenu: Button;

    private scoreBest: Tablo;
    private labelBest: BitmapText;

    private scoreNow: Tablo;
    private labelNow: BitmapText;

    private nexter: Nexter;

    protected scene: BaseScene;

    constructor(scene: BaseScene) {
        super(scene);

        const gameWidthHalf: number = this.scene.gameWidth * .5;

        const leftPanel: Image = this.scene.add.image(0, 0, "ui", "panelScore");
        leftPanel.setOrigin(1, 0);
        leftPanel.x = gameWidthHalf;
        leftPanel.y = 80;
        this.add(leftPanel);

        const rightPanel: Image = this.scene.add.image(0, 0, "ui", "panelScore");
        rightPanel.setOrigin(1, 0);
        rightPanel.scaleX = -1;
        rightPanel.x = gameWidthHalf;
        rightPanel.y = 80;
        this.add(rightPanel);

        var dX: number = 32;
        var dY: number = 68;
        var dYTablo: number = 35;
        var fontSize: number = 23;

        this.scoreBest = new Tablo(this.scene, "left", 0xFFFF00);
        this.scoreBest.x = leftPanel.x - leftPanel.width + dX;
        this.scoreBest.y = leftPanel.y + dYTablo;
        this.scoreBest.setScore(0);
        this.add(this.scoreBest);

        this.scoreNow = new Tablo(this.scene, "right");
        this.scoreNow.x = rightPanel.x + rightPanel.width - dX;
        this.scoreNow.y = rightPanel.y + dYTablo;
        this.scoreNow.setScore(0);
        this.add(this.scoreNow);

        this.labelBest = this.scene.add.bitmapText(
            leftPanel.x - leftPanel.width + dX,
            leftPanel.y + dY,
            "aivaStroke",
            "", fontSize
        ).setOrigin(0, 0);
        this.labelBest.setMaxWidth(130);
        this.labelBest.setLeftAlign();
        this.add(this.labelBest);

        this.labelNow = this.scene.add.bitmapText(
            rightPanel.x + rightPanel.width - dX,
            rightPanel.y + dY,
            "aivaStroke",
            "", fontSize
        ).setOrigin(1, 0);
        this.labelNow.setMaxWidth(130);
        this.labelNow.setRightAlign();
        this.add(this.labelNow);

        this.nexter = new Nexter(this.scene);
        this.nexter.x = gameWidthHalf;
        this.nexter.y = leftPanel.y + 130;
        this.add(this.nexter);

        this.butMenu = new Button(this.scene, "btnPink", "", () => this.onBack());
        this.butMenu.addLabel("MENU", "aivaStroke", 36, 0, -12);
        this.butMenu.x = gameWidthHalf
        this.butMenu.y = 45;
        this.add(this.butMenu);

        this.language();
    }

    public init(): void {
        this.visible = true;
        this.nexter.init();
        this.butMenu.activate();
    }

    public free(): void {
        this.visible = false;
        this.nexter.free();
        this.butMenu.deactivate();
    }

    public setScoreBest(score: number): void {
        this.scoreBest.setScoreNow(score);
    }

    public scoreInitialize(score: number): void {
        this.scoreNow.setScoreNow(score);
    }

    public setScore(score: number): void {
        this.scoreNow.setScore(score);
    }

    private onBack(): void {
        this.scene.game.events.emit(SceneEvents.CHANGE_SCENE, ScenesConstants.names.menu);
    }

    public language(): void {
        this.labelBest.text = Language.getText(Language.LABEL_BEST);
        this.labelNow.text = Language.getText(Language.LABEL_NOW);
        this.butMenu.text = Language.getText(Language.MENU);
        this.nexter.language();
    }
}