import { Button } from "./Button";
import { Tween, Scene } from "../../exports";

export class URLButton extends Button {
    
    private static readonly SHOWING_POSITION: number = -186;

    private url: string;
    private tween: Tween;
    private delay: number;
    
    constructor(scene: Scene, iconName: string, delay: number) {
        super(scene, "btnSocial", iconName, () => this.execute());
    
        this.delay = delay;
        this.url = "";
        this.alpha = 0;
    }

    public init(): void {
        this.removeTweens();
        this.tween = this.scene.add.tween({
            targets: this,
            y: URLButton.SHOWING_POSITION,
            alpha: 1,
            duration: 500,
            delay: this.delay,
            onComplete: () => this.activate()
        });
    }

    public free(): void {
        this.deactivate();
        this.removeTweens();
        this.tween = this.scene.add.tween({
            targets: this,
            y: 0,
            alpha: 0,
            duration: 500,
            delay: this.delay
        });
    }

    private removeTweens(): void {
        if (this.tween) {
            this.tween.remove();
        }
    }

    public addURL(url: string): void {
        this.url = url;
    }

    private execute(): void {
        window.open(this.url);
    }

    protected guard(): boolean {
        return this.url !== "";
    }
}