import { GameConfigs } from "./config/GameConfigs";
import { Language } from "./content/Language";
import { GameDataModel } from "./data/models/GameDataModel";
import { Game, Size } from "./exports";
import { AmbientSoundPlugin } from "./plugins/sounds/AmbientSoundPlugin";
import { ScenesConstants } from "./scenes/constants";
import { IScale } from "./scenes/interfaces/IScale";
import { BackgroundScene } from "./scenes/views/BackgroundScene";
import { BaseScene } from "./scenes/views/BaseScene";
import { BootScene } from "./scenes/views/BootScene";
import { GameScene } from "./scenes/views/GameScene";
import { LoginScene } from "./scenes/views/LoginScene";
import { MenuScene } from "./scenes/views/MenuScene";
import { PopupsScene } from "./scenes/views/PopupsScene";
import { PreloaderScene } from "./scenes/views/PreloaderScene ";
import { SceneManager } from "./scenes/views/SceneManager";
import { TutorialScene } from "./scenes/views/TutorialScene";
import { UIScene } from "./scenes/views/UIScene";
import { FPSMeterApp } from "./utils/FPSMeterApp";
import { SeasonsUtils } from "./utils/SeasonsUtils";
import { UniverseScene } from "./scenes/views/UniverseScene";
import { EnterFrameManager } from "./scenes/managers/EnterFrameManager";

export class App extends Game {

    public gameDataModel: GameDataModel = new GameDataModel();

    private meter: FPSMeterApp;

    constructor() {
        super(GameConfigs);

        this.loadData();
        this.loadLanguage();
        this.initPlugins();
        this.initScenes();

        this.incrementSeason();
    }

    private loadData(): void {
        this.gameDataModel.readData();
    }

    private loadLanguage(): void {
        switch (GameDataModel.language) {
            case "rus":
                Language.setRussian();
                break;

            default:
                Language.setEnglish();
                break;
        }
    }

    private initPlugins(): void {
        this.plugins.install("ambientSoundPlugin", AmbientSoundPlugin, true);
    }

    private initScenes(): void {
        this.scene.add(ScenesConstants.names.manager, SceneManager);
        this.scene.add(ScenesConstants.names.boot, BootScene);
        this.scene.add(ScenesConstants.names.background, BackgroundScene);
        this.scene.add(ScenesConstants.names.preloader, PreloaderScene);
        this.scene.add(ScenesConstants.names.login, LoginScene);
        this.scene.add(ScenesConstants.names.menu, MenuScene);
        this.scene.add(ScenesConstants.names.game, GameScene);
        this.scene.add(ScenesConstants.names.tutorial, TutorialScene);
        this.scene.add(ScenesConstants.names.universe, UniverseScene);
        this.scene.add(ScenesConstants.names.ui, UIScene);
        this.scene.add(ScenesConstants.names.popups, PopupsScene);
    }

    private incrementSeason(): void {
        GameDataModel.seasonId = (GameDataModel.seasonId + 1) % SeasonsUtils.SEASONS_COUNT;
        (new GameDataModel()).saveData();
    }

    protected start(): void {
        super.start();

        if (__DEV__) {
            this.meter = new FPSMeterApp();
        }
        this.scale.on("resize", this.onResizeGame, this);
        this.scene.start(ScenesConstants.names.boot);
    }

    public step(time: number, delta: number): void {
        super.step(time, delta);
        EnterFrameManager.update(delta);
        if (this.meter) {
            this.meter.tick();
        }
    }

    private onResizeGame(gameSize: Size): void {
        const width: number = gameSize.width;
        const height: number = gameSize.height;

        const scaleX: number = width / +this.config.width;
        const scaleY: number = height / +this.config.height;

        const scale: IScale = {
            scaleMin: Math.min(scaleX, scaleY),
            scaleMax: Math.max(scaleX, scaleY)
        };

        this.scene.scenes.forEach((scene: BaseScene) => {
            if (scene.resize && this.scene.isActive(scene.sys.config.toString())) {
                scene.resize(width, height, scale);
            }
        });
    }
}
