import { Language } from "../../content/Language";
import { Container } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Achievement } from "../../ui/components/Achievement";
import { Button } from "../../ui/views/Button";
import { SubMenu } from "./SubMenu";
import { MenuButtonsBuilder } from "../builders/MenuButtonsBuilder";
import { MenuEvents } from "../events/MenuEvents";
import { MenuConstants } from "../constants";
import { IPopupOptions } from "../../popups/interfaces/IPopupOptions";
import { PopupConstants } from "../../popups/constants";
import { PopupEvents } from "../../popups/events/PopupEvents";
import { IScore } from "../../data/interfaces/IScore";
import { GameDataModel } from "../../data/models/GameDataModel";

export class ScoreMenu extends SubMenu {

    public static COUNT_POSITIONS: number = 6;

    private readonly RANGE_Y: number = 7;
    private readonly DELTA_Y_FOR_TABLE: number = 5;

    protected scene: BaseScene;

    private spriteTable: Container;
    private table: Achievement[];

    private btnTrash: Button;
    private btnBack: Button;

    private index: number;

    constructor(scene: BaseScene) {
        super(scene);

        //  
        this.btnBack = MenuButtonsBuilder.createBtnSmall(this.scene, this, this.arrButtons, () => this.backToMainMenu());

        this.createTable();

        //   
        this.btnTrash = new Button(this.scene, "btnPinkPoint", "iconNo", () => this.clearScores());
        this.btnTrash.x = this.scene.gameWidth - 35;
        this.btnTrash.y = 250;
        this.add(this.btnTrash);
    }

    private backToMainMenu(): void {
        for (let i: number = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
            this.table[i].free();
        }
        this.scene.game.events.emit(
            MenuEvents.HIDE_SUBMENU,
            MenuConstants.subMenus.SCORES
        );
        this.scene.game.events.emit(
            MenuEvents.SHOW_SUBMENU,
            MenuConstants.subMenus.MAIN
        );
    }

    private clearScores(): void {
        this.spriteTable.alpha = 0;

        this.btnBack.deactivate();
        this.btnTrash.deactivate();

        const popupOptions: IPopupOptions = {
            id: PopupConstants.ids.POPUP_APPROVE,
            yes: () => this.clearScoresAnyway(),
            no: () => this.init()
        };
        this.scene.game.events.emit(PopupEvents.SHOW_POPUP, popupOptions);
    }

    private clearScoresAnyway(): void {
        // _universe.clearSharedObject();

        for (let i: number = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
            this.table[i].free();
        }
        this.init();
    }

    private createTable(): void {
        this.spriteTable = this.scene.add.container(this.scene.gameWidth * .5, this.scene.gameHeight * .5);
        this.add(this.spriteTable);

        let achievement: Achievement;

        this.table = [];

        for (let i: number = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
            achievement = new Achievement(this.scene);
            this.spriteTable.add(achievement);
            this.table.push(achievement);
        }
    }

    //  
    public init(index: number = -1): void {
        this.index = index;

        this.btnBack.activate();
        this.btnTrash.activate();

        this.spriteTable.alpha = 1;

        this.scene.game.events.emit(
            MenuEvents.CHANGE_TITLE,
            Language.getText(Language.SCORES)
        );
        this.scene.game.events.emit(
            MenuEvents.HIDE_SUBMENU,
            MenuConstants.subMenus.MAIN
        );

        super.init();

        this.showScores(index);
    }

    //  
    public showScores(indexActive: number = -1): void {
        // var read: SharedObject = SharedObject.getLocal("monsterLines");
        // var i: number;
        const index: number = 0;
        // var arrScores: Array = [];

        const state: string = "active";

        // if (read.data.arrScores != null) {
        //     arrScores = read.data.arrScores;
        //     for (i = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
        //         if (arrScores[i] != undefined) {
        //             color = state + arrScores[i][2].toString().toUpperCase() + this.round(i);
        //             this.updateAchevement(i, index, color, arrScores[i][1], arrScores[i][0], indexActive);
        //         } else {
        //             this.updateAchevement(i, index);
        //         }
        //     }
        // } else {
        //     index = 0;
        //     for (i = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
        //         this.updateAchevement(i, index);
        //     }
        // }
        for (let i = 0; i < ScoreMenu.COUNT_POSITIONS; i++) {
            const score: IScore = GameDataModel.scores[i];
            const color: string = score.monsterColor === "unknown"
                ? score.monsterColor
                : state + score.monsterColor.toUpperCase() + this.round(i);
            this.updateAchevement(i, index, color, score.playerName, score.score, indexActive);
        }
        this.refreshPositionsTable();
    }

    private updateAchevement(
        indexInTable: number,
        index: number,
        color: string = "u",
        playerName: string = "???",
        score: string = "---",
        isActive: number = -1
    ): void {
        this.table[indexInTable].init(color, playerName, score, isActive === indexInTable);

        let scale: number = 1 - Math.abs(indexInTable - index) * .1;
        scale = scale < .7 ? .7 : scale;

        this.table[indexInTable].scaleX = this.table[indexInTable].scaleY = scale;
    }

    private refreshPositionsTable(): void {
        this.table[0].y = 0;

        for (let i: number = 1; i < ScoreMenu.COUNT_POSITIONS; i++) {
            this.table[i].x = 0;
            this.table[i].y = this.table[i - 1].y + this.table[i - 1].height * .5 + this.table[i].height * .5 + this.RANGE_Y;
        }
        this.spriteTable.y = this.scene.gameWidth * .5 - this.spriteTable.height / 2 + this.DELTA_Y_FOR_TABLE;
    }

    private round(index: number): number {
        index++;
        index = index > 4 ? 4 : index;
        return index;
    }

    public language(): void {
        this.btnBack.text = Language.getText(Language.BACK);
    }
}