import { Language } from "../../content/Language";
import { GameDataModel } from "../../data/models/GameDataModel";
import { BitmapText, Image } from "../../exports";
import { PopupBase } from "./PopupBase";
import { SeasonsEvents } from "../../panels/events/SeasonsEvents";
import { IPopupOptions } from "../interfaces/IPopupOptions";

export class PopupInfo extends PopupBase {

    private sprite: Image;
    private imgHeader: Image;

    protected tfMessage: BitmapText;
    protected sizeForText: number = 35;

    constructor(scene: Phaser.Scene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        this.sprite = this.scene.add.image(0, 0, "ui", "messageBoxDialog");
        this.sprite.setOrigin(.5);

        this.tfMessage = this.scene.add.bitmapText(0, -50, "aivaStroke", "", this.sizeForText);
        this.tfMessage.setOrigin(.5, 0);

        this.imgHeader = this.scene.add.image(0, -35, "ui", "messageBoxDialogHeader0");
        this.imgHeader.setOrigin(.5, 1);

        this.add([
            this.sprite,
            this.tfMessage,
            this.imgHeader
        ]);
    }
    
    public callPopup(data?: IPopupOptions): void {
        this.onChangeSeason();
        this.init();
    }

    protected addListeners(): void {
        this.scene.game.events.addListener(SeasonsEvents.CHANGE_SEASON, this.onChangeSeason, this);
        super.addListeners();
	}

	protected removeListeners(): void {
        this.scene.game.events.removeListener(SeasonsEvents.CHANGE_SEASON, this.onChangeSeason, this);
        super.removeListeners();
    }
    
    private onChangeSeason(): void {
        this.imgHeader.setTexture("ui", "messageBoxDialogHeader" + GameDataModel.seasonId);
    }

    public language(): void {
        this.tfMessage.text = Language.getText(Language.LOADING);
    }
}