import { Point } from "../../exports";
import { ComponentPool } from "../../utils/ComponentPool";
import { MapModel } from "../models/MapModel";

export class PathfinderUtils {

    public static TYPE_NORMAL: string = "normal";
    public static TYPE_DIAGONAL: string = "diagonal";
    public static MAX_TURN: number = 50;

    private static instance: PathfinderUtils;

    private componentPool: ComponentPool = new ComponentPool();
    private mapModel: MapModel = new MapModel();
    
    private arrI: number[] = [-1, 0, 1, 0];
    private arrJ: number[] = [0, 1, 0, -1];
    private arrIDiagonal: number[] = [-1, 0, 1, 0, -1, -1, 1, 1];
    private arrJDiagonal: number[] = [0, 1, 0, -1, -1, 1, 1, -1];

    private activeArrayI: number[];
    private activeArrayJ: number[];

    private way: boolean;
    private turn: number;
    private turnMax: number;
    private step: number;

    private arrPoints: Point[] = [];
    private mapMaskStone: number[][];

    private pointStart: Point;
    private pointEnd: Point;

    constructor() {
        if (!PathfinderUtils.instance) {
            PathfinderUtils.instance = this;
        }
        return PathfinderUtils.instance;
    }

    private free(): void {
        this.arrPoints.forEach((point: Point) => this.componentPool.dispose(point));
        this.arrPoints.length = 0;
    }

    //      
    private clearMaskStone(): void {
        this.step = 1;
        this.way = false;
        this.mapMaskStone = [];

        //   
        for (let i: number = 0; i < this.mapModel.mapMask.length; i++) {
            this.mapMaskStone[i] = [];
            for (let j: number = 0; j < this.mapModel.mapMask[i].length; j++) {
                if (this.mapModel.mapMask[i][j] !== 0) {
                    this.mapMaskStone[i][j] = -1;
                } else {
                    this.mapMaskStone[i][j] = 0;
                }
            }
        }
        //  "0"   
        this.mapMaskStone[this.pointStart.y][this.pointStart.x] = 1;
    }

    public setStartAndEndPoints(pointStart: Point, pointEnd: Point): void {
        this.pointStart = pointStart;
        this.pointEnd = pointEnd;
    }

    public getWay(type: string, turnMax: number): Point[] {
        this.turnMax = turnMax;
        this.free();

        const point: Point = this.componentPool.get(Point);
        point.x = this.pointEnd.x;
        point.y = this.pointEnd.y;
        this.arrPoints.push(point);

        //    ,   
        if (type === PathfinderUtils.TYPE_NORMAL) {
            this.activeArrayI = this.arrI;
            this.activeArrayJ = this.arrJ;
        } else {
            this.activeArrayI = this.arrIDiagonal;
            this.activeArrayJ = this.arrJDiagonal;
        }
        //   
        this.clearMaskStone();

        this.turn = 0;

        while (this.turn < this.turnMax) {
            //    ,  
            if (!this.way) {
                this.step++;
                for (let i: number = 0; i < this.mapMaskStone.length; i++) {
                    for (let j: number = 0; j < this.mapMaskStone[i].length; j++) {
                        if (this.mapMaskStone[i][j] === this.step - 1) {
                            this.goWater(i, j);
                        }
                    }
                }

            } else {
                this.step--;

                //    
                this.searchStep(this.pointEnd.y, this.pointEnd.x, this.step);

                break;
            }

            this.turn++;
        }

        //    ()
        return this.arrPoints;
    }

    //  
    public goWater(i: number, j: number): void {
        for (let a: number = 0; a < this.activeArrayI.length; a++) {
            // ,    
            if (i + this.activeArrayI[a] === this.pointEnd.y && j + this.activeArrayJ[a] === this.pointEnd.x) {
                //  
                this.way = true;
                return;
            }

            if (MapModel.inMap(i + this.activeArrayI[a], j + this.activeArrayJ[a]) && //     
                this.mapMaskStone[i + this.activeArrayI[a]][j + this.activeArrayJ[a]] === 0 && //    
                this.mapModel.mapMask[i + this.activeArrayI[a]][j + this.activeArrayJ[a]] === 0) { //  

                this.mapMaskStone[i + this.activeArrayI[a]][j + this.activeArrayJ[a]] = this.step; //  
            }
        }
    }

    //        
    private searchStep(i: number, j: number, step: number): void {
        if (step <= 0) {
            return;
        }
        for (let a: number = 0; a < this.activeArrayI.length; a++) {
            if (MapModel.inMap(i + this.activeArrayI[a], j + this.activeArrayJ[a]) && this.mapMaskStone[i + this.activeArrayI[a]][j + this.activeArrayJ[a]] === step) {
                const point: Point = this.componentPool.get(Point);
                point.x = j + this.activeArrayJ[a];
                point.y = i + this.activeArrayI[a];

                this.arrPoints.push(point);
                this.step--;

                this.searchStep(i + this.activeArrayI[a], j + this.activeArrayJ[a], this.step);
                break;
            }
        }
    }
}
