import { Scene } from "phaser";
import { Color, Container, Image, Tween, Vector2 } from "../../exports";
import { ComponentPool } from "../../utils/ComponentPool";

export class Star extends Container {

    private static readonly POWER: number = 50;

    private componentPool: ComponentPool;
    private sprite: Image;

    private color: Color;
    private vector: Vector2;
    private tween: Tween;

    private speed: number;
    
    constructor(scene: Scene) {
        super(scene);
        this.componentPool = new ComponentPool();
        this.color = new Color();
        this.vector = new Vector2();
        this.sprite = this.scene.add.image(0, 0, "monstersInit3", "particle0");
        this.add(this.sprite);
    }

    public init(posX: number, posY: number, scale: number, power: number = 1): void {
        //   
        this.x = posX;
        this.y = posY;

        this.sprite.x = 0;
        this.sprite.y = 0;

        this.sprite.alpha = 0;
        this.sprite.scaleX = this.sprite.scaleY = scale;

        this.sprite.setTexture("monstersInit3", `particle${Phaser.Math.Between(0, 2)}`);

        var arrColors: number[] = [];
        arrColors.push(255 * Math.random()); // red
        arrColors.push(255 * Math.random()); // green
        arrColors.push(255 * Math.random()); // blue

        var index: number = 0;
        var max: number = arrColors[index];
        if (arrColors[1] > max) {
            max = arrColors[1];
            index = 1;
        }
        if (arrColors[2] > max) {
            max = arrColors[2];
            index = 2;
        }
        arrColors[index] = 254;

        this.color.setTo(arrColors[0], arrColors[1], arrColors[2]);
        this.sprite.tint = this.color.color;

        const startSpeed: number = (power - (1 - scale)) * Star.POWER;
        this.speed = Phaser.Math.Between(startSpeed * .8, startSpeed * 1.2);
        this.angle = Phaser.Math.DegToRad(360 * Math.random());

        this.vector.setToPolar(this.angle, this.speed);

        this.tween = this.scene.add.tween({
            targets: this.sprite,
            x: this.vector.x,
            y: this.vector.y,
            alpha: 1,
            duration: 500 * Math.random() + 250,
            ease: Phaser.Math.Easing.Sine.Out,
            onComplete: () => {
                this.free();
            }
        });
    }

    private removeTweens(): void {
        if (this.tween) {
            this.tween.remove();
        }
    }

    public free(): void {
        this.removeTweens();
        this.componentPool.dispose(this);
        this.parentContainer.remove(this);
    }
}