import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { IDeletedLine } from "../../data/interfaces/IDeletedLine";
import { GameProcessModel } from "../../data/models/GameProcessModel";
import { Game, Point } from "../../exports";
import { MonsterColor } from "../../monsters/constants";
import { Monster } from "../../monsters/views/Monster";
import { ComponentPool } from "../../utils/ComponentPool";
import { MapModel } from "../models/MapModel";

export class LinefinderUtils {

    private static instance: LinefinderUtils;

    private game: Game;

    private mapModel: MapModel = new MapModel();
    private componentPool: ComponentPool = new ComponentPool();
    private gameProcessModel: GameProcessModel;
    
    //   
    private _arrHorizontal: Point[];
    private _arrVertical: Point[];
    private _arrDiagonal1: Point[];
    private _arrDiagonal2: Point[];
    private _arrDelete: Point[];

    private _arr: Point[] = [];
    private _arrLeft: Point[] = [];
    private _arrRight: Point[] = [];
    private _left: boolean = false;
    private _right: boolean = false;
    private _n: number;
    private _m: number;
    private _indexI: number;
    private _indexJ: number;
    private _count: number;
    
    constructor(game: Game) {
        if (!LinefinderUtils.instance) {
            LinefinderUtils.instance = this;
            this.game = game;

            this.gameProcessModel = new GameProcessModel(this.game);
        }
        return LinefinderUtils.instance;
    }

    /**
     *        ( )
     */
    public searchMain(i: number, j: number, color: string, min: number, cost: number): void {
        // 
        if (color !== MonsterColor.YELLOW) {
            this.searchLines(i, j, color, min, cost);
        } else {
            this.searchElvis(i, j, color, min, cost);
        }
        this.searchAllElvis();
        
        this.game.events.emit(GameProcessEvents.END_TURN);
    }

    public searchAllElvis(): void {
        this.mapModel.allMonsters.forEach((monster: Monster) => {
            if (monster.color === MonsterColor.YELLOW) {
                this.searchElvis(MapModel.toTile(monster.y), MapModel.toTile(monster.x), monster.color, monster.min, monster.cost);
            }
        });
    }

    //      
    public searchLines(i: number, j: number, color: string, min: number, cost: number): void {
        this._arrHorizontal = this.getLine(i, j, color, min, "h");
        this._arrVertical = this.getLine(i, j, color, min, "v");
        this._arrDiagonal1 = this.getLine(i, j, color, min, "d1");
        this._arrDiagonal2 = this.getLine(i, j, color, min, "d2");

        this._arrDelete = [];
        const p: Point = this.componentPool.get(Point);
        p.x = j;
        p.y = i;

        this._arrDelete = this._arrHorizontal.concat(this._arrVertical, this._arrDiagonal1, this._arrDiagonal2);
        this._arrDelete.unshift(p);

        if (this._arrDelete.length >= min) {
            this.gameProcessModel.monsterIsDel = true;
            this.deleteLines(min, cost, color);
        }
    }

    //      
    public searchElvis(i: number, j: number, color: string, min: number, cost: number): void {
        this._arrHorizontal = this.getLineElvis(i, j, color, "h");
        this._arrVertical = this.getLineElvis(i, j, color, "v");
        this._arrDiagonal1 = this.getLineElvis(i, j, color, "d1");
        this._arrDiagonal2 = this.getLineElvis(i, j, color, "d2");

        this._arrDelete = [];
        const p: Point = this.componentPool.get(Point);
        p.x = j;
        p.y = i;

        this._arrDelete = this._arrHorizontal.concat(this._arrVertical, this._arrDiagonal1, this._arrDiagonal2);
        this._arrDelete.unshift(p);

        if (this._arrDelete.length >= min) {
            this.gameProcessModel.elvisIsDel = true;
            this.gameProcessModel.monsterIsDel = true;
            this.deleteLines(min, cost, color);
        }
    }

    private deleteLines(min: number, cost: number, color: string): void {
        const deletedLine: IDeletedLine = {
            line: this._arrDelete,
            min: min,
            cost: cost,
            color: color
        };
        this.game.events.emit(GameProcessEvents.DELETE_LINES, deletedLine);
    }

    //       
    private getLine(i: number, j: number, color: string, min: number, type: string): Point[] {
        //        
        this._arr = [];
        switch (type) {
            case "h":
                this._indexI = 0;
                this._indexJ = 1;
                break;

            case "v":
                this._indexI = 1;
                this._indexJ = 0;
                break;

            case "d1":
                this._indexI = -1;
                this._indexJ = 1;
                break;

            case "d2":
                this._indexI = 1;
                this._indexJ = 1;
                break;
        }

        this._n = this._indexI;
        this._m = this._indexJ;

        while (true) {
            if (MapModel.inMap(i + this._n, j + this._m)) {
                if (this.mapModel.mapColor[i + this._n][j + this._m] === color) {
                    const pr: Point = this.componentPool.get(Point);
                    pr.x = j + this._m;
                    pr.y = i + this._n;
                    this._arr.push(pr);
                    this._n += this._indexI;
                    this._m += this._indexJ;
                } else {

                    break;
                }
            } else {
                break;
            }
        }

        this._indexI *= -1;
        this._indexJ *= -1;

        this._n = this._indexI;
        this._m = this._indexJ;

        while (true) {
            if (MapModel.inMap(i + this._n, j + this._m)) {
                if (this.mapModel.mapColor[i + this._n][j + this._m] === color) {
                    const pl: Point = this.componentPool.get(Point);
                    pl.x = j + this._m;
                    pl.y = i + this._n;
                    this._arr.push(pl);
                    this._n += this._indexI;
                    this._m += this._indexJ;
                } else {
                    break;
                }
            } else {
                break;
            }
        }
        return this._arr.length >= min - 1 ? this._arr : [];
    }

    //        
    private getLineElvis(i: number, j: number, color: string, type: string): Point[] {
        this._arr = [];
        this._arrLeft = [];
        this._arrRight = [];
        this._left = false;
        this._right = false;

        //        
        switch (type) {
            case "h":
                this._indexI = 0;
                this._indexJ = 1;
                break;

            case "v":
                this._indexI = 1;
                this._indexJ = 0;
                break;

            case "d1":
                this._indexI = -1;
                this._indexJ = 1;
                break;

            case "d2":
                this._indexI = 1;
                this._indexJ = 1;
                break;
        }

        this._n = this._indexI;
        this._m = this._indexJ;
        this._count = 0;

        while (this._count < 3) {
            if (MapModel.inMap(i + this._n, j + this._m)) {
                if (this._count < 2) {
                    if (this.mapModel.mapMask[i + this._n][j + this._m] === 1) {
                        const p1: Point = this.componentPool.get(Point);
                        p1.x = j + this._m;
                        p1.y = i + this._n;
                        this._arrRight.push(p1);
                        this._n += this._indexI;
                        this._m += this._indexJ;
                    } else {
                        this._arrRight = [];
                        break;
                    }
                } else {
                    if (this.mapModel.mapColor[i + this._n][j + this._m] === color) {
                        const p2: Point = this.componentPool.get(Point);
                        p2.x = j + this._m;
                        p2.y = i + this._n;
                        this._arrRight.push(p2);
                        this._right = true;
                    }
                }
            }
            this._count++;
        }

        this._indexI *= -1;
        this._indexJ *= -1;

        this._n = this._indexI;
        this._m = this._indexJ;
        this._count = 0;

        while (this._count < 3) {
            if (MapModel.inMap(i + this._n, j + this._m)) {
                if (this._count < 2) {
                    if (this.mapModel.mapMask[i + this._n][j + this._m] === 1) {
                        const p3: Point = this.componentPool.get(Point);
                        p3.x = j + this._m;
                        p3.y = i + this._n;
                        this._arrLeft.push(p3);
                        this._n += this._indexI;
                        this._m += this._indexJ;
                    } else {
                        this._arrLeft = [];
                        break;
                    }
                } else {
                    if (this.mapModel.mapColor[i + this._n][j + this._m] === color) {
                        const p4: Point = this.componentPool.get(Point);
                        p4.x = j + this._m;
                        p4.y = i + this._n;
                        this._arrLeft.push(p4);
                        this._left = true;
                    }
                }
            }
            this._count++;
        }

        if (this._left) {
            this._arr = this._arr.concat(this._arrLeft);
        }
        if (this._right) {
            this._arr = this._arr.concat(this._arrRight);
        }

        return this._arr;
    }
}