import { Container, Scene, Sprite, Point } from "../../exports";
import { IAnimatable } from "../../scenes/interfaces/IAnimatable";
import { MapModel } from "../../map/models/MapModel";
import { AMath } from "../../utils/AMath";
import { EnterFrameManager } from "../../scenes/managers/EnterFrameManager";
import { CursorEvents } from "../events/CursorEvents";

export class Cursour extends Container implements IAnimatable {

    private mapModel: MapModel;

    private charge: Sprite;
    private loaderMagic: Sprite;

    private timerMagic: number;
    private timerMagicTotal: number = 50;

    constructor(scene: Scene) {
        super(scene);

        this.mapModel = new MapModel();

        this.charge = this.scene.add.sprite(0, 0, "monstersInit2", "sparkBall");
        this.charge.scaleX = this.charge.scaleY = 2.5;

        this.loaderMagic = this.scene.add.sprite(0, 0, "monstersInit3", "loaderMagic");
        this.loaderMagic.play("loaderMagic");
        this.loaderMagic.anims.pause();

        this.add([
            this.charge,
            this.loaderMagic
        ]);

        this.charge.setVisible(false);
        this.loaderMagic.setVisible(false);
    }

    public activate(): void {
        this.scene.game.events.addListener(CursorEvents.RESET, this.onReset, this);
        this.scene.game.events.addListener(CursorEvents.CHANGE_POSITION, this.onChangePosition, this);
        EnterFrameManager.add(this);
    }

    public deactivate(): void {
        EnterFrameManager.remove(this);
        this.scene.game.events.removeListener(CursorEvents.RESET, this.onReset, this);
        this.scene.game.events.removeListener(CursorEvents.CHANGE_POSITION, this.onChangePosition, this);
    }

    private onReset(): void {
        this.timerMagic = this.timerMagicTotal;
        if (this.loaderMagic.visible) {
            this.loaderMagic.setVisible(false);
        }
    }

    private onChangePosition(): void {
        this.x = MapModel.toPix(this.mapModel.pointCursour.x);
        this.y = MapModel.toPix(this.mapModel.pointCursour.y);
    }

    public onUpdate(deltaTime: number): void {
        if (this.mapModel.isGreen && this.mapModel.isMagic && !this.mapModel.isKill) {
            if (this.timerMagic > 0) {
                this.timerMagic--;
                this.loadMagic(this.timerMagic, this.timerMagicTotal);
            }
        }
        this.resetMagicCharge();
    }

    private resetMagicCharge(): void {
        if (this.mapModel.isMagic && !Point.Equals(this.mapModel.pointCursour, this.mapModel.pointMagic)) {
            if (this.mapModel.isGreen) {
                this.mapModel.isKill = false;
                this.mapModel.isMagic = false;
                this.onReset();
            }
        }
    }

    private loadMagic(time: number, timeTotal: number): void {
        if (time > 0) {
            if (!this.loaderMagic.visible) {
                this.loaderMagic.setVisible(true);
            }
            this.loaderMagic.anims.setProgress(AMath.toPercent(time, timeTotal));
        } else {
            this.mapModel.isKill = true;
            this.onReset();
            this.charge.setVisible(true);
            this.charge.play("sparkBall");
            this.charge.on("animationcomplete", this.onComplete, this);
        }
    }

    private onComplete(): void {
        this.charge.off("animationcomplete", this.onComplete, this);
        this.charge.anims.stop();
        this.charge.setVisible(false);
    }
}