import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { GameProcessModel } from "../../data/models/GameProcessModel";
import { MapView } from "../../map/views/MapView";
import { Panel } from "../../panels/views/Panel";
import { AmbientSoundPlugin } from "../../plugins/sounds/AmbientSoundPlugin";
import { ScenesConstants } from "../constants";
import { BaseScene } from "./BaseScene";
import { PanelQuest } from "../../panels/views/PanelQuest";
import { MapModel } from "../../map/models/MapModel";

export class GameScene extends BaseScene {

    private ambientSoundPlugin: AmbientSoundPlugin;

    private gameProcessModel: GameProcessModel;
    
    private panel: Panel;
    private panelQuest: PanelQuest;
    // private _gamePanel: GamePanel;

    private mapView: MapView;

    protected autoActivated: boolean = false;
    
    constructor() {
        super(ScenesConstants.names.game);
    }

    protected buildScene(): void {
        this.ambientSoundPlugin = this.plugins.get("ambientSoundPlugin") as AmbientSoundPlugin;
        this.gameProcessModel = new GameProcessModel(this.game);

        this.panel = new Panel(this);
        this.panelQuest = new PanelQuest(this);
        this.panelQuest.y = MapModel.MAP_POS + MapModel.MAP_CELL_SIZE * MapModel.MAP_HEIGHT_MAX;
        this.panelQuest.setPointsForMoveble();

        // this._gamePanel = new GamePanel();
        // this._gamePanel.x = 35;
        // this._gamePanel.y = 45;
        // this._gamePanel.init(new Point(this._gamePanel.x, this._gamePanel.y - 100), new Point(this._gamePanel.x, this._gamePanel.y));
        // this._gamePanel.hide();
        // this._gamePanel.addEventListener(Events.CHANGE_VISIBILITY_CELL, this.changeVisibilityCell);

        this.panel.free();
        this.panelQuest.free();
        // this._panelTutorial.free();
    }

    public setMapView(mapView: MapView): void {
        this.mapView = mapView;
    }

    public activate(): void {
        super.activate();
        this.panel.init();
        this.panelQuest.reset();
        this.panelQuest.init();
        this.panelQuest.startNewQuests();

        this.resizeContainer.add([
            this.panel,
            this.mapView,
            this.panelQuest
        ]);
        this.ambientSoundPlugin.setActive("gameMusic");

        this.game.events.addListener(GameProcessEvents.INIT_SCORE, this.onInitScore, this);
        this.game.events.addListener(GameProcessEvents.UPDATE_SCORE, this.onUpdateScore, this);
    }

    public deactivate(): void {
        this.game.events.removeListener(GameProcessEvents.INIT_SCORE, this.onInitScore, this);
        this.game.events.removeListener(GameProcessEvents.UPDATE_SCORE, this.onUpdateScore, this);

        this.panel.free();
        this.panelQuest.free();

        this.resizeContainer.remove([
            this.panel,
            this.mapView,
            this.panelQuest
        ]);
    }

    private onInitScore(): void {
        this.panel.scoreInitialize(this.gameProcessModel.score);
    }

    private onUpdateScore(): void {
        this.panel.setScore(this.gameProcessModel.score);
    }
}
