import { BitmapText, Container, Image, Scene } from "../../exports";

export class Quest extends Container {

    public static readonly SHOWING_DELAY: number = 250;
    public static readonly POS_Y: number = -20;

    private icon: Image;
    private tfCount: BitmapText;
    private tfPoints: BitmapText;
    private iks: Image;

    private _color: number;
    private _total: number;
    private _isComplete: boolean;

    constructor(scene: Scene) {
        super(scene);

        this.iks = this.scene.add.image(5, -30, "ui", "iks");
        this.iks.x = 5;
        this.iks.y = -30;
        this.add(this.iks);

        this.icon = this.scene.add.image(this.iks.x - 45, this.iks.y + 35, "ui", "next0");
        this.icon.setOrigin(.5, 1);
        this.icon.x = this.iks.x - 45;
        this.icon.y = this.iks.y + 35;
        this.add(this.icon);

        this.tfPoints = this.scene.add.bitmapText(this.icon.x, this.icon.y, "aivaGold", "", 35);
        this.tfPoints.setOrigin(.5, 0);
        this.add(this.tfPoints);

        this.tfCount = this.scene.add.bitmapText(this.iks.x + 15, this.iks.y - 3, "aivaStroke", "", 50);
        this.tfCount.tint = 0xFFFFCC;
        this.tfCount.setOrigin(0, .5);
        this.tfCount.setLeftAlign();
        this.add(this.tfCount);;
    }

    public init(color: number, points: number, count: number, delay: number = 0): void {
        this.scaleX = this.scaleY = 0;
        this.tfPoints.scaleX = this.tfPoints.scaleY = 1;
        setTimeout(() => this.show(), delay);

        this._color = color;
        this._total = count;

        this.icon.setTexture("ui", `next${this._color}`);
        this.tfPoints.text = points.toString();
        this.tfCount.text = this._total.toString();

        this.icon.visible = this.tfCount.visible = this.iks.visible = true;

        this.tfPoints.setOrigin(.5, 0);
        this.tfPoints.x = this.icon.x;
        this.tfPoints.y = this.icon.y;

        this._isComplete = false;

        // addEventListener(TouchEvent.TOUCH, this.onTouch);
    }

    private onTouch(evt: TouchEvent): void {
        // var t: Touch = evt.getTouch(this);

        // if (t) {
        //     switch (t.phase) {
        //         case TouchPhase.BEGAN:
        //             this.refresh(100);
        //             break;
        //     }
        // }
    }

    private show(): void {
        this.scene.add.tween({
            targets: this,
            scaleX: 1,
            scaleY: 1,
            duration: 1200,
            ease: Phaser.Math.Easing.Elastic.Out,
            easeParams: [1, .5]
        });
    }

    public refresh(count: number): void {
        if (this._isComplete) {
            return;
        }

        this._total -= count;
        if (this._total <= 0) {
            this._isComplete = true;
            this._total = 0;
            this.scene.add.tween({
                targets: this,
                scaleX: 1.3,
                scaleY: 1.3,
                duration: 1200,
                ease: Phaser.Math.Easing.Elastic.Out,
                easeParams: [1, .5],
                onComplete: () => this.hideQuest()
            });
        }
        this.tfCount.text = this._total.toString();
    }

    private hideQuest(): void {
        this.scene.add.tween({
            targets: this,
            scaleX: 0,
            scaleY: 0,
            duration: 300,
            ease: Phaser.Math.Easing.Sine.Out,
            onComplete: () => this.showPoints()
        });
    }

    private showPoints(): void {
        this.icon.visible = this.tfCount.visible = this.iks.visible = false;
        this.scaleX = this.scaleY = 1;

        this.tfPoints.setOrigin(.5);
        this.tfPoints.x = 0;
        this.tfPoints.y = Quest.POS_Y;

        this.scene.add.tween({
            targets: this.tfPoints,
            scaleX: 2,
            scaleY: 2,
            duration: 1000,
            ease: Phaser.Math.Easing.Elastic.Out,
            easeParams: [1, .5],
            onComplete: () => this.addPoints()
        });
    }

    private addPoints(): void {
        // _universe.panelQuest.dispatchEventWith(Events.CHALLENGE_COMPLETE, false, this);
    }

    public free(): void {
        this.scene.tweens.killTweensOf(this);
        this.scene.tweens.killTweensOf(this.tfPoints);
        this.addPoints();
    }

    public get color(): number { return this._color; }
    public get total(): number { return this._total; }
    public get isComplete(): boolean { return this._isComplete; }
}