import { Container } from "../../exports";
import { URLButton } from "../../ui/views/URLButton";
import { BaseScene } from "../../scenes/views/BaseScene";

export class PanelRowButtons extends Container {

    private readonly DELAY_BETWEEN_SHOWING_BUTTONS: number = 150;

    private vBtns: URLButton[] = [];
    private countButtons: number;
    private range: number;

    constructor(scene: BaseScene, range: number, dist: number = 100) {
        super(scene);
        this.range = range;
        this.countButtons = 0;
    }

    public addBtn(iconName: string, url: string): void {
        const urlButton: URLButton = new URLButton(
            this.scene,
            iconName,
            this.countButtons * this.DELAY_BETWEEN_SHOWING_BUTTONS
        );
        urlButton.addURL(url);
        this.vBtns.push(urlButton);
        this.add(urlButton);
        this.countButtons++;
        
        this.arrangeButtons();
    }

    private arrangeButtons(): void {
        const startPosition: number = -(this.range * (this.countButtons - 1)) / 2;
        for (let i: number = 0; i < this.countButtons; i++) {
            this.vBtns[i].x = this.range * i + startPosition;
        }
    }

    public init(): void {
        for (let i: number = 0; i < this.countButtons; i++) {
            this.vBtns[i].init();
            this.add(this.vBtns[i]);
        }
    }

    public free(): void {
        for (let i: number = 0; i < this.countButtons; i++) {
            this.vBtns[i].free();
        }
    }

    public clear(): void {
        if (this.parentContainer) {
            this.parentContainer.remove(this);
        }
        this.removeAll();
    }
}