export class SeasonsUtils {

    public static readonly SEASONS_COUNT: number = 4;

    public static readonly SEASON_SPRING: number = 0;
    public static readonly SEASON_SUMMER: number = 1;
    public static readonly SEASON_AUTUMN: number = 2;
    public static readonly SEASON_WINTER: number = 3;

    public static readonly COLOR_CELL_SPRING: number = 0xDBB862;
    public static readonly COLOR_CELL_SUMMER: number = 0xC69E42;
    public static readonly COLOR_CELL_AUTUMN: number = 0x3F3B48;
    public static readonly COLOR_CELL_WINTER: number = 0xB9DBFC;

    private static readonly seasonsNames: string[] = [
        "Spring",
        "Summer",
        "Autumn",
        "Winter"
    ];

    private static readonly seasonsColors: number[] = [
        SeasonsUtils.COLOR_CELL_SPRING,
        SeasonsUtils.COLOR_CELL_SUMMER,
        SeasonsUtils.COLOR_CELL_AUTUMN,
        SeasonsUtils.COLOR_CELL_WINTER
    ];

    public static getSeasonNameByID(seasonID: number): string {
        return this.seasonsNames[seasonID];
    }

    public static getColorSeasonByID(seasonID: number): number {
        return this.seasonsColors[seasonID];
    }
}