import { IGameData } from "../interfaces/IGameData";
import { LocalStorageUtils } from "../../utils/LocalStorageUtils";
import { IPlayerData } from "../interfaces/IPlayerData";
import { IScore } from "../interfaces/IScore";
import { ScoreMenu } from "../../menu/views/ScoreMenu";

export class GameDataModel {

    public static readonly COUNT_PLAYERS: number = 7;

    private static instance: GameDataModel;

    private static gameData: IGameData;

    private static _playerId: number;

    constructor() {
        if (!GameDataModel.instance) {
            GameDataModel.instance = this;
        }
        return GameDataModel.instance;
    }

    public readData(): void {
        GameDataModel.gameData = LocalStorageUtils.readData() || this.createDefaultData();
        console.log(GameDataModel.gameData);
    }

    public saveData(): void {
        LocalStorageUtils.saveData(GameDataModel.gameData);
    }

    public deletePlayerData(playerId: number): void {
        GameDataModel.allPlayersData[playerId] = this.getEmptyPlayerData();
        this.saveData();
    }

    private createDefaultData(): IGameData {
        return {
            volumeSounds: 1,
            volumeMusic: 1,
            season: 0,
            language: "rus",
            playersData: this.getDefaultPlayersData(),
            scores: this.getDefaultScoresData()
        } as IGameData;
    }

    private getDefaultPlayersData(): IPlayerData[] {
        const playersData: IPlayerData[] = [];

        for (let indexPlayer: number = 0; indexPlayer < GameDataModel.COUNT_PLAYERS; indexPlayer++) {
            playersData[indexPlayer] = this.getEmptyPlayerData();
        }
        return playersData;
    }

    private getEmptyPlayerData(): IPlayerData {
        return {
            name: "???",
            monster: "",
            gameOver: true,
            darksEscape: true,
            mapColor: [],
            score: 0,
            nextMonsters: [],
            isVisibilityCells: false,
            quests: []
        } as IPlayerData;
    }

    private getDefaultScoresData(): IScore[] {
        const scores: IScore[] = [];

        for (let indexPlayer: number = 0; indexPlayer < ScoreMenu.COUNT_POSITIONS; indexPlayer++) {
            scores[indexPlayer] = this.getEmptyScoreData();
        }
        return scores;
    }

    private getEmptyScoreData(): IScore {
        return {
            playerName: "???",
            score: "---",
            monsterColor: "p"
        } as IScore;
    }

    public static get volumeSounds(): number {
        return GameDataModel.gameData.volumeSounds;
    }
    
    public static set volumeSounds(value: number) {
        GameDataModel.gameData.volumeSounds = value;
    }
    
    public static get volumeMusic(): number {
        return GameDataModel.gameData.volumeMusic;
    }
    
    public static set volumeMusic(value: number) {
        GameDataModel.gameData.volumeMusic = value;
    }
    
    public static get allPlayersData(): IPlayerData[] {
        return GameDataModel.gameData.playersData;
    }
    
    public static get playerId(): number {
        return GameDataModel._playerId;
    }

    public static set playerId(value: number) {
        if (value < 0 || GameDataModel.COUNT_PLAYERS <= value) {
            return;
        }
        GameDataModel._playerId = value;
    }
    
    public static get playerData(): IPlayerData {
        return GameDataModel.gameData.playersData[GameDataModel.playerId];
    }

    public static get seasonId(): number {
        return GameDataModel.gameData.season;
    }

    public static set seasonId(value: number) {
        GameDataModel.gameData.season = value;
    }

    public static get language(): string {
        return GameDataModel.gameData.language;
    }

    public static set language(value: string) {
        GameDataModel.gameData.language = value;
    }

    public static get policy(): boolean {
        return this.playerData.darksEscape;
    }

    public static set policy(value: boolean) {
        this.playerData.darksEscape = value;
    }

    public static get scores(): IScore[] {
        return GameDataModel.gameData.scores;
    }

    public static set score(value: number) {
        GameDataModel.playerData.score = value;
    }

    public static set quests(value: any[]) {
        GameDataModel.playerData.quests = value;
    }

    public static get quests(): any[] {
        return GameDataModel.playerData.quests;
    }
}