import { IAnimatable } from "../interfaces/IAnimatable";

export class EnterFrameManager {

    private static objects: IAnimatable[] = [];
    private static countObjects: number = 0;

    public static add(object: IAnimatable): void {
        const objectIndex: number = EnterFrameManager.objects.indexOf(object);
        if (objectIndex < 0) {
            EnterFrameManager.objects.push(object);
            EnterFrameManager.countObjects = EnterFrameManager.objects.length;
        }
    }

    public static remove(object: IAnimatable): void {
        const objectIndex: number = EnterFrameManager.objects.indexOf(object);
        if (objectIndex >= 0) {
            EnterFrameManager.objects.splice(objectIndex, 1);
            EnterFrameManager.countObjects = EnterFrameManager.objects.length;
        }
    }

    public static clear(): void {
        EnterFrameManager.objects.length = 0;
        EnterFrameManager.countObjects = EnterFrameManager.objects.length;
    }

    public static update(deltaTime: number): void {
        const deltaInSeconds: number = deltaTime * .001;
        for (let i: number = 0; i < EnterFrameManager.countObjects; i++) {
            EnterFrameManager.objects[i].onUpdate(deltaInSeconds);
        }
    }
}