export class ComponentPool {

    private static instance: ComponentPool;

    private pools: { [key: string]: any[]; } = {};

    constructor() {
        if (!ComponentPool.instance) {
            ComponentPool.instance = this;
        }
        return ComponentPool.instance;
    }

    private getPool(key: string): any[] {
        if (key in this.pools) {
            return this.pools[key];
        }
        return this.pools[key] = [];
    }

    public get<T>(classType: new(...args: any) => T, ...args: any): T {
        const poolKey: string = classType.prototype.constructor.name;
        const pool: T[] = this.getPool(poolKey);
        return pool.pop() || this.create(classType, ...args);
    }

    private create<T>(classType: new(...args: any) => T, ...args: any): T {
        if (args) {
            return new classType(...args);
        }
        return new classType();
    }

    public dispose(classInstance: any): void {
        if (classInstance) {
            const poolKey: string = classInstance.constructor.name;
            const pool: any[] = this.getPool(poolKey);
            pool[pool.length] = classInstance;
        }
    }

    public empty(): void {
        this.pools = {};
    }
}