import { CursorEvents } from "../../cursor/events/CursorEvents";
import { Cursour } from "../../cursor/views/Cursour";
import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { GameDataModel } from "../../data/models/GameDataModel";
import { GameProcessModel } from "../../data/models/GameProcessModel";
import { Container, Graphics, Image, Pointer } from "../../exports";
import { MonsterColor, MonsterConstants } from "../../monsters/constants";
import { IMonsterData } from "../../monsters/interfaces/IMonsterData";
import { Monster } from "../../monsters/views/Monster";
import { Spark } from "../../monsters/views/Spark";
import { BaseScene } from "../../scenes/views/BaseScene";
import { ComponentPool } from "../../utils/ComponentPool";
import { SeasonsUtils } from "../../utils/SeasonsUtils";
import { MapEvents } from "../events/MapEvents";
import { IMapCellData } from "../interfaces/IMapCellData";
import { MapModel } from "../models/MapModel";
import { LinefinderUtils } from "../utils/LinefinderUtils";
import { PathfinderUtils } from "../utils/PathfinderUtils";
import { Choice } from "./Choice";
import { CostPerMonster } from "./CostPerMonster";
import { MonsterNavigator } from "./MonsterNavigator";
import { Star } from "./Star";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";

export class MapView extends Container {

    private mapModel: MapModel;
    private gameProcessModel: GameProcessModel;
    private pathfinder: PathfinderUtils;
    private linefinder: LinefinderUtils;
    private componentPool: ComponentPool;

    private layerBackground: Container;
    private layerCell: Graphics;
    private layerMonsters: Container;
    private layerObjects: Container;
    private layerChoice: Container;

    private vBackgroundCells: Image[];

    private _navigator: MonsterNavigator;

    private choice: Choice;
    private cursor: Cursour;

    constructor(scene: BaseScene) {
        super(scene);

        this.mapModel = new MapModel();
        this.gameProcessModel = new GameProcessModel(this.scene.game);
        this.linefinder = new LinefinderUtils(this.scene.game);
        this.pathfinder = new PathfinderUtils();
        this.componentPool = new ComponentPool();

        this.layerBackground = this.scene.add.container(0, 0);
        this.layerCell = this.scene.add.graphics();
        this.layerChoice = this.scene.add.container(0, 0);
        this.layerObjects = this.scene.add.container(0, 0);
        this.layerMonsters = this.scene.add.container(0, 0);

        this.add([
            this.layerCell,
            this.layerBackground,
            this.layerChoice,
            this.layerMonsters,
            this.layerObjects
        ]);

        this.choice = new Choice(scene);
        this.layerChoice.add(this.choice);

        this._navigator = new MonsterNavigator(this.scene);
        this._navigator.x = this.layerBackground.x;
        this._navigator.y = this.layerBackground.y;
        this.add(this._navigator);

        this.cursor = new Cursour(scene);
        this.add(this.cursor);

        this.drawMap();
    }

    public onUpdateDepths(): void {
        this.layerMonsters.sort("depth");
    }

    public activate(): void {
        this.scene.game.events.addListener(MapEvents.CHANGE_VISIBILITY_MAP_CELLS, this.changeVisibilityCell, this);
        this.scene.game.events.addListener(MapEvents.ADD_MONSTER, this.addMonster, this);
        this.scene.game.events.addListener(MapEvents.UPDATE_DEPTHS, this.onUpdateDepths, this);
        this.scene.game.events.addListener(MapEvents.ADD_COST_MONSTER, this.onAddCostOfMonster, this);
        this.scene.game.events.addListener(MapEvents.ADD_STARS, this.onAddStars, this);

        this.layerBackground.setInteractive(
            new Phaser.Geom.Rectangle(
                0,
                0,
                MapModel.MAP_WIDTH_MAX * MapModel.MAP_CELL_SIZE,
                MapModel.MAP_HEIGHT_MAX * MapModel.MAP_CELL_SIZE
            ),
            Phaser.Geom.Rectangle.Contains
        );
        this.layerBackground.on("pointerdown", this.onMapDown, this);
        this.layerBackground.on("pointerup", this.onMapUp, this);

        this.choice.activate();
        this.cursor.activate();
    }

    public deactivate(): void {
        this.scene.game.events.removeListener(MapEvents.CHANGE_VISIBILITY_MAP_CELLS, this.changeVisibilityCell, this);
        this.scene.game.events.removeListener(MapEvents.ADD_MONSTER, this.addMonster, this);
        this.scene.game.events.removeListener(MapEvents.UPDATE_DEPTHS, this.onUpdateDepths, this);
        this.scene.game.events.removeListener(MapEvents.ADD_COST_MONSTER, this.onAddCostOfMonster, this);
        this.scene.game.events.removeListener(MapEvents.ADD_STARS, this.onAddStars, this);

        this.layerBackground.disableInteractive();
        this.layerBackground.off("pointerdown", this.onMapDown, this);
        this.layerBackground.off("pointerup", this.onMapUp, this);

        this.choice.deactivate();
        this.cursor.deactivate();
    }

    //   
    private drawMap(): void {
        const hitArea: Graphics = this.scene.add.graphics();
        hitArea.fillStyle(0xFF00FF, 0);
        hitArea.fillRect(
            0,
            0,
            MapModel.MAP_WIDTH_MAX * MapModel.MAP_CELL_SIZE,
            MapModel.MAP_HEIGHT_MAX * MapModel.MAP_CELL_SIZE
        );

        this.layerBackground.add(hitArea);

        this.vBackgroundCells = [];

        const seasonName: string = SeasonsUtils.getSeasonNameByID(GameDataModel.seasonId);

        for (let ay: number = 0; ay <= MapModel.MAP_HEIGHT_MAX; ay++) {
            for (let ax: number = 0; ax <= MapModel.MAP_WIDTH_MAX; ax++) {
                const wood: Image = this.scene.add.image(
                    MapModel.MAP_CELL_SIZE * ay,
                    MapModel.MAP_CELL_SIZE * ax,
                    "backgrounds",
                    "cell" + seasonName
                );
                this.layerBackground.add(wood);
                this.vBackgroundCells.push(wood);
            }
        }
    }

    public drawCell(): void {
        this.layerCell.clear();
        const color: number = SeasonsUtils.getColorSeasonByID(GameDataModel.seasonId);
        this.layerCell.fillStyle(color, .5);
        for (let ay: number = 0; ay < MapModel.MAP_HEIGHT_MAX; ay++) {
            for (let ax: number = 0; ax < MapModel.MAP_WIDTH_MAX; ax++) {
                if ((ay * MapModel.MAP_WIDTH_MAX + ax) % 2 === 0) {
                    this.layerCell.fillRect(
                        MapModel.MAP_CELL_SIZE * ax,
                        MapModel.MAP_CELL_SIZE * ay,
                        MapModel.MAP_CELL_SIZE,
                        MapModel.MAP_CELL_SIZE
                    );
                }
            }
        }
        this.changeVisibilityCell();
    }

    private changeVisibilityCell(): void {
        this.layerCell.visible = GameDataModel.playerData.isVisibilityCells;
    }

    public redrawMap(): void {
        const seasonName: string = SeasonsUtils.getSeasonNameByID(GameDataModel.seasonId);
        const countCells: number = this.vBackgroundCells.length;

        for (let i: number = 0; i < countCells; i++) {
            this.vBackgroundCells[i].frame = this.scene.sys.textures.getFrame("backgrounds", "cell" + seasonName);
        }
    }

    public loadMonsters(): void {
        // this.isLoadMonsters = true;
        this.gameProcessModel.addingMonsters = this.gameProcessModel.addingInRoom;

        for (let i: number = 0; i < this.mapModel.mapColor.length; i++) {
            for (let j: number = 0; j < this.mapModel.mapColor[i].length; j++) {
                if (this.mapModel.mapColor[i][j] !== "") {
                    //  
                    const strMonster: string = this.mapModel.mapColor[i][j];
                    const monster: Monster = this.getMonster(strMonster);

                    // todo: relocate this in map model in takePlace()
                    this.mapModel.mapMask[i][j] = 1;
                    this.mapModel.mapMonsters[i][j] = monster;

                    monster.init(j, i);
                    this.layerMonsters.add(monster);

                    //     ...
                    if (this.mapModel.mapIsFull) {
                        this.scene.game.events.emit(GameProcessEvents.END_GAME);
                    }
                }
            }
        }
        this.gameProcessModel.addingMonsters = false;
        this.gameProcessModel.dontMove = this.gameProcessModel.dontMoveInRoom;
    }

    public addMonster(): void {
        if (!this.gameProcessModel.gameOver) {
            let j: number;
            let i: number;
            //      
            while (true) {
                j = Phaser.Math.Between(0, MapModel.MAP_WIDTH_MAX_INDEX);
                i = Phaser.Math.Between(0, MapModel.MAP_HEIGHT_MAX_INDEX);

                if (this.mapModel.mapMask[i][j] === 0) {
                    break;
                }
            }

            //  
            const strMonster: string = this.gameProcessModel.arrNextMonsters[this.gameProcessModel.monsterInGameTotal - this.gameProcessModel.monsterInGame];
            const monster: Monster = this.getMonster(strMonster);

            this.mapModel.mapMask[i][j] = 1;
            this.mapModel.mapColor[i][j] = strMonster;
            this.mapModel.mapMonsters[i][j] = monster;

            monster.init(j, i);
            this.layerMonsters.add(monster);

            this.linefinder.searchMain(i, j, monster.color, monster.min, monster.cost);
        }
    }

    private getMonster(color: string): Monster {
        const monster: Monster = this.componentPool.get(Monster, this.scene);
        const model: IMonsterData = (MonsterConstants as any)[color];
        monster.setMonsterData(model);
        return monster;
    }

    private onMapDown(pointer: Pointer, pointerX: number, pointerY: number): void {

        const indexI: number = MapModel.toTile(pointerY);
        const indexJ: number = MapModel.toTile(pointerX);

        if (MapModel.inMap(indexI, indexJ)) {
            this.mapModel.pointCursour.x = indexJ;
            this.mapModel.pointCursour.y = indexI;
            this.scene.game.events.emit(CursorEvents.CHANGE_POSITION);

            if (this.mapModel.isGreen) {
                if (this.mapModel.mapMask[indexI][indexJ] > 0
                    && this.mapModel.mapColor[indexI][indexJ] !== MonsterColor.GREEN) {

                    this.mapModel.isMagic = true;

                    this.mapModel.pointMagic.x = indexJ;
                    this.mapModel.pointMagic.y = indexI;

                    this.scene.game.events.emit(CursorEvents.RESET);
                }
            }
        }
    }

    private onMapUp(pointer: Pointer, pointerX: number, pointerY: number): void {
        const indexI: number = MapModel.toTile(pointerY);
        const indexJ: number = MapModel.toTile(pointerX);

        if (indexI < 0 || indexJ < 0) {
            return;
        }
        if (MapModel.inMap(indexI, indexJ) && this.mapModel.mapMask[indexI][indexJ] === -1) {
            return;
        }
        if (!this.gameProcessModel.gameOver) {
            //   
            if (MapModel.inMap(indexI, indexJ) && this.mapModel.mapMask[indexI][indexJ] === 1) {
                if (!this.mapModel.isKill) {
                    this.mapModel.mapMonsters[indexI][indexJ].onClickHandler();
                    return;
                } else {
                    if (indexI === this.mapModel.pointMagic.y && indexJ === this.mapModel.pointMagic.x) {
                        this.mapModel.aimMonster = this.mapModel.mapMonsters[indexI][indexJ];
                        this.freeCell(MapModel.toTile(this.mapModel.aimMonster.y), MapModel.toTile(this.mapModel.aimMonster.x), this.mapModel.aimMonster.cost);
                        this.mapModel.isKill = false;
                    }
                }
            }
            this.onOutHandler();

            if (this.mapModel.activeMonster) {
                //     
                if (!this.gameProcessModel.dontMove) {
                    if (MapModel.inMap(indexI, indexJ)) {
                        if (this.mapModel.mapMask[indexI][indexJ] === 0) {
                            this.mapModel.pointEnd.y = indexI;
                            this.mapModel.pointEnd.x = indexJ;

                            this.scene.game.events.emit(CursorEvents.RESET);
                        } else {
                            return;
                        }
                    } else {
                        return;
                    }

                    if (this.gameProcessModel.isTutorial) {
                        this.gameProcessModel.turns++;
                    }
                    this.pathfinder.setStartAndEndPoints(this.mapModel.pointStart, this.mapModel.pointEnd);
                    switch (this.mapModel.activeMonster.color) {
                        case MonsterColor.BLUE:
                            this.mapModel.activeMonster.portal();
                            break;

                        case MonsterColor.GREEN:
                            this.mapModel.activeMonster.setWay(this.pathfinder.getWay(this.mapModel.activeMonster.typePathfinder, this.mapModel.activeMonster.maxTurn));
                            if (this.mapModel.aimMonsterData.isAimed) {
                                if (this.mapModel.activeMonster == null) {
                                    this.killMonster(
                                        this.mapModel.aimMonsterData.position.y,
                                        this.mapModel.aimMonsterData.position.x,
                                        this.mapModel.aimMonsterData.cost
                                    );
                                } else {
                                    this.keepCell(
                                        this.mapModel.aimMonsterData.position.y,
                                        this.mapModel.aimMonsterData.position.x
                                    );
                                }
                            }
                            break;

                        default:
                            this.mapModel.activeMonster.setWay(this.pathfinder.getWay(this.mapModel.activeMonster.typePathfinder, this.mapModel.activeMonster.maxTurn));
                    }
                }
            } else {
                return;
            }
        }
    }

    private onOutHandler(): void {
        if (this.mapModel.isGreen) {
            this.mapModel.isKill = false;
            this.mapModel.isMagic = false;
            this.scene.game.events.emit(CursorEvents.RESET);
        }
    }

    public freeCell(i: number, j: number, cost: number): void {
        this.mapModel.mapMask[i][j] = 0;

        this.mapModel.aimMonsterData.isAimed = true;
        this.mapModel.aimMonsterData.position.x = j;
        this.mapModel.aimMonsterData.position.y = i;
        this.mapModel.aimMonsterData.cost = cost;
    }

    //  ,    
    public keepCell(i: number, j: number): void {
        this.mapModel.mapMask[i][j] = 1;
        this.mapModel.aimMonsterData.isAimed = false;
    }

    //  ,    
    public killMonster(i: number, j: number, cost: number): void {

        const mapCellData: IMapCellData = {
            tileX: j,
            tileY: i,
            color: ColorIDsUtils.getCharColorFromColor(this.mapModel.mapColor[i][j]),
            cost: `+${cost}`
        }
        this.onAddCostOfMonster(mapCellData);

        this.mapModel.aimMonsterData.isAimed = false;

        this.mapModel.mapMask[i][j] = 0;
        this.mapModel.mapColor[i][j] = "";

        this.mapModel.mapMonsters[i][j].outGame();
        this.mapModel.mapMonsters[i][j] = null;

        // 
        const spark: Spark = this.componentPool.get(Spark, this.scene);
        spark.init(MapModel.toPix(j), MapModel.toPix(i));
        this.layerObjects.add(spark);

        if (this.scene.game.cache.audio.get("Spark")) {
            this.scene.sound.play("Spark", { volume: GameDataModel.volumeSounds });
        }
        this.gameProcessModel.score += cost;
    }

    private onAddCostOfMonster(mapCellData: IMapCellData): void {
        const costPerMonster: CostPerMonster = this.componentPool.get(CostPerMonster, this.scene);
        costPerMonster.init(mapCellData.tileX, mapCellData.tileY, mapCellData.cost, mapCellData.color);
        this.layerObjects.add(costPerMonster);
    }

    private onAddStars(mapCellData: IMapCellData): void {
        const particlesCountMIN: number = 8;
        const particlesCountMAX: number = 15;

        const particlesScaleMIN: number = .3;
        const particlesScaleMAX: number = 1;

        const countStars: number = Phaser.Math.Between(particlesCountMIN, particlesCountMAX);

        for (var i: number = 0; i < countStars; i++) {
            const star: Star = this.componentPool.get(Star, this.scene);
            star.init(MapModel.toPix(mapCellData.tileX), MapModel.toPix(mapCellData.tileY), particlesScaleMAX * Math.random() + particlesScaleMIN);
            this.layerObjects.add(star);
        }

        if (mapCellData.color === GameDataModel.playerData.monster) {
            this.scene.sound.removeByKey("Bonus");
            this.scene.sound.play("Bonus", { volume: GameDataModel.volumeSounds });
        } else {
            this.scene.sound.removeByKey("Bell");
            this.scene.sound.play("Bell", { volume: GameDataModel.volumeSounds });
        }
    }

    public get navigator(): MonsterNavigator {
        return this._navigator;
    }
}