import { Language } from "../../content/Language";
import { GameDataModel } from "../../data/models/GameDataModel";
import { GameProcessModel } from "../../data/models/GameProcessModel";
import { BitmapText, Container, Image, Point, Tween } from "../../exports";
import { MapModel } from "../../map/models/MapModel";
import { MonsterColor, MonsterConstants } from "../../monsters/constants";
import { BaseScene } from "../../scenes/views/BaseScene";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";
import { ComponentPool } from "../../utils/ComponentPool";
import { ChallengeVO } from "../data/ChallengeVO";
import { QuestsVO } from "../data/QuestsVO";
import { Quest } from "./Quest";

export class PanelQuest extends Container {

    private readonly COUNT_QUEST: number = 3;
    private readonly COUNT_MIN: number = 20;
    private readonly COUNT_MAX: number = 50;
    private readonly LINE_MAX: number = 2;
    private readonly MULTIPLIER_LINES: number = 3;
    private readonly MULTIPLIER_COUNTS: number = 5;
    private readonly RANGE_BETWEEN_QUESTS: number = 170;
    private readonly QUESTS_POS_Y: number = 115;

    public static readonly CHALLENGE_LINES: string = "CHALLENGE_LINES";
    public static readonly CHALLENGE_COUNTS: string = "CHALLENGE_COUNTS";

    private readonly CHALLENGES: string[] = [PanelQuest.CHALLENGE_LINES, PanelQuest.CHALLENGE_COUNTS];

    private tfTitle: BitmapText;
    private arrColors: number[] = [];

    private vQuests: Quest[];
    private vQuestsComplete: Quest[];

    private pointCenterForQuests: Point;
    private pointRangesForQuests: Point;

    private challenge: string = PanelQuest.CHALLENGE_COUNTS;

    private questsVO: QuestsVO = new QuestsVO();
    private gameProcessModel: GameProcessModel;
    private gameDataModel: GameDataModel;
    private componentPool: ComponentPool;

    private isChallengeComplete: boolean;
    private _isAllChallengesCompleted: boolean;
    private countdown: number;

    private moveble: Tween;

    private startPositionsForStars: Point;

    protected scene: BaseScene;

    constructor(scene: BaseScene) {
        super(scene);

        this.gameDataModel = new GameDataModel();
        this.gameProcessModel = new GameProcessModel(this.scene.game);
        this.componentPool = new ComponentPool();

        const bg: Image = this.scene.add.image(0, 0, "ui", "panelBottom").setOrigin(.5, 0);
        this.add(bg);

        this.tfTitle = this.scene.add.bitmapText(bg.x, 20, "aivaWhite", "", 28);
        this.tfTitle.setOrigin(.5, 0);
        this.add(this.tfTitle);

        this.vQuests = [];
        this.vQuestsComplete = [];

        const startPosX: number = this.getQuestsStartPosX(this.COUNT_QUEST);
        for (let i: number = 0; i < this.COUNT_QUEST; i++) {
            const quest: Quest = this.componentPool.get(Quest, this.scene);
            quest.x = i * this.RANGE_BETWEEN_QUESTS + startPosX;
            quest.y = this.QUESTS_POS_Y;
            this.add(quest);

            this.vQuestsComplete.push(quest);
        }
        this.x = this.scene.gameWidth * .5;
    }

    private getQuestsStartPosX(countQuests: number): number {
        return ((countQuests - 1) * this.RANGE_BETWEEN_QUESTS) / -2;
    }

    public setPointsForMoveble(): void {
        this.startPositionsForStars = this.componentPool.get(Point);
        this.startPositionsForStars.x = this.x;
        this.startPositionsForStars.y = this.y - MapModel.MAP_POS;
    }

    public startNewQuests(): void {
        this._isAllChallengesCompleted = true;

        this.challenge = this.CHALLENGES[Phaser.Math.Between(0, this.CHALLENGES.length - 1)];
        this.countdown = 0;

        this.questsVO.initNewQuests(this.challenge);
        this.questsVO.updateCountdown(this.countdown);

        this.fillAllColors();

        for (let i: number = 0; i < this.COUNT_QUEST; i++) {
            const color: number = this.arrColors.splice(Phaser.Math.Between(0, this.arrColors.length - 1), 1)[0];
            const fullColor: MonsterColor = ColorIDsUtils.getFullColorFromIndex(color) as MonsterColor;
            const minInLine: number = MonsterConstants[MonsterColor[fullColor]].countInLineMin;

            const count: number = this.getCount(minInLine);
            const points: number = this.getPoints(count, minInLine);

            this.vQuestsComplete[0].init(color, points, count, i * Quest.SHOWING_DELAY);
            this.add(this.vQuestsComplete[0]);
            this.vQuests = this.vQuests.concat(this.vQuestsComplete.splice(0, 1));

            this.questsVO.updateChallenge(i, color, points, count);
        }
        this.questsVO.saveData();
        this.shiftQuests(true);
        this.language();
    }

    private loadQuests(): void {
        this.challenge = this.questsVO.questID;

        for (let i: number = 0; i < this.COUNT_QUEST; i++) {
            const color: number = this.questsVO.getColor(i);
            const count: number = this.questsVO.getCount(i);
            const points: number = this.questsVO.getPoints(i);

            if (color >= 0 && count > 0) {
                this.vQuestsComplete[0].init(color, points, count, i * Quest.SHOWING_DELAY);
                this.add(this.vQuestsComplete[0]);
                this.vQuests = this.vQuests.concat(this.vQuestsComplete.splice(0, 1));
            }
        }
        this.shiftQuests(true);
        this.language();
    }

    public init(): void {
        this.visible = true;
        // this.y = _moveble.startPoint.y;

        this.questsVO.parseLoadingData(GameDataModel.quests);

        this._isAllChallengesCompleted = this.checkAllChallenges();

        if (this._isAllChallengesCompleted) {
            if (this.questsVO.countdown == 0) {
                this.allChallengesCompleted();
            }
            this.countdown = this.questsVO.countdown;
            // addEventListener(Events.QUESTS_COUNTDOWN, onQuestCountdown);
        } else {
            // _moveble.init(loadQuests);
            this.loadQuests();
        }

        // addEventListener(Events.DELETE_MONSTERS, onStepToChallenge);
        // addEventListener(Events.CHALLENGE_COMPLETE, onChallengeComplete);
    }

    private checkAllChallenges(): boolean {
        let isChallengeCompleted: boolean = true;

        for (let i: number = 0; i < this.COUNT_QUEST; i++) {
            const color: number = this.questsVO.getColor(i);

            if (color >= 0) {
                isChallengeCompleted = false;
                break;
            }
        }
        return isChallengeCompleted;
    }

    private onQuestCountdown(evt: Event): void {
        this.countdown--;
        this.questsVO.updateCountdown(this.countdown);
        this.questsVO.saveData();

        if (this.countdown <= 0) {
            // removeEventListener(Events.QUESTS_COUNTDOWN, onQuestCountdown);
            // _moveble.init(startNewQuests);
        }
    }

    private onChallengeComplete(questComplete: Quest): void {
        this.isChallengeComplete = false;

        const indexOfCompletedQuest: number = this.vQuests.indexOf(questComplete);

        this.gameProcessModel.score += this.questsVO.getValueFromColor(questComplete.color, QuestsVO.CHALLENGE_POINTS);;
        this.gameDataModel.saveData();

        this.questsVO.clearChallenge(questComplete.color);
        this.questsVO.saveData();

        this.vQuestsComplete = this.vQuestsComplete.concat(this.vQuests.splice(indexOfCompletedQuest, 1));
        this.remove(questComplete);

        if (this.visible) {
            // _universe.addStarsOnQuest(_startPositionsForStars.x + questComplete.x, _startPositionsForStars.y + questComplete.y + Quest.POS_Y);

            if (this.vQuests.length > 0) {
                this.shiftQuests();
            } else {
                this.allChallengesCompleted();
            }
        }
    }

    private allChallengesCompleted(): void {
        this.questsVO.init();
        this.countdown = this.questsVO.countdown;
        this._isAllChallengesCompleted = true;
        // addEventListener(Events.QUESTS_COUNTDOWN, onQuestCountdown);
        this.questsVO.saveData();
        // setTimeout(_moveble.free, 1000);
    }

    private shiftQuests(force: boolean = false): void {
        if (this.isOtherQuestsComplete()) {
            return;
        }
        const countQuests: number = this.vQuests.length;
        const startPosX: number = this.getQuestsStartPosX(countQuests);
        for (let i: number = 0; i < countQuests; i++) {
            if (force) {
                this.vQuests[i].x = i * this.RANGE_BETWEEN_QUESTS + startPosX;
                this.vQuests[i].y = this.QUESTS_POS_Y;
            } else {
                // Builder.addTween(_vQuests[i], newPositionForQuests[i].x, newPositionForQuests[i].y);
            }
        }
    }

    private onStepToChallenge(challenge: ChallengeVO): void {

        const countQuests: number = this.vQuests.length;
        for (let i: number = 0; i < countQuests; i++) {
            if (this.vQuests[i].color == challenge.color) {
                if (this.challenge === PanelQuest.CHALLENGE_COUNTS) {
                    this.vQuests[i].refresh(challenge.count);
                } else {
                    if (challenge.count >= this.questsVO.getValueFromColor(challenge.color, QuestsVO.CHALLENGE_COUNT)) {
                        this.vQuests[i].refresh(challenge.count);
                    }
                }
                this.isChallengeComplete = this.vQuests[i].isComplete;
                this.questsVO.updateChallengeFromQuest(this.vQuests[i]);
                this.questsVO.saveData();
                break;
            }
        }
    }

    public free(): void {
        this.visible = false;

        this.vQuestsComplete = this.vQuestsComplete.concat(this.vQuests);
        this.vQuests = [];

        for (let i: number = 0; i < this.COUNT_QUEST; i++) {
            if (this.vQuestsComplete[i].isComplete) {
                this.vQuestsComplete[i].free();
            }
            this.remove(this.vQuestsComplete[i]);
        }
    }

    public isOtherQuestsComplete(): boolean {
        for (let i: number = 0; i < this.vQuests.length; i++) {
            if (this.vQuests[i].isComplete) {
                return true;
            }
        }
        return false;
    }

    public reset(): void {
        this.questsVO.init();
        this.questsVO.saveData();
    }

    private getCount(minInLine: number): number {
        let count: number;

        switch (this.challenge) {
            case PanelQuest.CHALLENGE_LINES:
                count = minInLine + Phaser.Math.Between(0, this.LINE_MAX);
                break;

            case PanelQuest.CHALLENGE_COUNTS:
                count = Phaser.Math.Between(this.COUNT_MIN, this.COUNT_MAX);
                break;
        }

        return count;
    }

    private getPoints(count: number, minInLine: number): number {
        let points: number;

        switch (this.challenge) {
            case PanelQuest.CHALLENGE_LINES:
                points = count * (count - minInLine + this.MULTIPLIER_LINES);
                break;

            case PanelQuest.CHALLENGE_COUNTS:
                points = count * this.MULTIPLIER_COUNTS;
                break;
        }

        return points;
    }

    private fillAllColors(): void {
        this.arrColors = [];
        for (let i: number = 0; i < ColorIDsUtils.count; i++) {
            this.arrColors.push(i);
        }
    }

    public language(): void {
        this.tfTitle.text = Language.getText((Language as any)[this.challenge]);
    }

    public get isAllChallengesCompleted(): boolean {
        return this._isAllChallengesCompleted;
    }
}