import { GameDataModel } from "../../data/models/GameDataModel";
import { BitmapText, Container, Graphics, Shape, Sprite, Tween } from "../../exports";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";
import { LoginEvents } from "../events/LoginEvents";

export class Profile extends Container {

    private hitArea: Shape.Circle;
    private graphics: Graphics;
    private monster: Sprite;
    private tfPlayerName: BitmapText;
    private idProfile: number;

    private arrPos: string[] = ["Out", "Up"];

    private tween: Tween;

    constructor(scene: Phaser.Scene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        this.monster = this.scene.add.sprite(0, 0, "").setScale(1.6).play("addUser");

        this.tfPlayerName = this.scene.add.bitmapText(0, 80, "aivaStroke", "PLAYER", 36).setOrigin(.5);

        this.graphics = this.scene.add.graphics({ fillStyle: { color: 0xaa0000, alpha: 0 } });
        this.hitArea = new Shape.Circle(0, 0, 60);
        this.graphics.fillCircleShape(this.hitArea);

        this.add([
            this.monster,
            this.tfPlayerName,
            this.graphics
        ]);
        this.hitArea.setPosition(this.monster.x, this.monster.y);

        this.init();
        this.show();
    }

    public init(): void {
        this.scaleX = 0;
        this.scaleY = 0;
    }

    public free(): void {
        this.monster.anims.pause();
    }

    public setProfileData(namePlayer: string, color: string, id: number): void {
        this.tfPlayerName.text = namePlayer;

        let postfix: string = "";

        const prefix: string = ColorIDsUtils.getColorFromID(color);
        if (prefix !== "addUser") {
            postfix = this.arrPos[Phaser.Math.Between(0, this.arrPos.length - 1)];
        }

        this.monster.play(prefix + postfix, true, Phaser.Math.Between(0, this.monster.anims.getTotalFrames() - 1));
        this.idProfile = id;

        this.init();
        this.show();
    }

    public hide(): void {
        this.removeListeners();

        this.removeTweens();
        this.tween = this.scene.add.tween({
            targets: this,
            scaleX: 0,
            scaleY: 0,
            duration: 300,
            onComplete: () => this.free()
        });
    }

    public show(): void {
        this.monster.anims.resume();

        this.removeTweens();
        this.tween = this.scene.add.tween({
            targets: this,
            scaleX: 1,
            scaleY: 1,
            duration: 500,
            onComplete: () => this.addListeners()
        });
    }

    private removeTweens(): void {
        if (this.tween) {
            this.tween.remove();
        }
    }

    public addListeners(): void {
        this.setInteractive(this.hitArea, Shape.Circle.Contains);
        this.on("pointerup", this.onClick, this);
    }

    public removeListeners(): void {
        this.disableInteractive();
        this.off("pointerup", this.onClick, this);
    }

    private onClick(): void {
        this.scene.sound.play("Move", { volume: GameDataModel.volumeSounds });
        this.scene.events.emit(LoginEvents.SELECT_PROFILE, this.id);
    }

    public get id(): number {
        return this.idProfile;
    }
}