import { Language } from "../../content/Language";
import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { BitmapText, Container, Image } from "../../exports";
import { MapModel } from "../../map/models/MapModel";
import { Monster } from "../../monsters/views/Monster";
import { BaseScene } from "../../scenes/views/BaseScene";
import { TutorialScene } from "../../scenes/views/TutorialScene";
import { Button } from "../../ui/views/Button";
import { TutorialEvents } from "../events/TutorialEvents";

export class PanelTutorialTop extends Container {

    protected scene: BaseScene;

    private sprite: Image;
    private _butMenu: Button;
    private _butPrev: Button;
    private _butNext: Button;

    private tfCount: BitmapText;
    private tfLabel: BitmapText;
    private tfTask: BitmapText;

    private moveZoneJubby: Image;

    constructor(scene: BaseScene) {
        super(scene);

        this.moveZoneJubby = this.scene.add.image(0, 0, "ui", "moveZone");
        this.add(this.moveZoneJubby);

        const gameWidthHalf: number = this.scene.gameWidth * .5;

        this.sprite = this.scene.add.image(0, 0, "ui", "panelBottom");
        this.sprite.setOrigin(.5, 0);
        this.sprite.x = gameWidthHalf;
        this.sprite.y = 80;
        this.sprite.scaleY = .9;
        this.add(this.sprite);

        this._butMenu = new Button(this.scene, "btnPink", "", () => this.exitFromTutorial());
        this.butMenu.addLabel("BACK", "aivaStroke", 36, 0, -12);
        this.butMenu.x = gameWidthHalf;
        this.butMenu.y = 45;
        this.add(this.butMenu);

        this.tfCount = this.scene.add.bitmapText(this.sprite.x, this.sprite.y + 28, "aivaGold", "", 36).setOrigin(.5);
        this.tfCount.tint = 0xFFFF00;
        this.add(this.tfCount);

        this.tfLabel = this.scene.add.bitmapText(this.sprite.x, this.sprite.y + 70, "aivaGold", "", 42).setOrigin(.5);
        this.add(this.tfLabel);

        this.tfTask = this.scene.add.bitmapText(this.sprite.x, this.sprite.y + 95, "aivaStroke", "", 24).setOrigin(.5, 0);
        this.tfTask.setTint(0xFFFFFF, 0xFFFFFF, 0x99FFFF, 0x99FFFF);
        this.tfTask.setMaxWidth(490);
        this.tfTask.setCenterAlign();
        this.add(this.tfTask);

        this._butPrev = new Button(this.scene, "btnPink", "", () => this.onPrevTutorial());
        this.butPrev.addLabel("BACK", "aivaStroke", 36, 0, -12);
        this.butPrev.x = gameWidthHalf - 175;
        this.butPrev.y = 935;
        this.add(this.butPrev);

        this._butNext = new Button(this.scene, "btnGreen", "", () => this.onNextTutorial());
        this.butNext.addLabel("BACK", "aivaStroke", 36, 0, -12);
        this.butNext.x = gameWidthHalf + 175;
        this.butNext.y = 935;
        this.add(this.butNext);
    }

    public init(): void {
        this.onMoveZoneHide();

        this.visible = true;
        this.butMenu.activate();
        this.butPrev.activate();
        this.butNext.activate();

        this.butPrev.visible = true;
        this.butNext.visible = true;

        this.scene.game.events.addListener(TutorialEvents.MOVE_ZONE_SHOW, this.onMoveZoneShow, this);
        this.scene.game.events.addListener(TutorialEvents.MOVE_ZONE_HIDE, this.onMoveZoneHide, this);

        this.language();
    }

    private onPrevTutorial(): void {
        this.scene.game.events.emit(TutorialEvents.CHANGE_TUTORIAL, -1);
    }

    private onNextTutorial(): void {
        this.scene.game.events.emit(TutorialEvents.CHANGE_TUTORIAL, 1);
    }

    public exitFromTutorial(): void {
        this.scene.game.events.emit(GameProcessEvents.END_GAME);
    }

    public free(): void {
        this.visible = false;
        this.butMenu.deactivate();
        this.butPrev.deactivate();
        this.butNext.deactivate();

        this.scene.game.events.removeListener(TutorialEvents.MOVE_ZONE_SHOW, this.onMoveZoneShow, this);
        this.scene.game.events.removeListener(TutorialEvents.MOVE_ZONE_HIDE, this.onMoveZoneHide, this);
    }

    private onMoveZoneShow(monster: Monster): void {
        this.moveZoneJubby.x = monster.x;
        this.moveZoneJubby.y = monster.y + MapModel.MAP_POS;
        this.addAt(this.moveZoneJubby, 0);
    }

    private onMoveZoneHide(): void {
        if (this.exists(this.moveZoneJubby)) {
            this.remove(this.moveZoneJubby);
        }
    }

    public language(): void {
        this.butPrev.text = Language.getText(Language.BACK);
        this.butNext.text = Language.getText(Language.NEXT);
        this.butMenu.text = Language.getText(Language.MENU);
    }

    public updatePanel(advice: number): void {
        this.tfTaskText = Language.getText((Language as any)[`TASK_HELP${advice}`]);

        this.tfCountText = `#${advice}`;
        this.tfLabelText = Language.getText((Language as any)[`TITLE_HELP${advice}`]);

        if (advice >= TutorialScene.TUTORIAL_1 && advice <= TutorialScene.TUTORIAL_14) {
            if (!this.butPrev.visible) {
                this.butPrev.visible = true;
            }
            if (!this.butNext.visible) {
                this.butNext.visible = true;
            }
        }

        if (advice <= TutorialScene.TUTORIAL_1) {
            this.butPrev.visible = false;
        }
        if (advice >= TutorialScene.TUTORIAL_14 || advice === TutorialScene.TUTORIAL_3) {
            this.butNext.visible = false;
        }
    }

    public set tfCountText(str: string) {
        this.tfCount.text = str;
    }

    public set tfLabelText(str: string) {
        this.tfLabel.text = str;
    }

    public set tfTaskText(str: string) {
        this.tfTask.text = str;
    }

    public get butMenu(): Button {
        return this._butMenu;
    }

    public get butPrev(): Button {
        return this._butPrev;
    }

    public get butNext(): Button {
        return this._butNext;
    }
}