import { IGameData } from "../data/interfaces/IGameData";

export class LocalStorageUtils {

    private static readonly KEY_STORAGE: string = "ag_moli";

    public static saveData(data: IGameData): void {
        if (LocalStorageUtils.isLocalStorageAvailable) {
            localStorage.setItem(
                LocalStorageUtils.KEY_STORAGE,
                JSON.stringify(data)
            );
        }
    }

    public static readData(): IGameData | null {
        if (LocalStorageUtils.isLocalStorageAvailable) {
            const data: string | null = localStorage.getItem(LocalStorageUtils.KEY_STORAGE);
            if (data) {
                return JSON.parse(data);
            }
        }
        return null;
    }

    public static clearLocalStorage(): void {
        if (LocalStorageUtils.isLocalStorageAvailable) {
            localStorage.removeItem(LocalStorageUtils.KEY_STORAGE);
        }
    }

    private static get isLocalStorageAvailable(): boolean {
        return true;
    }
}