import { GameDataModel } from "../../data/models/GameDataModel";
import { MapEvents } from "../../map/events/MapEvents";
import { MenuEvents } from "../../menu/events/MenuEvents";
import { PanelRowButtons } from "../../panels/views/PanelRowButtons";
import { PanelSeasons } from "../../panels/views/PanelSeasons";
import { AmbientSoundPlugin } from "../../plugins/sounds/AmbientSoundPlugin";
import { ButtonProfile } from "../../ui/views/ButtonProfile";
import { SoundToggle } from "../../ui/views/SoundToggle";
import { Toggle } from "../../ui/views/Toggle";
import { ScenesConstants } from "../constants";
import { SceneEvents } from "../events/SceneEvents";
import { UISceneEvents } from "../events/UISceneEvents";
import { IMuteSound } from "../interfaces/IMuteSound";
import { BaseScene } from "./BaseScene";

export class UIScene extends BaseScene {

    private ambientSoundPlugin: AmbientSoundPlugin;

    private toggleSounds: Toggle;
    private toggleMusic: Toggle;
    private toggleMapCells: Toggle;

    private panelSeasons: PanelSeasons;
    private buttonProfile: ButtonProfile;
    private panelRowSocialButtons: PanelRowButtons;

    constructor() {
        super(ScenesConstants.names.ui);
    }

    protected buildScene(): void {
        this.ambientSoundPlugin = this.plugins.get("ambientSoundPlugin") as AmbientSoundPlugin;

        this.panelSeasons = new PanelSeasons(this, 35, 45);
        this.resizeContainer.add(this.panelSeasons);

        this.toggleSounds = new Toggle(this, "btnGreenPoint", Toggle.ICON_SOUND, () => this.muteSounds());
        this.toggleSounds.x = this.gameWidth - 110;
        this.toggleSounds.y = 45;
        this.toggleSounds.activate();
        GameDataModel.volumeSounds > 0 ? this.toggleSounds.switchOn() : this.toggleSounds.switchOff();
        this.resizeContainer.add(this.toggleSounds);

        this.toggleMusic = new Toggle(this, "btnGreenPoint", Toggle.ICON_MUSIC, () => this.muteMusic());
        this.toggleMusic.x = this.gameWidth - 35;
        this.toggleMusic.y = 45;
        this.toggleMusic.activate();
        GameDataModel.volumeMusic > 0 ? this.toggleMusic.switchOn() : this.toggleMusic.switchOff();
        this.resizeContainer.add(this.toggleMusic);

        this.toggleMapCells = new Toggle(this, "btnGreenPoint", Toggle.ICON_CELL, () => this.setMapCellsVisibility());
        this.toggleMapCells.x = 35;
        this.toggleMapCells.y = -50;
        this.toggleMapCells.alpha = 0;
        this.resizeContainer.add(this.toggleMapCells);

        this.buttonProfile = new ButtonProfile(this, () => {
            this.game.events.emit(SceneEvents.CHANGE_SCENE, ScenesConstants.names.login);
            this.changeVisibilityProfileButton(false);
        });
        this.buttonProfile.x = this.gameWidth - this.buttonProfile.getBounds().width / 2 - 10;
        this.buttonProfile.y = this.gameHeight - this.buttonProfile.getBounds().height / 2;
        this.resizeContainer.add(this.buttonProfile);

        this.panelRowSocialButtons = new PanelRowButtons(this, 100);
        this.panelRowSocialButtons.addBtn("socialVK", "https://vk.com/aivagame");
        this.panelRowSocialButtons.addBtn("socialOK", "https://ok.ru/group/53163777130695");
        this.panelRowSocialButtons.addBtn("socialFB", "https://www.facebook.com/aivaapps/?fref=ts");
        this.panelRowSocialButtons.x = this.gameWidth / 2;
        this.panelRowSocialButtons.y = this.gameHeight + 50;
        this.resizeContainer.add(this.panelRowSocialButtons);
    }

    public activate(): void {
        super.activate();
        this.game.events.addListener(MenuEvents.MUTE_SOUNDS, this.updateSoundsUI, this);

        this.game.events.addListener(UISceneEvents.CHANGE_VISIBILITY_SEASONS_PANEL, this.changeVisibilitySeasonsPanel, this);
        this.game.events.addListener(UISceneEvents.CHANGE_VISIBILITY_PROFILE_BUTTON, this.changeVisibilityProfileButton, this);
        this.game.events.addListener(UISceneEvents.CHANGE_VISIBILITY_SOCIAL_BUTTONS, this.changeVisibilitySocialButtons, this);
        this.game.events.addListener(UISceneEvents.CHANGE_VISIBILITY_MAP_CELLS_TOGGLE, this.changeVisibilityMapCells, this);
    }

    public deactivate(): void {
        this.game.events.removeListener(MenuEvents.MUTE_SOUNDS, this.updateSoundsUI, this);

        this.game.events.removeListener(UISceneEvents.CHANGE_VISIBILITY_SEASONS_PANEL, this.changeVisibilitySeasonsPanel, this);
        this.game.events.removeListener(UISceneEvents.CHANGE_VISIBILITY_PROFILE_BUTTON, this.changeVisibilityProfileButton, this);
        this.game.events.removeListener(UISceneEvents.CHANGE_VISIBILITY_SOCIAL_BUTTONS, this.changeVisibilitySocialButtons, this);
        this.game.events.removeListener(UISceneEvents.CHANGE_VISIBILITY_MAP_CELLS_TOGGLE, this.changeVisibilityMapCells, this);
    }

    private updateSoundsUI(data: IMuteSound): void {
        if (data.toggleId === SoundToggle.MUSIC) {
            data.muted ? this.toggleMusic.switchOff() : this.toggleMusic.switchOn();
        } else {
            data.muted ? this.toggleSounds.switchOff() : this.toggleSounds.switchOn();
        }
    }

    private changeVisibilitySeasonsPanel(visibility: boolean): void {
        visibility ? this.panelSeasons.show() : this.panelSeasons.hide();
    }

    private changeVisibilityProfileButton(visibility: boolean): void {
        if (visibility) {
            this.buttonProfile.setProfile(
                GameDataModel.playerData.name,
                GameDataModel.playerData.monster
            );
            this.buttonProfile.show();
        } else {
            this.buttonProfile.hide();
        }
    }

    private changeVisibilitySocialButtons(visibility: boolean): void {
        visibility ? this.panelRowSocialButtons.init() : this.panelRowSocialButtons.free();
    }

    private changeVisibilityMapCells(visibility: boolean): void {
        if (visibility) {
            GameDataModel.playerData.isVisibilityCells
                ? this.toggleMapCells.switchOn()
                : this.toggleMapCells.switchOff();
            this.add.tween({
                targets: this.toggleMapCells,
                y: 45,
                alpha: 1,
                duration: 500,
                onComplete: () => this.toggleMapCells.activate()
            });
        } else {
            this.toggleMapCells.deactivate();
            this.add.tween({
                targets: this.toggleMapCells,
                y: -50,
                alpha: 0,
                duration: 500
            });
        }
    }

    private muteSounds(): void {
        const volume: number = this.toggleSounds.switched ? 1 : 0;
        GameDataModel.volumeSounds = volume;
        this.gameDataModel.saveData();

        this.game.events.emit(
            SceneEvents.MUTE_SOUNDS,
            {
                toggleId: SoundToggle.SOUNDS,
                muted: this.toggleSounds.switched
            } as IMuteSound
        );
    }

    private muteMusic(): void {
        const volume: number = this.toggleMusic.switched ? 1 : 0;
        GameDataModel.volumeMusic = volume;
        this.gameDataModel.saveData();
        this.ambientSoundPlugin.setVolume(GameDataModel.volumeMusic);

        this.game.events.emit(
            SceneEvents.MUTE_SOUNDS,
            {
                toggleId: SoundToggle.MUSIC,
                muted: this.toggleMusic.switched
            } as IMuteSound
        );
    }

    private setMapCellsVisibility(): void {
        GameDataModel.playerData.isVisibilityCells = this.toggleMapCells.switched;
        this.gameDataModel.saveData();
        this.game.events.emit(MapEvents.CHANGE_VISIBILITY_MAP_CELLS);
    }
}