import { Language } from "../../content/Language";
import { BitmapText, DOMElement } from "../../exports";
import { BaseScene } from "../../scenes/views/BaseScene";
import { Button } from "../../ui/views/Button";
import { LoginEvents } from "../events/LoginEvents";
import { SignUpInBase } from "./SignUpInBase";
import { UserChoise } from "./UserChoise";
import { ILoginData } from "../interfaces/ILoginData";

export class SignUp extends SignUpInBase {

    protected scene: BaseScene;

    private monsterChoise: UserChoise;
    private tfLabelMonster: BitmapText;
    private tfLabelPlayer: BitmapText;
    private tfNamePlayer: DOMElement;

    private btnBack: Button;
    private btnDone: Button;

    constructor(scene: BaseScene, x?: number, y?: number, children?: Phaser.GameObjects.GameObject[]) {
        super(scene, x, y, children);

        //   
        this.monsterChoise = new UserChoise(scene);
        this.monsterChoise.scaleX = this.monsterChoise.scaleY = 1.25;
        this.monsterChoise.y = -60;

        //    
        this.tfLabelMonster = this.scene.add.bitmapText(0, -170, "aivaGold", "", 48).setOrigin(.5);

        //     
        this.tfLabelPlayer = this.scene.add.bitmapText(0, 100, "aivaGold", "", 48).setOrigin(.5);

        //     
        this.tfNamePlayer = this.scene.add.dom(0, 165).createFromCache("playerName");

        //  
        this.btnBack = new Button(this.scene, "btnPink", "", () => this.back());
        this.btnBack.addLabel("BACK", "aivaStroke", 36, 0, -12);
        this.btnBack.y = -260;
        this.arrButtons.push(this.btnBack);

        //      
        this.btnDone = new Button(this.scene, "btnGreen", "", () => this.done());
        this.btnDone.addLabel("CREATE", "aivaStroke", 36, 0, -12);
        this.btnDone.y = 260;
        this.arrButtons.push(this.btnDone);

        this.add([
            this.monsterChoise,
            this.tfLabelMonster,
            this.tfLabelPlayer,
            this.btnBack,
            this.btnDone,
            this.tfNamePlayer
        ]);
    }

    public init(): void {
        this.monsterChoise.init();

        this.monsterChoise.random();
        (this.tfNamePlayer.getChildByName("namePlayer") as any).placeholder = Language.getText(Language.PLAYER_NAME_PLACEHOLDER);
        (this.tfNamePlayer.getChildByName("namePlayer") as any).value = "";

        super.init();
    }

    public free(): void {
        this.monsterChoise.free();
        super.free();
    }

    private back(): void {
        this.scene.events.emit(LoginEvents.BACK_TO_LOGIN);
        this.hide();
    }

    private done(): void {
        const playerData: ILoginData = {
            playerName: (this.tfNamePlayer.getChildByName("namePlayer") as any).value,
            playerColor: this.monsterChoise.getColor()
        };
        this.scene.events.emit(LoginEvents.CREATE_PROFILE, playerData);
        this.hide();
    }

    public language(): void {
        this.btnDone.text = Language.getText(Language.DONE);
        this.btnBack.text = Language.getText(Language.BACK);

        this.tfLabelPlayer.text = Language.getText(Language.CHAMPION);
        this.tfLabelMonster.text = Language.getText(Language.BONUS);
    }
}