import { Language } from "../../content/Language";
import { GameProcessEvents } from "../../data/events/GameProcessEvents";
import { BitmapText, Container, Image, Scene } from "../../exports";
import { ColorIDsUtils } from "../../utils/ColorIDsUtils";

export class Nexter extends Container {

    private readonly COUNT_MONSTERS: number = 3;
    private readonly POS_X: number = 70;

    private vNexts: Image[];
    private title: BitmapText;

    private arrNext: string[];

    constructor(scene: Scene) {
        super(scene);

        this.add(this.scene.add.image(0, 0, "ui", "panelNext"));

        this.title = this.scene.add.bitmapText(0, 0, "aivaStroke", "", 23).setOrigin(.5, 0);
        this.add(this.title);

        this.vNexts = [];

        for (let i: number = 0, j: number = -1; i < this.COUNT_MONSTERS; i++, j++) {
            const mc: Image = this.scene.add.image(0, 0, "ui", "next0");
            mc.setOrigin(.5, 1);
            mc.y = (i % 2 == 0) ? 0 : 10;
            mc.x = j * this.POS_X;
            this.add(mc);

            this.vNexts.push(mc);
        }
    }

    public init(): void {
        this.vNexts.forEach((nextMonster: Image) => nextMonster.setScale(0));
        this.scene.game.events.addListener(GameProcessEvents.SET_NEXT_MONSTERS, this.onSetNextMonster, this);
    }

    public free(): void {
        this.scene.game.events.removeListener(GameProcessEvents.SET_NEXT_MONSTERS, this.onSetNextMonster, this);
    }

    private onSetNextMonster(data: string[]): void {
        this.arrNext = data;

        for (let i: number = 0; i < this.COUNT_MONSTERS; i++) {
            this.scene.add.tween({
                targets: this.vNexts[i],
                scaleX: 0,
                scaleY: 0,
                duration: 200,
                onComplete: () => (i == this.COUNT_MONSTERS - 1) ? this.showNextMonster() : null
            });
        }
    }

    private showNextMonster(): void {
        for (let i: number = 0; i < this.COUNT_MONSTERS; i++) {
            this.vNexts[i].setTexture("ui", `next${ColorIDsUtils.getIndexFromColor(this.arrNext[i])}`);
            this.scene.add.tween({
                targets: this.vNexts[i],
                scaleX: 1,
                scaleY: 1,
                duration: 200
            });
        }
    }

    public language(): void {
        this.title.text = Language.getText(Language.NEXT_MONSTER);
    }
}