import { GameDataModel } from "../../data/models/GameDataModel";
import { Quest } from "../views/Quest";

export class QuestsVO {

    private readonly COUNTDOWN_TOTAL: number = 20;

    private readonly QUEST_ID: number = 0;
    private readonly QUEST_COUNTDOWN: number = 1;
    private readonly QUEST_CHALLENGES: number = 2;

    public static readonly CHALLENGE_COLOR: number = 0;
    public static readonly CHALLENGE_POINTS: number = 1;
    public static readonly CHALLENGE_COUNT: number = 2;

    private arrQuests: any[] = [];
    private arrChallenges: any[][] = [[], [], []];

    private gameDataModel: GameDataModel;

    constructor() {
        this.gameDataModel = new GameDataModel();
        this.init();
    }

    public init(): void {
        this.arrChallenges = [[], [], []];
        this.arrQuests[this.QUEST_ID] = "";
        this.arrQuests[this.QUEST_COUNTDOWN] = this.COUNTDOWN_TOTAL;
        this.arrQuests[this.QUEST_CHALLENGES] = this.arrChallenges;
    }

    public parseLoadingData(arr: any[]): void {
        if (arr && arr.length > 0) {
            this.arrQuests[this.QUEST_ID] = arr[this.QUEST_ID];
            this.arrQuests[this.QUEST_COUNTDOWN] = arr[this.QUEST_COUNTDOWN];
            this.arrChallenges = arr[this.QUEST_CHALLENGES];
        } else {
            this.init();
        }
    }

    public initNewQuests(questID: string): void {
        this.arrQuests[this.QUEST_ID] = questID;
    }

    public updateCountdown(countdown: number): void {
        this.arrQuests[this.QUEST_COUNTDOWN] = countdown;
    }

    public updateChallenge(index: number, color: number, points: number, count: number): void {
        this.arrChallenges[index][QuestsVO.CHALLENGE_COLOR] = color;
        this.arrChallenges[index][QuestsVO.CHALLENGE_POINTS] = points;
        this.arrChallenges[index][QuestsVO.CHALLENGE_COUNT] = count;
        this.arrQuests[this.QUEST_CHALLENGES] = this.arrChallenges;
    }

    public updateChallengeFromQuest(quest: Quest): void {
        var countQuests: number = this.arrChallenges.length;
        for (var i: number = 0; i < countQuests; i++) {
            if (this.arrChallenges[i][QuestsVO.CHALLENGE_COLOR] == quest.color) {
                this.arrChallenges[i][QuestsVO.CHALLENGE_COUNT] = quest.count;
            }
        }
    }

    public clearChallenge(color: number): void {
        for (let i: number = 0; i < this.arrChallenges.length; i++) {
            if (this.arrChallenges[i][QuestsVO.CHALLENGE_COLOR] == color) {
                this.updateChallenge(i, -1, 0, 0);
                break;
            }
        }
    }

    public getColor(index: number): number {
        if (this.arrChallenges[index][QuestsVO.CHALLENGE_COLOR] == undefined) {
            return -1;
        }
        return this.arrChallenges[index][QuestsVO.CHALLENGE_COLOR];
    }

    public getCount(index: number): number {
        return this.arrChallenges[index][QuestsVO.CHALLENGE_COUNT];
    }

    public getPoints(index: number): number {
        return this.arrChallenges[index][QuestsVO.CHALLENGE_POINTS];
    }

    public getValueFromColor(color: number, value: number): number {
        for (let i: number = 0; i < this.arrChallenges.length; i++) {
            if (this.arrChallenges[i][QuestsVO.CHALLENGE_COLOR] == color) {
                return this.arrChallenges[i][value];
            }
        }
        return 0;
    }

    public saveData(): void {
        GameDataModel.quests = this.arrQuests;
        this.gameDataModel.saveData();
    }

    public get questID(): string { return this.arrQuests[this.QUEST_ID]; }
    public get countdown(): number { return this.arrQuests[this.QUEST_COUNTDOWN]; }
}